import axios from "axios";
const url = "api/billByEmail";

/* eslint-disable no-async-promise-executor */

class SendBillByEmailService {
  static sendEmail(values, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, values, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static getEmails(whatsAppMessage, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.put(url, whatsAppMessage, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default SendBillByEmailService;
