<template>
  <div>
    <nav-bar-cashier />
    <b-row class="mt-3">
      <b-col lg="3" md="12" sm="12">
        <b-card header-tag="header">
          <template v-slot:header>
            <h5 class="mb-0">{{ $t("customer_details") }}</h5>
          </template>
          <b-overlay :show="show" rounded="sm" class="mt-1 mb-1 bg-white">
            <b-row v-if="customer.length">
              <b-col>
                <h5>
                  <small
                    >{{ $t("category") }}:
                    {{
                      tariffId(customer.length ? customer[0].tarrifTypeId : "")
                    }}</small
                  >
                </h5>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    <b-icon icon="person-badge"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerName"
                  ></b-form-input
                ></b-input-group>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    <b-icon icon="map"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerAddress"
                  ></b-form-input
                ></b-input-group>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    <b-icon icon="telephone"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerPhone"
                  ></b-form-input
                ></b-input-group>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    <b-icon icon="envelope-fill"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerEmail"
                  ></b-form-input
                ></b-input-group>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    {{ $t("document_id") }}
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerNationalId"
                  ></b-form-input
                ></b-input-group>
                <b-input-group size="sm" class="mb-1">
                  <b-input-group-prepend is-text>
                    {{ $t("nuit") }}
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="sm"
                    :disabled="!editting"
                    v-model="customerNuit"
                  ></b-form-input
                ></b-input-group>
                <b-row>
                  <b-col v-if="!editting"
                    ><b-button
                      v-b-modal.modal-center
                      variant="success"
                      block
                      @click="editCustomer()"
                      size="sm"
                    >
                      {{ $t("update_details") }}</b-button
                    ></b-col
                  >
                  <b-col v-if="editting"
                    ><b-button
                      v-b-modal.modal-center
                      variant="danger"
                      block
                      @click="cancelEditing()"
                      size="sm"
                    >
                      {{ $t("cancel") }}</b-button
                    ></b-col
                  >
                  <b-col v-if="editting"
                    ><b-button
                      v-b-modal.modal-center
                      variant="secondary"
                      block
                      :disabled="!editting"
                      @click="editCustomerFinal()"
                      size="sm"
                    >
                      <b-icon icon="pen"></b-icon></b-button
                  ></b-col>
                </b-row>
              </b-col>
            </b-row>
            <b-row v-else class="text-danger">
              <b-col>{{ error }}</b-col>
            </b-row>
          </b-overlay>
        </b-card>
      </b-col>
      <b-col>
        <b-card header-tag="header">
          <template v-slot:header>
            <b-row>
              <b-col cols="6">
                <h5 class="align-middle">
                  {{ $t("cashier") }}:
                  <small>{{
                    `${new Date().getDate()}/${
                      new Date().getMonth() + 1
                    }/${new Date().getFullYear()}`
                  }}</small>
                </h5>
              </b-col>
              <b-col
                ><b-input-group size="md" class="mb-2">
                  <b-input-group-prepend is-text>
                    <b-icon icon="pen"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="md"
                    v-model="barcode"
                    :placeholder="$t('type_customer_barcode')"
                  ></b-form-input
                  ><b-input-group-append>
                    <b-button
                      :disabled="!barcode || barcode.length < 6"
                      size="sm"
                      @click="searchCustomer()"
                      text="Buscar"
                      variant="secondary"
                    >
                      Buscar
                    </b-button>
                  </b-input-group-append></b-input-group
                ></b-col
              >
            </b-row>
          </template>
          <b-overlay :show="show" rounded="sm" class="mt-1 mb-1 bg-white">
            <b-row v-if="readings.length">
              <b-col
                ><h5 class="">
                  <small
                    >{{ readings.length }} {{ $t("last_purchases") }}</small
                  >
                </h5>
              </b-col>
            </b-row>

            <b-row v-if="readings.length">
              <b-col>
                <table
                  ref="readings-details"
                  class="table table-sm table-hover table-bordered table-stripped"
                >
                  <thead class="bg-secondary text-white">
                    <tr>
                      <th>
                        {{ $t("month_consumption") }}
                      </th>
                      <th>{{ $t("receipt") }}</th>
                      <th>{{ $t("value") }}</th>
                      <th>{{ $t("penalty") }}</th>
                      <th class="text-center">{{ $t("total_to_pay") }}</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="bill in facturas"
                      :key="bill.billId"
                      style="cursor: pointer"
                    >
                      <td class="align-middle">{{ bill.createdAt }}</td>
                      <td class="align-middle">{{ bill.billId }}</td>
                      <td class="align-middle">
                        {{ parseFloat(bill.subTotal) + parseFloat(bill.vat) }}
                      </td>
                      <td class="align-middle">
                        {{ bill.forfeit }}
                      </td>
                      <td class="text-center align-middle">
                        {{ bill.total }}
                      </td>
                      <td class="align-middle">
                        <b-button
                          v-if="bill.status == 0"
                          v-b-modal.modal-center
                          variant="danger"
                          block
                          :disabled="show"
                          @click="submitBill(bill)"
                          size="sm"
                          ><b-icon icon="credit-card"></b-icon>
                          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                          {{ $t("pay") }}
                        </b-button>
                        <b-button
                          v-else
                          v-b-modal.modal-center
                          variant="success"
                          block
                          disabled
                          size="sm"
                          ><b-icon icon="check-square-fill"></b-icon
                          >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                          {{ $t("paid") }}
                        </b-button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </b-col>
            </b-row>
          </b-overlay>

          <b-row v-if="error_readings">
            <b-col class="text-danger">{{ error_readings }}</b-col>
          </b-row>
        </b-card>
      </b-col>
      <b-col lg="3" md="12" sm="12">
        <b-card header-tag="header">
          <template v-slot:header>
            <b-row>
              <b-col lg="7"
                ><h5 class="align-middle">
                  <small>{{ currentBalance.toFixed(2) }} MZN</small>
                </h5></b-col
              >
              <b-col class="text-right">
                <b-button
                  v-b-modal.modal-center
                  variant="success"
                  block
                  :disabled="!payments.length"
                  @click="generatePDFFile()"
                  size="sm"
                  ><b-icon icon="printer-fill"></b-icon>
                </b-button>
              </b-col>
            </b-row>
          </template>
          <table
            ref="payment-details"
            class="table table-sm table-hover table-stripped"
            v-if="payments.length"
          >
            <thead>
              <tr
                style="font-size: 12px"
                v-for="(payment, index) in payments"
                :key="index"
              >
                <td>{{ index + 1 }}</td>
                <td>{{ payment.customerName }}</td>
                <td class="text-right">{{ payment.totalPaid }}</td>
              </tr>
            </thead>
          </table>
        </b-card>
      </b-col>
    </b-row>

    <b-modal
      hide-footer
      ref="invalidate-forfeit"
      :title="$t('invalidate_forfeit')"
    >
      <p class="text-danger" style="font-size: 10pt">
        Olá <b class="text-dark">{{ user[0].name }}</b
        >! Certifique a data do pagamento que consta no talão de depósito, para
        efectivar o cancelamento da multa de
        <b class="text-dark">{{ customerName }}</b>
      </p>
      <b-row>
        <b-col>
          <b-button block size="sm" @click="cancelValidation()">{{
            $t("cancel")
          }}</b-button>
        </b-col>
        <b-col>
          <b-button
            block
            size="sm"
            variant="danger"
            @click="invalidateMulta()"
            >{{ $t("invalidate_forfeit") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>

    <b-modal
      hide-footer
      size="md"
      scrollable
      ref="validate-bill"
      :title="$t('pay_now')"
    >
      <div>
        <b-overlay :show="show" rounded="sm">
          <table class="table table-sm table-bordered" style="font-size: 10pt">
            <thead>
              <tr>
                <th>
                  {{ customer.length ? customer[0].name.toUpperCase() : "" }}
                </th>
                <th class="text-right">
                  {{ customer.length ? customer[0].barcode : "" }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{{ $t("bill_receipt_number") }}</td>
                <td class="text-right">{{ billId }}</td>
              </tr>

              <tr>
                <td>{{ $t("current_forfeit") }}</td>
                <td class="text-right" v-money-format="currentForfeit"></td>
              </tr>

              <tr>
                <td>{{ $t("current_bill") }}</td>
                <td class="text-right" v-money-format="subTotal"></td>
              </tr>
              <tr>
                <th>{{ $t("total_payable") }}</th>
                <th class="text-right" v-money-format="currentBillValue"></th>
              </tr>
            </tbody>
          </table>
          <b-row
            ><b-col
              ><small>{{ $t("date_of_payment") }}</small>
              <b-form-datepicker
                v-model="paymentDate"
                size="sm"
                class="mb-2"
              ></b-form-datepicker> </b-col
          ></b-row>
          <b-row>
            <b-col
              ><small>{{ $t("payment_method") }}</small>
              <b-form-select
                v-model="paymentMethod"
                :options="options"
                size="sm"
              ></b-form-select
            ></b-col>
            <b-col
              ><small>{{ $t("payment_reference") }}</small>
              <b-form-group label-for="payment_reference">
                <b-form-input
                  type="text"
                  size="sm"
                  v-model="paymentReference"
                ></b-form-input> </b-form-group
            ></b-col>
          </b-row>
          <b-row>
            <b-col
              ><small>{{ $t("amount_received") }}</small>
              <b-form-group label-for="amount_received">
                <b-form-input
                  type="number"
                  size="sm"
                  step="any"
                  :min="0"
                  v-model="amountReceived"
                ></b-form-input> </b-form-group
            ></b-col>
            <b-col
              ><small>{{ $t("debit_balance") }}</small>
              <b-form-group label-for="debit_balance">
                <b-form-input
                  type="number"
                  size="sm"
                  step="any"
                  v-model="debitBalance"
                  disabled
                ></b-form-input> </b-form-group
            ></b-col>
          </b-row>
          <b-row>
            <b-col
              ><b-button
                @click="finalizepayment()"
                :disabled="!amountReceived > grandTotal || show"
                block
                size="sm"
                variant="success"
                >{{ $t("pay_now") }}</b-button
              ></b-col
            >
          </b-row>
        </b-overlay>
      </div>
    </b-modal>
  </div>
</template>

<script>
import moment from "moment";
import NavBarCashier from "../components/NavbarCashier";

import BillingCalculations from "../calcs/BillingCalculations";

import TariffService from "../http-service/SimulatorService";
import ReadingService from "../http-service/ReadingService";
import CashierPayments from "../http-service/CashierPayments";

import SearchCustomerBySearialNumber from "../http-service/SearchCustomerBySearialNumber";
import CustomerService from "../http-service/CustomerService";
import PaymentService from "../http-service/PaymentService.js";
import DebtService from "../http-service/DebtService";

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;
import JsBarcode from "jsbarcode/bin/JsBarcode";

import { mapGetters } from "vuex";

export default {
  name: "Manager",

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company"]),
  },

  created() {
    this.getAllPayments();
  },

  components: {
    NavBarCashier,
  },

  data() {
    return {
      customerName: "",
      customerAddress: "",
      customerNationalId: "",
      customerPhone: "",
      customerEmail: "",
      customerNuit: "",

      editting: false,
      error: "",
      error_readings: "",
      show: false,
      barcode: "",
      customer: [],
      currentTariff: [],
      readings: [],
      facturas: [],
      payments: [],

      paymentMethod: "",
      paymentReference: "",
      amountReceived: 0,
      debitBalance: 0,
      grandTotal: 0,
      currentBillValue: 0,
      currentForfeit: 0,
      paymentDate: `${new Date().getFullYear()}-${
        new Date().getMonth() + 1
      }-${new Date().getDate()}`,
      referenceOfPayment: "",

      options: [
        { value: 1, text: "Boca do Caixa" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "MPESA Open API" },
        { value: 8, text: "E-Mola" },
      ],

      currentBalance: 0,
      subTotal: 0,
      vat: 0,
      billId: 0,
      consumption: 0,
      forfeit: 0,
      totalPaid: 0,
      receiptNumber: "",
      deadlineOfBill: "",
    };
  },

  watch: {
    amountReceived: function () {
      if (this.amountReceived.length == 0) {
        this.debitBalance = 0;
        return;
      }
      this.debitBalance =
        parseFloat(this.currentBillValue) - parseFloat(this.amountReceived);
    },

    paymentMethod: function () {
      if (this.paymentMethod == 7) {
        console.log("Initiating M-Pesa transaction");
      }
    },

    paymentDate: function () {
      const dataLimite = moment(new Date(this.deadlineOfBill))
        .add(15, "days")
        .toDate();
      const gerarMulta = moment(this.paymentDate).isSame(dataLimite, "month");
      console.log(this.paymentDate);
      console.log(gerarMulta);
      if (!gerarMulta) {
        this.currentBillValue = (
          this.currentBillValue - this.currentForfeit
        ).toFixed(2);
        this.$refs["invalidate-forfeit"].show();
        this.currentForfeit = 0;
      } else {
        this.searchCustomer();
      }
    },
  },

  methods: {
    generatePDFFile() {
      const canvas = document.createElement("canvas");
      JsBarcode(canvas, "123456789", { format: "CODE39" });
      const barcode = canvas.toDataURL("image/png");
      console.log(barcode);
      const currentDay = `${new Date().getDate()}-${
        new Date().getMonth() + 1
      }-${new Date().getFullYear()}`;

      console.log(currentDay);

      const body = [];

      for (let payment of this.payments) {
        const rows = [];
        rows.push({ text: payment.billId + "/SAF/2021", style: "estilo" });
        rows.push({ text: payment.customerName, style: "estilo" });
        rows.push({ text: payment.reference, style: "estilo" });
        rows.push({ text: payment.dateOfPayment, style: "estilo" });
        rows.push({ text: payment.createdAt, style: "estilo" });
        rows.push({
          text: this.getPaymentMethod(payment.paymentMethod),
          style: "estilo",
        });
        rows.push({ text: payment.totalPaid.toFixed(2), style: "estilo" });

        body.push(rows);
      }

      const docDefinition = {
        content: [
          {
            columns: [
              {
                text: "RELATÓRIO DO CAIXA: " + `${currentDay}`,
                style: "header",
              },
              {
                text:
                  "TOTAL RECEBIDO: " +
                  this.currentBalance.toFixed(2) +
                  " MZN\n\n",
                style: "header",
              },
            ],
          },
          {
            table: {
              body: [
                [
                  { text: "Factura/Recibo", style: "columnsTitle" },
                  { text: "Nome do cliente", style: "columnsTitle" },
                  { text: "Referência", style: "columnsTitle" },
                  { text: "Data Depósito", style: "columnsTitle" },
                  { text: "Data de Confirmação", style: "columnsTitle" },
                  { text: "Modo de Pagamento", style: "columnsTitle" },
                  { text: "Valor Pago", style: "columnsTitle" },
                ],
                ...body,
              ],
            },
          },
        ],
        styles: {
          header: { fontSize: 10, bold: true },
          columnsTitle: {
            fontSize: 8,
            bold: true,
            fillColor: "#7159c1",
            color: "#FFFFFF",
          },
          estilo: {
            fontSize: 8,
          },
        },
      };
      pdfMake.createPdf(docDefinition).open();
    },

    getPaymentMethod(method) {
      if (method == 1) {
        return "Boca do Caixa";
      } else if (method == 2) {
        return "Depósito Bancário";
      } else if (method == 3) {
        return "Transferência Bancária";
      } else if (method == 4) {
        return "POS";
      } else if (method == 5) {
        return "Cheque";
      } else if (method == 6) {
        return "SISTAFE";
      } else if (method == 7) {
        return "M-Pesa Open API";
      } else if (method == 8) {
        return "E-Mola";
      }
    },

    cancelValidation() {
      this.$refs["invalidate-forfeit"].hide();
      this.searchCustomer();
    },

    async invalidateMulta() {
      this.show = true;

      const passingValues = {
        billId: this.billId,
        staffId: this.user[0].id,
        invalidateMulta: 1,
      };

      try {
        const updateDebt = await DebtService.insertDebt(
          passingValues,
          this.token
        );

        if (updateDebt.success == true) {
          this.searchCustomer();
        } else {
          console.log(updateDebt.message);
        }
      } catch (err) {
        console.log(err.message);
      }
      this.show = false;
      this.$refs["invalidate-forfeit"].hide();
    },

    async finalizepayment() {
      this.show = true;
      const passingValues = {
        id: this.billId,
        billId: this.billId,
        barcode: this.barcode,
        customerName: this.customer[0].name,
        tarrifTypeId: this.customer[0].tarrifTypeId,
        branchId: this.customer[0].branchId,
        staffId: this.user[0].id,
        staffName: this.user[0].name,
        consumption: this.consumption,
        vat: this.vat,
        subTotal: this.subTotal,
        fine: this.forfeit,
        totalPaid: this.amountReceived,
        saldoDevedor: this.debitBalance,
        reference: this.paymentReference,
        dateOfPayment: this.paymentDate,
        paymentMethod: this.paymentMethod,
      };

      try {
        const payment = await PaymentService.insertPayment(
          passingValues,
          this.token
        );
        if (payment.success == true) {
          this.$bvToast.toast(payment.message, {
            title: "",
            variant: "success",
            solid: true,
          });
          this.amountReceived = 0;
          this.debitBalance = 0;
          this.referenceOfPayment = "";
          this.paymentMethod = "";
          this.dateOfPayment = "";
          this.searchCustomer();
          this.getAllPayments();
        } else {
          this.$bvToast.toast(payment.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
      } catch (err) {
        console.log(err.message);
      }
      this.show = false;
      this.$refs["validate-bill"].hide();
    },

    async getAllPayments() {
      this.show = true;
      try {
        const payments = await CashierPayments.getPaymentsByStaffId(
          this.user[0].id,
          this.token
        );
        if (payments.success == true) {
          this.currentBalance = payments.totalPaid;
          this.payments = payments.result;
          // console.log(this.payments);
        } else {
          this.currentBalance = 0;
        }
      } catch (err) {
        this.currentBalance = 0;
        console.log(err.message);
      }
      this.show = false;
    },

    submitBill(bill) {
      this.currentForfeit = parseFloat(bill.forfeit);
      this.consumption = bill.totalUnities;
      this.receiptNumber = bill.receiptNumber;
      this.billId = bill.billId;
      this.vat = parseFloat(bill.vat);
      this.subTotal = parseFloat(bill.subTotal) + parseFloat(bill.vat);
      this.currentBillValue = bill.total;
      this.fine = parseFloat(bill.forfeit);
      this.deadlineOfBill = bill.createdAt;
      this.$refs["validate-bill"].show();
    },

    getSubtotalValue(bill) {
      let data;
      const consumption = bill.consumption;

      if (this.customer[0].tarrifTypeId == 2 && consumption <= 5) {
        data = BillingCalculations.domesticBelowFive(5, this.currentTariff[0]);
        const subTotal = data[0].subTotal;
        const forfeit = data[0].total * bill.forfeit;
        const totalOfBill = data[0].total + forfeit;
        const vat = data[0].tax;
        this.currentBillValue = totalOfBill.toFixed(2);
        const details = {
          subTotal: subTotal.toFixed(2),
          totalOfBill: totalOfBill.toFixed(2),
          forfeit: forfeit.toFixed(2),
          vat: vat,
        };
        return details;
      } else if (
        (this.customer[0].tarrifTypeId == 2 ||
          this.customer[0].tarrifTypeId == 3) &&
        consumption > 5
      ) {
        data = BillingCalculations.domesticAboveFive(
          consumption,
          this.currentTariff[0]
        );
        const subTotal = data[0].subTotal;
        const forfeit = data[0].total * bill.forfeit;
        const totalOfBill = data[0].total + forfeit;
        const vat = data[0].tax;
        this.currentBillValue = totalOfBill.toFixed(2);
        const details = {
          subTotal: subTotal.toFixed(2),
          totalOfBill: totalOfBill.toFixed(2),
          forfeit: forfeit.toFixed(2),
          vat: vat,
        };
        return details;
      } else if (this.customer[0].tarrifTypeId == 1) {
        data = BillingCalculations.fontCalc(consumption, this.currentTariff[0]);
        const subTotal = data[0].subTotal;
        const forfeit = data[0].total * bill.forfeit;
        const totalOfBill = data[0].total + forfeit;
        const vat = data[0].tax;
        this.currentBillValue = totalOfBill.toFixed(2);
        const details = {
          subTotal: subTotal.toFixed(2),
          totalOfBill: totalOfBill.toFixed(2),
          forfeit: forfeit.toFixed(2),
          vat: vat,
        };
        return details;
      } else if (this.customer[0].tarrifTypeId >= 4) {
        data = BillingCalculations.calculatePublicComercialAndIndustrial(
          consumption,
          this.currentTariff[0]
        );
        const subTotal = data[0].subTotal;
        const forfeit = data[0].total * bill.forfeit;
        const totalOfBill = data[0].total + forfeit;
        const vat = data[0].tax;
        this.currentBillValue = totalOfBill.toFixed(2);
        const details = {
          subTotal: subTotal.toFixed(2),
          totalOfBill: totalOfBill.toFixed(2),
          forfeit: forfeit.toFixed(2),
          vat: vat,
        };
        return details;
      }
    },

    populateValues() {
      this.customerName = this.customer[0].name;
      this.customerAddress = this.customer[0].address;
      this.customerNationalId = this.customer[0].meeter_id;
      this.customerPhone = this.customer[0].phone;
      this.customerEmail = this.customer[0].email;
      this.customerNuit = this.customer[0].nuit;
    },

    clearValues() {
      this.customerName = "";
      this.customerAddress = "";
      this.customerNationalId = "";
      this.customerPhone = "";
      this.customerEmail = "";
      this.customerNuit = "";
    },

    editCustomer() {
      this.editting = true;
    },

    cancelEditing() {
      this.editting = false;
    },

    async editCustomerFinal() {
      this.show = true;

      const passingValues = {
        id: this.customer[0].id,
        status: this.customer[0].status,
        name: this.customerName,
        address: this.customerAddress,
        phone: this.customerPhone,
        email: this.customerEmail,
        nuit: this.customerNuit,
        meeter_id: this.customerNationalId,
      };

      try {
        const customers = await CustomerService.insertOrUpdateCustomer(
          passingValues,
          this.token
        );
        if (customers.success == true) {
          this.show = false;
          this.$bvToast.toast(passingValues.name + " actualizado com sucesso", {
            title: "",
            variant: "success",
            solid: true,
          });
          this.searchCustomer();
        } else {
          this.customers = customers.result;
          this.show = false;
          this.$bvToast.toast(customers.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.show = false;
      }
    },

    async searchCustomer() {
      const value = this.barcode;
      this.show = true;
      if (value.length === 0) {
        this.show = false;
        this.customer = [];
        return;
      }

      try {
        let data = await SearchCustomerBySearialNumber.searchCustomers(
          this.barcode,
          this.token
        );
        if (data.success == true) {
          this.customer = data.result;
          this.show = false;
          this.populateValues();
          this.getTariffById();
          this.fetchAllReadings();
        } else {
          this.show = false;
          this.error = data.message;
          this.customer = [];
          this.readings = [];
          this.clearValues();
        }
      } catch (err) {
        this.$bvToast.toast("" + err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.show = false;
        this.customer = [];
        this.error = err.message;
        this.clearValues();
        this.readings = [];
      }
      this.show = false;
      this.editting = false;
    },

    async getTariffById() {
      this.show = true;
      try {
        const tariffs = await TariffService.getTarrifsByCategory(
          this.customer[0].tarrifTypeId
        );
        if (tariffs.success == true) {
          this.currentTariff = tariffs.result;
        } else {
          this.$bvToast.toast(`Tarifa não disponível.`, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
        }
        this.show = false;
      } catch (err) {
        this.show = false;
      }
    },

    async fetchAllReadings() {
      this.show = true;

      try {
        const readings = await ReadingService.getReadingsByCustomer(
          this.barcode,
          this.customer[0].tarrifTypeId,
          this.token
        );
        if (readings.success == true) {
          this.readings = readings.result;
          this.facturas = readings.facturas;
          this.error_readings = "";
        } else {
          this.error_readings = readings.message;
          this.readings = [];
          this.facturas = [];
        }
        this.show = false;
      } catch (err) {
        this.error_readings = err.message;
        this.readings = [];
        this.show = false;
      }
    },

    tariffId(tariff) {
      if (tariff == 1) {
        this.quantityMinTariff = 0;
        return "Fontenária";
      } else if (tariff == 2) {
        this.quantityMinTariff = 5;
        return "Doméstica";
      } else if (tariff == 3) {
        this.quantityMinTariff = 0;
        return "Municipal";
      } else if (tariff == 4) {
        this.quantityMinTariff = 15;
        return "Comercial";
      } else if (tariff == 5) {
        this.quantityMinTariff = 15;
        return "Público";
      } else if (tariff == 6) {
        this.quantityMinTariff = 15;
        return "Industrial";
      }
    },
  },
};
</script>
