import axios from 'axios'
const url = 'api/search_branch'

/* eslint-disable no-async-promise-executor */

class SearchBranches {
    /// Get Users By Company
    static search(search, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${search}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default SearchBranches