<template>
  <div class="pending-readings">
    <b-card class="mb-0">
      <b-row class="mb-2">
        <b-col cols="6"
          ><b class="text-black">Fila de validação de leituras</b></b-col
        >
        <b-col>
          <b-form-select size="sm" v-model="branchId" class="mb-3">
            <b-form-select-option
              v-for="branch in branches"
              :key="branch.id"
              :value="branch.id"
              >{{ branch.name }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col
          cols="1"
          @click="getPendingReadins()"
          class="text-right"
          style="cursor: pointer"
        >
          <b-button pill variant="success" size="sm"
            ><b-icon variant="light" icon="arrow-clockwise"></b-icon
          ></b-button>
        </b-col>
      </b-row>
      <hr class="bg-primary mt-1 mb-1 w-1" />
      <b-overlay :show="isLoading" rounded="sm" class="mt-2 mb-2 bg-white">
        <table
          v-if="readings.length > 0"
          class="table table-sm table-hover mt-0"
          style="width: 100%"
        >
          <tbody>
            <tr
              style="font-size: 9pt; cursor: pointer; font-weight: bold"
              class="bg-dark text-light"
            >
              <td>Data</td>
              <td>Contador</td>
              <td>Anterior</td>
              <td>Actual</td>
              <td>Consumo</td>
              <td class="text-center">Acções</td>
            </tr>
            <tr
              style="font-size: 9pt; cursor: pointer"
              v-for="reading in readings"
              :key="reading.id"
            >
              <td>{{ reading.createdAt }}</td>
              <td>{{ reading.barcode }}</td>
              <td>{{ reading.last }} m <sup>3</sup></td>
              <td>{{ reading.current }} m <sup>3</sup></td>
              <td>{{ reading.consumption }} m <sup>3</sup></td>
              <td>
                <b-row style="font-size: 10pt" class="text-center">
                  <b-col
                    class="text-primary"
                    v-b-tooltip.hover="{ variant: 'primary' }"
                    @click="validateReading(reading)"
                    title="Validar leitura"
                    ><b-icon variant="primary" icon="eye-fill"></b-icon> Validar
                    leitura</b-col
                  >
                </b-row>
              </td>
            </tr>
          </tbody>
        </table>
        <b-alert v-else variant="danger" class="mt-3" show dismissible
          >{{ errorMessage }} <b>&rArr;</b></b-alert
        >
      </b-overlay>
    </b-card>

    <b-modal
      size="xl"
      hide-footer
      ref="update-reading"
      :title="tariffId(currentReading.tarrifTypeId)"
    >
      <b-row>
        <b-col lg="4"
          ><b-img
            thumbnail
            fluid
            :src="
              currentReading.waterMeterImageUrl != null
                ? currentReading.waterMeterImageUrl
                : imageTemplate
            "
            alt="Image 1"
            width="380"
            class="mb-2"
          ></b-img
        ></b-col>
        <b-col>Reading details</b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-row>
            <b-col class="text-center">
              <small>{{ $t("last") + " " }}m <sup>3</sup></small>
              <b-form-group label-for="last">
                <b-form-input
                  type="number"
                  size="sm"
                  :min="currentReading.last"
                  v-model="currentReading.last"
                  disabled
                ></b-form-input> </b-form-group
            ></b-col>
            <b-col
              ><small>{{ $t("current") + " " }}m <sup>3</sup></small>
              <b-form-group label-for="current">
                <b-form-input
                  type="number"
                  size="sm"
                  :min="currentReading.last"
                  v-model="current"
                ></b-form-input> </b-form-group
            ></b-col>
            <b-col
              ><small>{{ $t("consumption") + " " }}m <sup>3</sup></small>
              <b-form-group label-for="consumption">
                <b-form-input
                  type="number"
                  size="sm"
                  v-model="consumption"
                  disabled
                ></b-form-input> </b-form-group
            ></b-col>
          </b-row>

          <b-row>
            <b-col
              ><b-button @click="setReadingsUpdate(1)" block size="sm">{{
                $t("cancel")
              }}</b-button></b-col
            >
            <b-col
              ><b-button
                @click="setReadingsUpdate(2)"
                block
                size="sm"
                variant="warning"
                >Rejeitar</b-button
              ></b-col
            >
            <b-col
              ><b-button
                @click="setReadingsUpdate(3)"
                block
                size="sm"
                variant="success"
                >Validar</b-button
              ></b-col
            >
          </b-row>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import ReadingService from "../../http-service/ReadingService";
export default {
  name: "pending-readings",

  data() {
    return {
      isLoading: false,
      readings: [],
      currentReading: {},
      waterMeterImageUrl: "",
      branchId: 145,
      errorMessage: "",
      consumption: 0,
      current: 0,
      imageTemplate:
        "https://firebasestorage.googleapis.com/v0/b/tsenane-994c3.appspot.com/o/images%2F20200900627_4029b1a2-530b-49c8-9e4f-c5ba98fd7887?alt=media&token=74344817-5d8f-4deb-94a6-e1d64e8cbd6d",
    };
  },

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company", "branches"]),
  },

  watch: {
    current: function () {
      if (this.current < this.currentReading.last) {
        this.consumption = 0;
        return;
      }
      this.consumption =
        parseFloat(this.current) - parseFloat(this.currentReading.last);
    },
  },

  created() {
    this.getPendingReadins();
  },

  methods: {
    async getPendingReadins() {
      this.isLoading = true;

      try {
        const pendingReadings = await ReadingService.getReadings(
          this.branchId,
          this.token
        );
        if (pendingReadings.success == true) {
          this.readings = pendingReadings.result;
          this.isLoading = false;
        } else {
          this.readings = [];
          this.isLoading = false;
          this.errorMessage = pendingReadings.message;
        }
      } catch (error) {
        console.log(error);
        this.isLoading = false;
      }
    },

    validateReading(reading) {
      this.currentReading = reading;
      this.consumption = reading.consumption;
      this.current = reading.current;
      this.$refs["update-reading"].show();
    },

    async setReadingsUpdate(number) {
      this.isLoading = true;
      if (number == 1) {
        // cancelling the validation of the reading
        this.isLoading = false;
        this.$refs["update-reading"].hide();
      } else if (number > 1) {
        // Rejecting or Validating the reading
        const status = number == 2 ? 2 : 0;

        const passingValues = {
          id: this.currentReading.id,
          status,
          current: this.current,
          consumption: this.consumption,
          consumoFacturado: this.consumption,
        };
        try {
          const validation = ReadingService.validateReading(
            passingValues,
            this.token
          );
          if (validation.success == true) {
            this.isLoading = false;
            console.log(validation.message);
            this.$refs["update-reading"].hide();
            this.getPendingReadins();
          } else {
            this.isLoading = false;
            this.$refs["update-reading"].hide();
            console.log(validation.message);
          }
        } catch (err) {
          this.$refs["update-reading"].hide();
          this.isLoading = false;
          console.log(err);
        }
      }
    },

    tariffId(tariff) {
      if (tariff == 1) {
        return "Tarifa Fontenária";
      } else if (tariff == 2) {
        return "Tarifa Doméstica";
      } else if (tariff == 3) {
        return "Tarifa Municipal";
      } else if (tariff == 4) {
        return "Tarifa Comercial";
      } else if (tariff == 5) {
        return "Tarifa Pública";
      } else if (tariff == 6) {
        return "Tarifa Industrial";
      } else if (tariff == 7) {
        return "Tarifa Toma de Água";
      }
    },
  },
};
</script>
