import axios from 'axios'
const url = 'api/proforma'

/* eslint-disable no-async-promise-executor */

class ProformaService {
    // Get User
    static getAllProforma(token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static getProformaById(proformaId, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${proformaId}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static deleteProforma(proformaId, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.delete(`${url}/${proformaId}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static insertProforma(proforma, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, proforma, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default ProformaService