<script>
import { mapGetters } from "vuex";

import PaymentService from "../../http-service/PaymentService";
import printJS from "print-js";

export default {
  name: "simulator-modal",

  props: ["purchaseDetails", "currentTarif", "quantity"],

  data() {
    return {
      show: false,
      prePaidMeterDue: 233.3333333333333,
      vendingSystem: 260.00,
      vendingSystemSecondMonth: 40.00,
      installationMeterBox: 1512.16,
      cutValve: 30.50,
      vat: 0,
      vatSecondMonthOnWards: 0,
      subtotalOfMeterInstallation: 0,
      total2PayFirstMonth: 0.00,
      total2PaySecondMonthOnwards: 0.00
    };
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "currentCustomer",
      "availableCustomerBalance",
    ]),
  },

  created() {
    console.log(this.purchaseDetails);
  },

  methods: {
    printDocument() {
      printJS("printable", "html");
    },

    totalMeterDues() {
      const subTotalFirstMonth = this.prePaidMeterDue + this.vendingSystem + this.installationMeterBox + this.cutValve
      this.vat = subTotalFirstMonth * 0.17
      this.subtotalOfMeterInstallation = this.vat + subTotalFirstMonth
      this.total2PayFirstMonth = this.subtotalOfMeterInstallation + this.purchaseDetails[0].total

      const secondMonthOnwards = this.prePaidMeterDue + this.vendingSystemSecondMonth
      this.vatSecondMonthOnWards = secondMonthOnwards * 0.17
      this.total2PaySecondMonthOnwards = secondMonthOnwards + this.vatSecondMonthOnWards + this.purchaseDetails[0].total
      
      return this.vat
    },

    async finalizePurchase() {
      this.show = true;

      const totalDue = parseFloat(this.purchaseDetails[0].total);
      const availableBalance = parseFloat(
        this.availableCustomerBalance == false
          ? 0
          : this.availableCustomerBalance[0].balance
      );

      const remainderBalance = availableBalance - totalDue;
      if (remainderBalance < 0) {
        this.$bvToast.toast(
          "Sem saldo suficiente para efectuar esta compra. O seu saldo é de " +
            availableBalance.toFixed(2) +
            " MZN",
          {
            variant: "warning",
            solid: true,
          }
        );
        this.show = false;
      } else {
        const passingValues = {
          customerId: this.currentCustomer.id,
          customerName: this.currentCustomer.name,
          meterNumber: this.currentCustomer.barcode,
          phoneNumber: this.currentCustomer.phone,
          tariffType: this.getTariffType(this.currentCustomer.tarrifTypeId),
          branchId: this.currentCustomer.branchId,
          staffId: this.currentUser[0].id,
          last: 0,
          current: 0,
          quantity: this.quantity,
          fixedTax: this.purchaseDetails[0].fixedTax,
          vat: this.purchaseDetails[0].tax,
          subTotal: this.purchaseDetails[0].subTotal,
          fine: 0,
          totalPaid: this.purchaseDetails[0].total,
          staff: this.currentUser[0].name,
          voucher: Math.floor(Math.random() * 100000000000000000000 - 1),
          remainderBalance: remainderBalance,
        };

        // console.log(remainderBalance);

        try {
          const purchase = PaymentService.insertPurchase(
            passingValues,
            this.token
          );
          this.$bvToast.toast(purchase.message, {
            variant: "success",
            solid: true,
          });
          this.show = false;
          this.printDocument();
          this.$emit("finalize-purchase");
        } catch (err) {
          this.$bvToast.toast(err.message, {
            variant: "danger",
            solid: true,
          });
          this.show = false;
        }
      }
    },

    getTariffType(tariff) {
      if (tariff == 1) {
        this.quantityMinTariff = 0;
        return "Fontenária";
      } else if (tariff == 2) {
        this.quantityMinTariff = 5;
        return "Doméstica";
      } else if (tariff == 3) {
        this.quantityMinTariff = 0;
        return "Municipal";
      } else if (tariff == 4) {
        this.quantityMinTariff = 25;
        return "Comércio & Público";
      } else if (tariff == 5) {
        this.quantityMinTariff = 50;
        return "Indústria";
      }
    },
  },
};
</script>