<template>
  <div>
    <nav-bar />
    <b-row>
      <b-col>
        <b-card header-tag="header">
          <b-row>
            <b-col lg="2"
              ><b-button variant="outline-primary" block>
                <b-icon icon="plus-square-fill"></b-icon>
                <span> Compor Mensagem</span></b-button
              >
              <!-- <hr class="bg-primary mt-1 mb-1" /> -->
              <b-list-group class="mt-1">
                <b-list-group-item
                  v-for="category in messageCategory"
                  :key="category.value"
                  class="d-flex justify-content-between align-items-center"
                  @click="getMessages(category.value)"
                >
                  <small
                    ><b>{{ category.text }}</b></small
                  >
                  <b-badge variant="secondary" pill>{{
                    getMessagesCount(category.value)
                  }}</b-badge>
                </b-list-group-item>
              </b-list-group>
            </b-col>
            <b-col class="">
              <b-input-group class="input-messages">
                <b-input-group-prepend is-text>
                  <b-icon icon="chat-dots-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                  type="text"
                  size="md"
                  @keyup="searchMessages()"
                  v-model="search"
                  placeholder="Pesquisar mensagens"
                ></b-form-input>
              </b-input-group>
              <!-- Message Body Component -->
              <b-overlay
                :show="isLoading"
                rounded="lg"
                class="mt-2 mb-2 bg-white"
              >
                <b-row class="mt-0" v-if="filteredMessages.length > 0">
                  <b-col>
                    <b-row
                      v-for="message in filteredMessages"
                      :key="message.id"
                      class="message-body"
                    >
                      <b-col lg="3">{{ message.customerName }}</b-col>
                      <b-col>{{ message.messageBody }}</b-col>
                      <b-col lg="2" class="text-right">{{
                        message.sentDateTime
                      }}</b-col>
                    </b-row>
                  </b-col>
                </b-row>
              </b-overlay>
              <!-- End Message Body Component -->
            </b-col>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import NavBar from "../components/Navbar";
import { mapGetters } from "vuex";

import SupportService from "../http-service/SupportService";

export default {
  name: "Profile",

  computed: {
    ...mapGetters(["user", "token"]),
  },

  created() {
    this.getAllMessages();
    // console.log(this.user);
  },

  components: {
    NavBar,
  },

  data() {
    return {
      isLoading: false,
      search: "",
      messages: [],
      filteredMessages: [],
      messageCategory: [
        { value: 0, text: "Reporte de fugas" },
        { value: 1, text: "Falta de água" },
        { value: 2, text: "Avarias" },
        { value: 3, text: "Falta de facturas" },
        { value: 4, text: "Reclamações" },
        { value: 5, text: "Outros" },
      ],
    };
  },

  methods: {
    searchMessages() {
      console.log("Hello from searching messages.");
    },

    async sendMessages() {
      this.isLoading = true;
    },

    async getAllMessages() {
      this.isLoading = true;
      try {
        const messages = await SupportService.getAllMessages(this.token);
        if (messages.success == true) {
          this.messages = messages.result;
          this.$store.commit("SET_NOTIFICATIONS", this.messages);
          this.isLoading = false;
        } else {
          this.messages = [];
          this.isLoading = false;
          this.$store.commit("SET_NOTIFICATIONS", []);
        }
      } catch (err) {
        this.$store.commit("SET_NOTIFICATIONS", []);
        this.isLoading = false;
        console.log(err);
      }
    },

    getMessagesCount(categoryType) {
      const filteredMessage = this.messages.filter((a) => {
        return a.categoryType == categoryType;
      });
      return filteredMessage.length;
    },

    getMessages(categoryType) {
      this.filteredMessages = this.messages.filter((a) => {
        return a.categoryType == categoryType;
      });
    },
  },
};
</script>

<style scoped>
.input-messages {
  width: 100%;
}
.message-body {
  font-size: 10pt;
  font-family: "Courier New", Courier, monospace;
  border-bottom: 1px solid burlywood;
}
</style>
