                        </th>
                      </tr>
                      <tr>
                        <td colspan="2"><b>Facturação sem IVA</b></td>
                        <td class="text-right">
                          {{ fountainWithoutVat.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ domesticWithoutVat.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ municipalWithoutVat.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ publicWithoutVat.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ commercialWithoutVat.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ industrialWithoutVat.toFixed(2) }}
                        </td>
                        <th
                          class="text-right"
                          v-money-format="totalInvoicedWithoutVat"
                        ></th>
                      </tr>
                      <tr>
                        <td colspan="2"><b>Valor de IVA (12%)</b></td>
                        <td class="text-right">
                          {{ calculateVat(fountainWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ calculateVat(domesticWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ calculateVat(municipalWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ calculateVat(publicWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ calculateVat(commercialWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ calculateVat(industrialWithoutVat).toFixed(2) }}
                        </td>
                        <th
                          class="text-right"
                          v-money-format="calculateVat(totalInvoicedWithoutVat)"
                        ></th>
                      </tr>
                      <tr>
                        <td colspan="2"><b>Facturação com IVA</b></td>
                        <td class="text-right">
                          {{ billWithVat(fountainWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ billWithVat(domesticWithoutVat).toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ billWithVat(municipalWithoutVat).toFixed(2) }}
                        </td>
