                        <td class="text-right">{{ publicDebts.toFixed(2) }}</td>
                        <td class="text-right">
                          {{ commercialDebts.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ industrialDebts.toFixed(2) }}
                        </td>
                        <th class="text-right" v-money-format="totalDebts"></th>
                      </tr>
                      <tr>
                        <td colspan="2"><b>Total facturado</b></td>
                        <td class="text-right">
                          {{
