                            ).toFixed(2)
                          }}
                        </td>
                        <td class="text-right">
                          {{
                            (
                              billWithVat(domesticWithoutVat) + domesticDebts
                            ).toFixed(2)
                          }}
                        </td>
                        <td class="text-right">
                          {{
                            (
                              billWithVat(municipalWithoutVat) + municipalDebts
                            ).toFixed(2)
                          }}
                        </td>
                        <td class="text-right">
                          {{
                            (
                              billWithVat(publicWithoutVat) + publicDebts
                            ).toFixed(2)
                          }}
                        </td>
                        <td class="text-right">
                          {{
                            (
                              billWithVat(commercialWithoutVat) +
                              commercialDebts
                            ).toFixed(2)
                          }}
                        </td>
                        <td class="text-right">
                          {{
                            (
                              billWithVat(industrialWithoutVat) +
                              industrialDebts
                            ).toFixed(2)
                          }}
                        </td>
                        <th
                          class="text-right"
                          v-money-format="
                            totalDebts + billWithVat(totalInvoicedWithoutVat)
                          "
                        ></th>
                      </tr>
                      <tr>
                        <th colspan="2"><b>Valor recebido</b></th>
                        <td class="text-right">
                          {{ fountainTotalPaid.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ domesticTotalPaid.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ municipalTotalPaid.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ publicTotalPaid.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ commercialTotalPaid.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ industrialTotalPaid.toFixed(2) }}
                        </td>
                        <th class="text-right" v-money-format="totalPaid"></th>
                      </tr>
                      <tr>
                        <th colspan="2">
                          <b>Valor em dívida durante o mês</b>
                        </th>
                        <td class="text-right">
                          {{ fountainUnpaidPaidDuringMonth.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ domesticUnpaidPaidDuringMonth.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ municipalUnpaidPaidDuringMonth.toFixed(2) }}
                        </td>
                        <td class="text-right">
                          {{ publicUnpaidPaidDuringMonth.toFixed(2) }}
                        </td>
                        <td class="text-right">
