                        <th
                          class="text-right"
                          v-money-format="totalUnpaidPaidDuringMonth"
                        ></th>
                      </tr>
                      <tr>
                        <th colspan="8">
                          <b>TAXA DO CEDENTE - 12% DE FACTURAÇÃO SEM IVA</b>
                        </th>
                        <th
                          class="text-right"
                          v-money-format="
                            calculateTaxaCedente(totalInvoicedWithoutVat)
                          "
                        ></th>
                      </tr>
                      <tr>
                        <th class="text-center text-white bg-dark" colspan="12">
                          <b>DÍVIDAS ACOMULADAS</b>
                        </th>
                      </tr>
                      <tr>
                        <th colspan="2"><b>TOTAL EM DÍVIDA</b></th>
                        <th class="text-right" v-money-format="getDebtByCat(1)"></th>
                        <th class="text-right" v-money-format="getDebtByCat(2)"></th>
                        <th class="text-right" v-money-format="getDebtByCat(3)"></th>
                        <th class="text-right" v-money-format="getDebtByCat(4)"></th>
                        <th class="text-right" v-money-format="getDebtByCat(5)"></th>
                        <th class="text-right" v-money-format="getDebtByCat(6)"></th>
                        <th class="text-right" v-money-format="getTotalDebt()"></th>
                      </tr>
                    </tbody>
                  </table>
                </b-col>
                <b-col>
                  <b-row>
                    <b-col><b>Notas importantes</b></b-col>
                    <b-col class="text-right">
                      <b-button
                        @click="insertReportNote()"
                        :disabled="isLoading || !readings"
                        block
                        size="sm"
                        variant="success"
                      >
                        <b-icon icon="journal-plus"></b-icon>
                        Salvar
                      </b-button></b-col
                    >
                  </b-row>
                  <b-form-textarea
                    class="mt-1"
                    id="important-notes"
                    size="sm"
                    v-model="reportNote"
                    placeholder="Adicionar uma nota ao relatório"
                    rows="3"
                    max-rows="8"
                    no-auto-shrink
                  ></b-form-textarea>
                  <b-list-group class="mt-1">
                    <b-list-group-item
                      v-for="note in reportNotesList"
                      :key="note.id"
                      ><small
                        ><b>{{ note.id }}</b
                        >. {{ note.description }}</small
                      ></b-list-group-item
                    >
                  </b-list-group>
                </b-col>
              </b-row>
              <b-row v-else>
                <b-col>
                  <b-list-group class="text-danger">
                    <b-list-group-item class="text-center text-danger">
                      <h5>Houve um erro ao gerar o relatório</h5>
                    </b-list-group-item>
                  </b-list-group>
                </b-col>
              </b-row>
