      </div>
    </b-container>
  </div>
</template>

<script>
import NavBar from "../components/Navbar";
import Vue from "vue";
import { mapGetters } from "vuex";
import moment from "moment";
moment.locale("pt-br");

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

import CustomerReport from "../http-service/CustomerReport";
import PaymentService from "../http-service/PaymentService";
import ReportNoteService from "../http-service/ReportNotesService";

import MoneyFormat from "../calcs/MoneyFormat";

export default Vue.extend({
  name: "Relatorios",

  created() {
    this.searchPayments();
    this.getAcomulatedDebtsByCategory()
  },

  mounted() {},

  components: {
    NavBar,
  },

  data() {
    return {
      isLoading: false,
      serverError: false,

      fromDate: moment().format("YYYY-MM-01"),
      toDate: moment().format("YYYY-MM-") + moment().daysInMonth(),
      month: new Date().getMonth(),
      reportType: 0,
      consumptionPeriod: moment().format("YYYY-MM-01"),

      customers: [],
      payments: [],
      totalPaid: 0,
      readings: [],
      debts: [],
      accomulatedDebts: [],

      // NOTAS IMPORTANTES
      reportNote: "",
      reportNotesList: [],
      noteId: 1,

      // TOTAL OF EXISTING CUSTOMERS
      fountainTotal: 0,
      domesticTotal: 0,
      municipalTotal: 0,
      publicTotal: 0,
      commercialTotal: 0,
      industrialTotal: 0,

      // TOTAL OF DEACTIVATED CUSTOMERS
      totalDeactivated: 0,
      fountainDeactivated: 0,
      domesticDeactivated: 0,
      municipalDeactivated: 0,
      publicDeactivated: 0,
      commercialDeactivated: 0,
      industrialDeactivated: 0,
      // TOTAL OF BILLED CUSTOMERS
      fountainReadingsTotal: 0,
      domesticReadingsTotal: 0,
      municipalReadingsTotal: 0,
      publicReadingsTotal: 0,
      commercialReadingsTotal: 0,
      industrialReadingsTotal: 0,
      // TOTAL OF VOLUME OF WATER BILLED
      fountainVolumeBilled: 0,
      domesticVolumeBilled: 0,
      municipalVolumeBilled: 0,
      publicVolumeBilled: 0,
      commercialVolumeBilled: 0,
      industrialVolumeBilled: 0,
      totalVolumeBilled: 0,
      // TOTAL INVOICED WITHOUT VAT
      totalInvoicedWithoutVat: 0,
      fountainWithoutVat: 0,
      domesticWithoutVat: 0,
      municipalWithoutVat: 0,
      publicWithoutVat: 0,
      commercialWithoutVat: 0,
      industrialWithoutVat: 0,
      // TOTAL DEBTS BY CATEGORY
      totalDebts: 0,
      fountainDebts: 0,
      domesticDebts: 0,
      municipalDebts: 0,
      publicDebts: 0,
      commercialDebts: 0,
      industrialDebts: 0,
      // TOTAL PAID BY CATEGORY
      fountainTotalPaid: 0,
      domesticTotalPaid: 0,
      municipalTotalPaid: 0,
      publicTotalPaid: 0,
      commercialTotalPaid: 0,
      industrialTotalPaid: 0,

      // TOTAL UNPAID BY CATEGORY DURING THE MONTH
      fountainUnpaidPaidDuringMonth: 0,
      domesticUnpaidPaidDuringMonth: 0,
      municipalUnpaidPaidDuringMonth: 0,
      publicUnpaidPaidDuringMonth: 0,
      comercialUnpaidPaidDuringMonth: 0,
      industrialUnpaidPaidDuringMonth: 0,
      totalUnpaidPaidDuringMonth: 0,

      // TOTAL UNPAID BILLS AND BILLS PAID PARTIALLY
      totaUnpaidBillsAndBillsPaidPartially: 0,
    };
  },

  computed: {
    ...mapGetters(["user", "token", "user", "company"]),
  },

  watch: {
    reportType: function () {
      if (this.reportType == 1) {
        this.generateComercialFinancialReport();
      }
    },
  },

  methods: {
    async getAcomulatedDebtsByCategory() {
      const categories = [1, 2, 3, 4, 5, 6, 7]

      categories.forEach(async (cat) => {
        const passingValue = {
          tariffId: cat
        }
        try {
          const debts = await ReportNoteService.getDebtsByCategory(passingValue, this.token)
          if(debts.success == true) {
            // console.log({ cat, debts: debts.result })
            this.accomulatedDebts.push({ cat: cat, debt: debts.result }) 
          } else {
            console.log(debts)
          }
        } catch(err) {
          console.log(err)
        }
      })
    },

    getDebtByCat(value) {
      const debtValue = this.accomulatedDebts.filter((a) => {
        return a.cat == value;
      });
      console.log(debtValue[0])
      return debtValue[0].debt
    },

    getTotalDebt() {
      return this.accomulatedDebts.reduce(
        function (acumulador, valorAtual) {
          return acumulador + valorAtual.debt;
        },
        0
      );
    },

    async getAllReportNotes() {
      this.isLoading = true;

      const passingValues = {
        month: this.readings ? this.readings[0].month : 0,
        year: this.readings ? this.readings[0].year : 0,
      };

      try {
        const reportNotes = await ReportNoteService.getAllService(
          passingValues,
          this.token
        );

        if (reportNotes.success == true) {
          this.reportNotesList = reportNotes.result;
        } else {
          this.reportNotesList = [];
        }
        this.isLoading = false;
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.reportNotesList = [];
        this.isLoading = false;
      }
    },

    async insertReportNote() {
      const passingValues = {
        description: this.reportNote,
        staffName: this.user[0].name,
        month: this.readings[0].month,
        year: this.readings[0].year,
      };

      if (this.reportNote.length < 50) {
        this.$bvToast.toast("A sua nota deve conter pelo menos 50 carateres.", {
          title: "Erro!",
          variant: "warning",
          solid: true,
        });
        return;
      }

      this.isLoading = true;

      try {
        const insertNote = await ReportNoteService.insertReportNote(
          passingValues,
          this.token
        );
        if (insertNote.success == true) {
          this.$bvToast.toast(insertNote.message, {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
          this.getAllReportNotes();
        } else {
          this.$bvToast.toast(insertNote.message, {
            title: "Erro!",
            variant: "danger",
            solid: true,
          });
        }
        this.isLoading = false;
      } catch (err) {
        console.log(err);
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
      this.reportNote = "";
      this.noteId = this.noteId++;
    },

    calculateTaxaCedente(value) {
      return value * 0.12;
    },

    calculateVat(amount) {
      return amount * 0.12;
    },

    billWithVat(value) {
      return value * 1.12;
    },

    filterCustomersByCategoryAndStatus() {
      const totalDeactivated = this.customers.filter((a) => {
        return a.status == 2;
      });
      this.totalDeactivated = totalDeactivated.length;

      const fountainDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 1 && a.status == 2;
      });
      this.fountainDeactivated = fountainDeactivated.length;

      const domesticDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 2 && a.status == 2;
      });
      this.domesticDeactivated = domesticDeactivated.length;

      const municipalDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 3 && a.status == 2;
      });
      this.municipalDeactivated = municipalDeactivated.length;

      const publicDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 5 && a.status == 2;
      });
      this.publicDeactivated = publicDeactivated.length;

      const commercialDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 4 && a.status == 2;
      });
      this.commercialDeactivated = commercialDeactivated.length;

      const industrialDeactivated = this.customers.filter((a) => {
        return a.tarrifTypeId == 6 && a.status == 2;
      });
