    searchPayments() {
      this.getReports();
      this.getPayments();
    },

    async getPayments() {
      this.isLoading = true;
      const passingValues = {
        from: this.fromDate,
        to: this.toDate,
        paymentMethod: 0,
      };

      try {
        const payments = await PaymentService.getAllPayments(
          passingValues,
          this.token
        );
        if (payments.success == true) {
          this.payments = payments.result;
          this.totalPaid = payments.totalRecebido;
          this.serverError = true;
        } else {
          this.payments = [];
          this.totalPaid = 0;
          this.$bvToast.toast(payments.message, {
            title: "Erro:",
            variant: "info",
            solid: true,
          });
          this.serverError = false;
        }
        this.isLoading = false;
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro:",
          variant: "info",
          solid: true,
        });
        this.isLoading = false;
      }
    },

    async getReports() {
      const passingValues = {
        from: this.fromDate,
        to: this.toDate,
        month: this.month,
      };
      this.isLoading = true;
      try {
        const reports = await CustomerReport.getReports(
          passingValues,
          this.token
        );
        if (reports.success == true) {
          this.customers = reports.customers;
          this.readings = reports.readings;
          this.debts = reports.debts;
          this.getAllReportNotes();
          this.populateData();
          this.serverError = true;
        } else {
          this.customers = [];
          this.debts = [];
          this.readings = [];
          this.$bvToast.toast(reports.message, {
            title: "Erro:",
            variant: "info",
            solid: true,
          });
          this.serverError = false;
          this.isLoading = false;
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Error:",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },

    getTotalUnpaidByCategory() {
      // FOUNTAINS UNPAID DURING THE MONTH
      const fountainUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 1 && a.status == 0;
      });
      this.fountainUnpaidPaidDuringMonth = fountainUnpaidPaidDuringMonth.reduce(
        function (acumulador, valorAtual) {
          return acumulador + valorAtual.totalOfBill;
        },
        0
      );
      // console.log("Fountain Unpaid", this.fountainUnpaidPaidDuringMonth);
      // DOMESTIC UNPAID DURING THE MONTH
      const domesticUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 2 && a.status == 0;
      });
      this.domesticUnpaidPaidDuringMonth = domesticUnpaidPaidDuringMonth.reduce(
        function (acumulador, valorAtual) {
          return acumulador + valorAtual.totalOfBill;
        },
        0
      );
      // console.log("Domestic Unpaid", this.domesticUnpaidPaidDuringMonth);
      // MUNICIPAL UNPAID DURING THE MONTH
      const municipalUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 3 && a.status == 0;
      });
      this.municipalUnpaidPaidDuringMonth =
        municipalUnpaidPaidDuringMonth.reduce(function (
          acumulador,
          valorAtual
        ) {
          return acumulador + valorAtual.totalOfBill;
        },
        0);
      // console.log("Municipal Unpaid", this.municipalUnpaidPaidDuringMonth);
      // PUBLIC UNPAID DURING THE MONTH
      const publicUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 5 && a.status == 0;
      });
      this.publicUnpaidPaidDuringMonth = publicUnpaidPaidDuringMonth.reduce(
        function (acumulador, valorAtual) {
          return acumulador + valorAtual.totalOfBill;
        },
        0
      );
      // console.log("Public Unpaid", this.publicUnpaidPaidDuringMonth);
      // COMMERCIAL UNPAID DURING THE MONTH
      const comercialUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 4 && a.status == 0;
      });
      this.comercialUnpaidPaidDuringMonth =
        comercialUnpaidPaidDuringMonth.reduce(function (
          acumulador,
          valorAtual
        ) {
          return acumulador + valorAtual.totalOfBill;
        },
        0);
      // console.log("Comercial Unpaid", this.comercialUnpaidPaidDuringMonth);
      // INDUSTRIAL UNPAID DURING THE MONTH
      const industrialUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.tarrifTypeId == 6 && a.status == 0;
      });
      this.industrialUnpaidPaidDuringMonth =
        industrialUnpaidPaidDuringMonth.reduce(function (
          acumulador,
          valorAtual
        ) {
          return acumulador + valorAtual.totalOfBill;
        },
        0);
      // console.log("Industrial Unpaid", this.industrialUnpaidPaidDuringMonth);
      // TOTAL UNPAID DURING THE MONTH
      const totalUnpaidPaidDuringMonth = this.readings.filter((a) => {
        return a.status == 0;
      });
      this.totalUnpaidPaidDuringMonth = totalUnpaidPaidDuringMonth.reduce(
        function (acumulador, valorAtual) {
          return acumulador + valorAtual.totalOfBill;
        },
        0
      );
      // console.log("Total Unpaid", this.totalUnpaidPaidDuringMonth);
    },

    populateData() {
      this.consumptionPeriod = moment(this.readings[0].consumptionPeriod)
        .format("MMMM YYYY")
        .toUpperCase();
      this.filterCustomersByCategory();
      this.filterCustomersByCategoryAndStatus();
      this.filterTotalReadingsByCategory();
      this.getBilledVolumeOfWaterByCategory();
      this.getInvoiceWithoutVatByCategory();
      this.getDebtsByCategory();
      this.getPaymentsByCategory();
      this.getTotalUnpaidByCategory();
      this.isLoading = false;
    },

    reportNoteItems() {
      const bodyList = [];

      this.reportNotesList.forEach((element) => {
        bodyList.push({ text: element.description, fontSize: 9 });
      });
      // console.log(bodyList);
      return bodyList;
    },

