import axios from 'axios'
const url = 'api/mpesa'

/* eslint-disable no-async-promise-executor */

class MpesaService {
    // Get User
    static c2b(mpesaValues, token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, mpesaValues, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static b2c(mpesaValues, token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.put(url, mpesaValues, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default MpesaService