<template>
  <div>
    <nav-bar />
    <b-container class="bv-example-row mt-2">
      <b-row>
        <b-col>
          <h4 ref="content" class="texte-center text-white">
            <center>
              {{ currentUser[0].name }} -
              <small>{{ getUserProfile(currentUser[0].userRole) }}</small>
            </center>
          </h4>
        </b-col>
      </b-row>
      <hr class="bg-info mt-1" />
      <b-row>
        <b-col lg="3"></b-col>
        <b-col>
          <b-card header-tag="header">
            <template v-slot:header>
              <h5 class="mb-0">{{ $t("update_profile") }}</h5>
            </template>
            <small>{{ $t("name") }}</small>
            <b-form-group id="name" label-for="name">
              <b-form-input
                id="name"
                type="text"
                v-model="name"
                :placeholder="$t('name')"
                size="sm"
                disabled
              ></b-form-input>
            </b-form-group>

            <small>{{ $t("phone") }}</small>
            <b-form-group id="phone" label-for="phone">
              <b-form-input
                id="phone"
                type="text"
                v-model="phone"
                :placeholder="$t('phone')"
                size="sm"
                disabled
              ></b-form-input>
            </b-form-group>

            <small>{{ $t("old_password") }}</small>
            <b-form-group label-for="old_password">
              <b-form-input
                id="oldPassword"
                type="password"
                v-model="oldPassword"
                :placeholder="$t('old_password')"
                size="sm"
                :disabled="isLoading"
              ></b-form-input>
            </b-form-group>

            <small>{{ $t("new_password") }}</small>
            <b-form-group label-for="new_password">
              <b-form-input
                id="newPassword"
                type="password"
                v-model="newPassword"
                :placeholder="$t('new_password')"
                size="sm"
                :disabled="isLoading"
              ></b-form-input>
            </b-form-group>

            <small>{{ $t("confirm_password") }}</small>
            <b-form-group label-for="confirm_password">
              <b-form-input
                id="confirmPassword"
                type="password"
                v-model="confirmPassword"
                :placeholder="$t('confirm_password')"
                size="sm"
                :disabled="isLoading"
              ></b-form-input>
            </b-form-group>

            <b-button
              class="mt-3 sm"
              :disabled="
                confirmPassword.length < 6 ||
                newPassword.length < 6 ||
                oldPassword.length < 6 ||
                isLoading == true
              "
              block
              @click="updateProfile()"
              size="sm"
            >
              <template v-if="isLoading">
                {{ $t("updating") }}
                <b-spinner small type="grow"></b-spinner>
              </template>
              <template v-else>{{ $t("update") }}</template>
            </b-button>
          </b-card>
        </b-col>
        <b-col lg="3"></b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import NavBar from "../components/Navbar";

import { mapGetters } from "vuex";

import UserLoginService from "../http-service/UserLoginService";

export default {
  name: "Profile",

  computed: {
    ...mapGetters(["user", "currentUser", "token"]),
  },

  created() {
    this.$store.commit("SET_CLEAR_CURRENT_VENDING");
    this.populateValues();
  },

  components: {
    NavBar,
  },

  data() {
    return {
      isLoading: false,
      name: "",
      phone: "",
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
      passwordConfirmation: "",
    };
  },

  methods: {
    populateValues() {
      this.name = this.currentUser[0].name;
      this.phone = this.currentUser[0].phoneNumber;
    },

    async updateProfile() {
      this.isLoading = true;

      if (this.currentUser[0].code == this.oldPassword) {
        // console.log(this.oldPassword, this.currentUser[0].code);
        if (this.newPassword == this.confirmPassword) {
          // Update Password
          const passingValues = {
            id: this.currentUser[0].id,
            code: this.newPassword,
          };
          // console.log(passingValues);
          try {
            const update = await UserLoginService.updatePassword(
              passingValues,
              this.token
            );
            if (update.success == true) {
              this.$bvToast.toast(update.message, {
                title: "",
                variant: "success",
                solid: true,
              });
            } else {
              this.$bvToast.toast(update.message, {
                title: "",
                variant: "warning",
                solid: true,
              });
            }
          } catch (err) {
            this.$bvToast.toast(err.message, {
              title: "",
              variant: "error",
              solid: true,
            });
          }
        } else {
          this.$bvToast.toast(
            "Nova senha não coincide com a senha de confirmação.",
            {
              title: "",
              variant: "danger",
              solid: true,
            }
          );
        }
      } else {
        this.$bvToast.toast(
          "A senha antiga não coincide com a que digitou, tente outra vez.",
          {
            title: "",
            variant: "danger",
            solid: true,
          }
        );
      }
      this.isLoading = false;
      this.oldPassword = "";
      this.newPassword = "";
      this.confirmPassword = "";
    },

    getUserProfile(userRole) {
      if (userRole == 1) {
        return "Súper Admin";
      } else if (userRole == 3) {
        return "Caixa";
      }
    },
  },
};
</script>