<template>
  <div name="new-cashier">
    <nav-bar-cashier />
    <b-container fluid class="bv-example-row mt-2">
      <b-col>
        <div class="card">
          <div class="card-body">
            <b-row
              ><b-col lg="3">
                <b-input-group size="md" class="mb-2">
                  <b-input-group-prepend is-text>
                    <b-icon icon="search"></b-icon>
                  </b-input-group-prepend>
                  <b-form-input
                    type="text"
                    size="md"
                    @keyup="searchCustomer()"
                    v-model="searchValue"
                    placeholder="Pesquisar clientes"
                  ></b-form-input>
                </b-input-group>
                <b-row class="mt-1">
                  <b-col>
                    <b-overlay :show="isLoading" rounded="sm" class="bg-white">
                      <b-list-group v-if="customers.length > 0">
                        <b-list-group-item
                          v-for="customer in customers"
                          :key="customer.id"
                          style="cursor: pointer; font-size: 10pt"
                          @click="openVendingPanel(customer)"
                          class="flex-column align-items-start"
                        >
                          <div
                            class="d-flex w-100 justify-content-between text-secondary"
                          >
                            <h2 class="mb-0" style="font-size: 10pt">
                              <b-icon
                                v-if="customer.status == 1"
                                icon="person-fill"
                              ></b-icon>
                              <b-icon
                                v-else
                                icon="person-fill"
                                variant="danger"
                              ></b-icon>
                              &nbsp;&nbsp;
                              <small
                                ><b>{{ customer.name }}</b></small
                              >
                            </h2>
                            <small class="text-muted">{{
                              tariffId(customer.tarrifTypeId)
                            }}</small>
                          </div>

                          <p class="mb-0">
                            <small class="text-muted"
                              >{{ customer.address }} - {{ customer.barcode }}
                              <br /><b
                                ><em>{{
                                  getMeterType(customer.meeter_type)
                                }}</em></b
                              ></small
                            >
                          </p>
                        </b-list-group-item>
                      </b-list-group>

                      <b-list-group class="text-danger" v-if="error">
                        <b-list-group-item class="text-center text-danger">
                          <h5>Sem nenhum resultado</h5>
                        </b-list-group-item>
                      </b-list-group>
                    </b-overlay>
                  </b-col>
                </b-row>
              </b-col>
              <b-col>
                <ValidationComponent v-if="clickedCustomer" />
                <b-alert variant="success" show v-else>
                  <b-icon
                    icon="exclamation-circle-fill"
                    variant="info"
                    font-scale="1.5"
                  ></b-icon>
                  Seleccione um cliente por favor
                </b-alert>
              </b-col>
            </b-row>
          </div>
        </div>
      </b-col>
    </b-container>
  </div>
</template>

<script>
import moment from "moment";
import NavBarCashier from "../components/NavbarCashier";
import SearchCustomers from "../http-service/SearchCustomers";
import ValidationComponent from "@/components/modals/ValidationComponent";

import { mapGetters } from "vuex";

export default {
  name: "CashierView",

  components: {
    NavBarCashier,
    ValidationComponent,
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "company",
      "currentCustomer",
    ]),
  },

  created() {
    this.getCustomers();
  },

  data() {
    return {
      isLoading: false,
      clickedCustomer: false,
      error: "",
      paymentOptions: [
        { value: 1, text: "Boca do Caixa" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "MPESA Open API" },
        { value: 8, text: "E-Mola" },
      ],
      searchValue: "",
      customers: [],
      today: moment().format("DD-MM-YYYY"),
    };
  },

  methods: {
    async searchCustomer() {
      this.clickedCustomer = false;
      if (this.searchValue.length == 0) {
        this.customers = [];
        this.$store.commit("SET_CURRENT_CUSTOMER", []);
        return;
      }
      this.isLoading = true;
      try {
        let data = await SearchCustomers.searchCustomers(
          this.searchValue,
          this.token
        );
        if (data.success == true) {
          this.customers = data.result;
          this.isLoading = false;
          this.error = false;
        } else {
          this.isLoading = false;
          this.customers = [];
          this.error = true;
        }
      } catch (err) {
        this.clickedCustomer = false;
        this.$bvToast.toast("" + err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.customers = [];
      }
    },

    async getCustomers() {
      this.isLoading = true;
      try {
        let data = await SearchCustomers.getCustomers(this.token);
        if (data.success == true) {
          this.customers = data.result;
          this.isLoading = false;
          this.error = false;
        } else {
          this.isLoading = false;
          this.customers = [];
          this.error = true;
        }
      } catch (err) {
        this.$bvToast.toast("" + err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
        this.customers = [];
      }
    },

    tariffId(tariff) {
      if (tariff == 1) {
        this.quantityMinTariff = 0;
        return "Fontenária";
      } else if (tariff == 2) {
        this.quantityMinTariff = 5;
        return "Doméstica";
      } else if (tariff == 3) {
        this.quantityMinTariff = 0;
        return "Municipal";
      } else if (tariff == 4) {
        this.quantityMinTariff = 15;
        return "Comercial";
      } else if (tariff == 5) {
        this.quantityMinTariff = 15;
        return "Público";
      } else if (tariff == 6) {
        this.quantityMinTariff = 15;
        return "Industrial";
      }
    },

    openVendingPanel(customer) {
      this.clickedCustomer = true;
      this.$store.commit("SET_CURRENT_CUSTOMER", customer);
    },

    getMeterType(meterType) {
      return meterType == 1 ? "Contador pós-pago" : "Contador pré-pago";
    },
  },
};
</script>
