<template>
  <div class="company-table">
    <b-card class="mt-0">
      <b-row class="mb-1">
        <b-col
          ><h5 class="text-black">
            {{ company[0].name }}
          </h5>
          <hr />
          {{ company[0].address }}
          <hr />
          {{ company[0].phone }}
          <hr />
          {{ company[0].email }}
          <hr />
          NUIT: {{ company[0].nuit }}
          <hr />
          {{ company[0].bankAccount }}
          <hr />
          {{ company[0].nib }}
        </b-col>
        <b-col lg="4" md="6" sm="6" class="text-center bg-dark p-3"
          ><b-button
            block
            v-b-modal.modal-center
            variant=""
            @click="openCompanyModal()"
            size="sm"
          >
            <i class="fas fa-edit"></i>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <span>{{ $t("edit-company-details") }}</span>
          </b-button>
          <br />
          <img
            src="../../assets/img/logo_12.jpeg"
            alt="TsenaneApp"
            style="border-radius: 40%"
            width="200"
          />
        </b-col>
      </b-row>
    </b-card>

    <b-modal hide-footer ref="edit-company" :title="$t('edit-company-details')">
      <small>{{ $t("company") }}</small>
      <b-form-group id="company" label-for="company">
        <b-form-input
          type="text"
          v-model="name"
          required
          size="sm"
          :placeholder="$t('company')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("phone") }}</small>
      <b-form-group id="phone" label-for="phone">
        <b-form-input
          type="text"
          v-model="phone"
          required
          size="sm"
          :placeholder="$t('phone')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("address") }}</small>
      <b-form-group id="address" label-for="address">
        <b-form-input
          type="text"
          v-model="address"
          required
          size="sm"
          :placeholder="$t('address')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("email") }}</small>
      <b-form-group id="email" label-for="email">
        <b-form-input
          type="text"
          v-model="email"
          required
          size="sm"
          :placeholder="$t('email')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("nuit") }}</small>
      <b-form-group id="nuit" label-for="nuit">
        <b-form-input
          type="number"
          v-model="nuit"
          required
          size="sm"
          :placeholder="$t('nuit')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("bank-account") }}</small>
      <b-form-group id="bank-account" label-for="bank-account">
        <b-form-input
          type="text"
          v-model="bankAccount"
          required
          size="sm"
          :placeholder="$t('bank-account')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("nib") }}</small>
      <b-form-group id="nib" label-for="nib">
        <b-form-input
          type="text"
          v-model="nib"
          required
          size="sm"
          :placeholder="$t('nib')"
        ></b-form-input>
      </b-form-group>

      <b-button
        class="mt-3 sm"
        :disabled="
          name.length <= 3 || bankAccount.length < 5 || isLoading == true
        "
        block
        @click="submitCompanyDetails()"
        size="sm"
      >
        <template v-if="isLoading">
          {{ $t("submiting") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("submit") }}</template>
      </b-button>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CompanyService from "../../http-service/CompanyService";

export default {
  name: "Company",

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company"]),
  },

  data() {
    return {
      isLoading: false,
      name: "",
      phone: "",
      email: "",
      nuit: "",
      bankAccount: "",
      nib: "",
      address: "",
    };
  },

  created() {},

  methods: {
    populateCompanyData() {
      this.name = this.company[0].name;
      this.nuit = this.company[0].nuit;
      this.phone = this.company[0].phone;
      this.email = this.company[0].email;
      this.address = this.company[0].address;
      this.status = 1;
      this.bankAccount = this.company[0].bankAccount;
      this.nib = this.company[0].nib;
    },

    openCompanyModal() {
      this.populateCompanyData();
      this.$refs["edit-company"].show();
    },

    async submitCompanyDetails() {
      if (this.name.length == 0) {
        this.$bvToast.toast("O campo Nome é de preenchimento obrigatório", {
          title: "",
          variant: "warning",
          solid: true,
        });
      } else if (this.phone.length < 9) {
        this.$bvToast.toast("O campo Telefone é de preenchimento obrigatório", {
          title: "",
          variant: "warning",
          solid: true,
        });
      } else if (this.nuit.length != 9) {
        this.$bvToast.toast("O campo NUIT deve conter 9 dígitos.", {
          title: "",
          variant: "warning",
          solid: true,
        });
      } else if (this.address.length == 0) {
        this.$bvToast.toast(
          "O campo Endereço é de preenchimento obrigatório.",
          {
            title: "",
            variant: "warning",
            solid: true,
          }
        );
      } else if (this.bankAccount.length == 0) {
        this.$bvToast.toast(
          "O campo Conta Bancária é de preenchimento obrigatório.",
          {
            title: "",
            variant: "warning",
            solid: true,
          }
        );
      } else if (this.nib.length == 0) {
        this.$bvToast.toast("O campo NIB é de preenchimento obrigatório.", {
          title: "",
          variant: "warning",
          solid: true,
        });
      } else {
        const passingValues = {
          id: this.company[0].id,
          name: this.name,
          nuit: this.nuit,
          phone: this.phone,
          email: this.email,
          address: this.address,
          status: 1,
          bankAccount: this.bankAccount,
          nib: this.nib,
        };
        this.isLoading = true;
        try {
          const submitCompany = await CompanyService.insertOrUpdateCompanyDetail(
            passingValues,
            this.token
          );
          if (submitCompany.success == true) {
            this.$bvToast.toast(submitCompany.message, {
              title: "",
              variant: "success",
              solid: true,
            });
            this.$store.commit("SET_COMPANY", submitCompany.result);
            this.populateCompanyData();
          } else {
            this.$bvToast.toast(submitCompany.message, {
              title: "",
              variant: "warning",
              solid: true,
            });
          }
        } catch (err) {
          this.$bvToast.toast(err.message, {
            title: "",
            variant: "danger",
            solid: true,
          });
        }
        this.isLoading = false;
        this.$refs["edit-company"].hide();
      }
    },
  },
};
</script>