import axios from 'axios'
const url = 'api/readingList'

/* eslint-disable no-async-promise-executor */

class ReadingListService {

    static getReadingList(readingList, token) { 
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, readingList, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default ReadingListService