<template>
  <div name="staff-list-by-store">
    <b-card class="mt-0">
      <b-row class="mb-1">
        <b-col
          ><h5 class="text-black">
            <small
              >{{ currentBranch.name }} [
              <small>{{
                currentBranchUserList.length + " " + $t("staff_list")
              }}</small>
              ]</small
            >
          </h5></b-col
        >
        <b-col
          lg="4"
          md="6"
          sm="6"
          @click="openModalUser()"
          style="cursor: pointer"
          class="text-right text-primary"
        >
          <b-icon icon="person-plus-fill"></b-icon> {{ $t("new_staff") }}</b-col
        >
      </b-row>
      <table
        v-if="currentBranchUserList.length > 0"
        ref="generate_pdf"
        class="table table-sm table-stripped table-hover"
        id="users-list"
      >
        <thead class="bg-secondary text-white">
          <tr>
            <th>{{ $t("name") }}</th>
            <th>{{ $t("phone") }}</th>
            <th>{{ $t("profile") }}</th>
            <th>{{ $t("status") }}</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="user in currentBranchUserList"
            :key="user.id"
            @click="editUser(user)"
            :class="{
              active: user.status == 1,
              'bg-secondary': user.status == 0,
            }"
          >
            <td>{{ user.name }}</td>
            <td>{{ user.phoneNumber }}</td>
            <td>{{ getUserRole(user.userRole) }}</td>
            <td>{{ user.status == 1 ? $t("active") : $t("inactive") }}</td>
          </tr>
        </tbody>
      </table>
      <p v-else class="text-danger text-left">
        <small
          ><em>{{ $t("no_users") }}</em></small
        >
      </p>
    </b-card>
    <b-modal
      hide-footer
      ref="add-new-user"
      :title="currentBranch.name + ': ' + $t('new_staff')"
    >
      <small>{{ $t("staff_name") }}</small>
      <b-form-group id="userName" label-for="userName">
        <b-form-input
          type="text"
          v-model="userName"
          required
          size="sm"
          :placeholder="$t('staff_name')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("phone") }}</small>
      <b-form-group id="userPhone" label-for="userPhone">
        <b-form-input
          id="userPhone"
          type="number"
          v-model="userPhoneNumber"
          required
          size="sm"
          :placeholder="$t('phone')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("email") }}</small>
      <b-form-group id="email" label-for="email">
        <b-form-input
          id="email"
          type="email"
          v-model="emailId"
          required
          size="sm"
          :placeholder="$t('email')"
        ></b-form-input>
      </b-form-group>

      <small v-if="!isEditing">{{ $t("auth_code") }}</small>
      <b-form-group v-if="!isEditing" label-for="userCode">
        <b-form-input
          id="userCode"
          type="number"
          size="sm"
          disabled
          v-model="code"
        ></b-form-input>
      </b-form-group>

      <b-row>
        <b-col>
          <small>{{ $t("user_role") }}</small>
          <b-form-select size="sm" v-model="userRole" class="mb-3">
            <b-form-select-option value="1">{{
              $t("admin")
            }}</b-form-select-option>
            <b-form-select-option value="2">Atendimento</b-form-select-option>
            <b-form-select-option value="3">Caixa</b-form-select-option>
            <b-form-select-option value="4">Analista de leituras</b-form-select-option>
          </b-form-select>
        </b-col>
        <b-col>
          <small>Status</small>
          <b-form-select size="sm" v-model="userStatus" class="mb-3">
            <b-form-select-option value="1">{{
              $t("active")
            }}</b-form-select-option>
            <b-form-select-option value="0">{{
              $t("inactive")
            }}</b-form-select-option>
          </b-form-select>
        </b-col>
      </b-row>

      <b-button
        class="mt-3 sm"
        :disabled="
          userName.length <= 0 ||
          userPhoneNumber.length < 9 ||
          isLoading == true
        "
        block
        @click="addNewUser()"
        size="sm"
      >
        <template v-if="isLoading">
          {{ $t("submiting") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("submit") }}</template>
      </b-button>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import UserService from "../../http-service/UserService";

export default {
  name: "staff-list",

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "currentBranchUserList",
      "companyStaff",
      "provinces",
      "districts",
      "branches",
      "currentBranch",
    ]),
  },

  data() {
    return {
      show: false,
      userName: "",
      userUid: "",
      userPhoneNumber: "",
      emailId: "",
      userRole: 1,
      userStatus: 1,
      id: 0,
      code: Math.floor(Math.random() * 1000000) + 1,
      isLoading: false,
      isEditing: false,

      currentDeletion: "",
    };
  },

  created() {},

  methods: {
    getUserRole(userRole) {
      if (userRole == 1) {
        return "Administrador";
      } else if (userRole == 2) {
        return "Atendimento";
      } else if (userRole == 3) {
        return "Caixa";
      } else if (userRole == 4) {
        return "Analista de leitura";
      }
    },

    editUser(user) {
      this.openModalUser();
      this.isEditing = true;
      this.userName = user.name;
      this.code = user.code;
      this.userRole = user.userRole;
      this.userPhoneNumber = user.phoneNumber;
      this.emailId = user.email;
      this.userStatus = user.status;
      this.id = user.id;
    },

    openModalUser() {
      this.$refs["add-new-user"].show();
    },

    async addNewUser() {
      const passingValues = {
        name: this.userName,
        phoneNumber: this.userPhoneNumber,
        email: this.emailId,
        userRole: this.userRole,
        status: this.userStatus,
        branchId: this.currentBranch.id,
        companyId: this.currentUser[0].companyId,
        code: this.code,
        id: this.id,
      };

      this.isLoading = true;

      try {
        const user = await UserService.insertUser(passingValues, this.token);
        this.$bvToast.toast(user.message, {
          title: "Sucesso",
          variant: "success",
          solid: true,
        });
        this.$refs["add-new-user"].hide();
        this.clearFields();
        this.isLoading = false;
        this.$emit("update-users");
        // console.log(user);
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
        this.$emit("getMembers");
      }
    },

    clearFields() {
      this.userName = "";
      this.phoneNumber = "";
      this.userRole = "";
      this.userPhoneNumber = "";
      this.emailId = "";
      this.id = 0;
      this.code = Math.floor(Math.random() * 1000000) + 1;
    },
  },
};
</script>

<style lang="scss" scoped>
.table {
  font-size: 12px;
  text-align: left;
  tr {
    cursor: pointer;
  }
}
</style>
