<template>
  <div>
    <nav-bar />
    <pulse-loader class="text-center m-4" v-if="isLoading" />
    <b-container fluid class="bv-example-row mt-2" v-else>
      <b-row>
        <b-col lg="3">
          <Quotation
            :pendingRegistrations="pendingRegistration"
            :invoiceId="invoiceId"
            v-on:update-quotation="getPreRegistrations()"
          />
        </b-col>
        <b-col>
          <PendingReadings />
        </b-col>
        <b-col lg="3">
          <b-row>
            <b-col
              ><b-input-group size="md" class="mb-2">
                <b-input-group-prepend is-text>
                  <b-icon icon="search"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                  type="text"
                  size="md"
                  @keyup="searchCustomer()"
                  v-model="searchValues"
                  :placeholder="$t('search_customer')"
                ></b-form-input> </b-input-group
            ></b-col>
          </b-row>
          <b-row>
            <b-col>
              <b-overlay :show="show" rounded="sm" class="mt-2 mb-2 bg-white">
                <b-list-group v-if="customers.length > 0">
                  <b-list-group-item
                    v-for="customer in customers"
                    :key="customer.id"
                    style="cursor: pointer; font-size: 10pt"
                    @click="openVendingPanel(customer)"
                    class="flex-column align-items-start"
                  >
                    <div class="d-flex w-100 justify-content-between">
                      <h2 class="mb-0" style="font-size: small">
                        <b-icon
                          v-if="customer.status == 1"
                          icon="person-check-fill"
                          class="text-muted"
                        ></b-icon>
                        <b-icon
                          v-else
                          icon="person-dash-fill"
                          variant="danger"
                        ></b-icon>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <small>{{ customer.name }}</small>
                      </h2>
                      <small class="text-muted"
                        ><em>{{ tariffId(customer.tarrifTypeId) }}</em></small
                      >
                    </div>

                    <p class="mb-0">
                      <small class="text-muted"
                        ><em
                          >{{ customer.address }} - {{ customer.barcode }}</em
                        ></small
                      >
                    </p>
                  </b-list-group-item>
                </b-list-group>

                <b-list-group class="text-danger" v-if="error">
                  <b-list-group-item class="text-center text-danger">
                    <h5>{{ $t("error") }}</h5>
                  </b-list-group-item>
                </b-list-group>
              </b-overlay>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import PulseLoader from "vue-spinner/src/PulseLoader.vue";
import NavBar from "../components/Navbar";
import Quotation from "../components/modals/Quotations.vue";

import SearchCustomers from "../http-service/SearchCustomers";
import PreRegistration from "../http-service/PreRegistration";
import ProformaService from "../http-service/ProformaService";
import PendingReadings from "../components/modals/PendingReadings";

import Vue from "vue";
import { mapGetters } from "vuex";

export default Vue.extend({
  data() {
    return {
      isLoading: false,
      show: false,
      showChild: false,
      searchValues: "",
      customers: [],
      error: false,
      pendingRegistration: [],
      invoiceId: 0,
    };
  },

  components: {
    NavBar,
    PulseLoader,
    Quotation,
    PendingReadings,
  },

  async created() {
    this.$store.commit("SET_CUSTOMER_READINGS", null);
    this.getPreRegistrations();
    this.getAllProforma();
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "branches",
      "provinces",
      "districts",
      "currentCustomer",
      "currentBill",
      "tariffs",
    ]),
  },

  watch: {},

  methods: {
    openVendingPanel(currentCustomer) {
      this.customers = [];
      this.$store.commit("SET_CURRENT_CUSTOMER", currentCustomer);
      this.$router.push("/customerview");
    },

    async getPreRegistrations() {
      this.showPreReg = true;
      const status = 0;

      try {
        const preRegis = await PreRegistration.getAllPreRegistrationCustomers(
          status,
          this.token
        );
        if (preRegis.success == true) {
          this.pendingRegistration = preRegis.result;
          this.invoiceId = preRegis.invoiceId;
        } else {
          this.pendingRegistration = [];
        }
      } catch (err) {
        console.log(err.message);
      }
    },

    async searchCustomer() {
      const value = this.searchValues;
      this.show = true;
      if (value.length === 0) {
        this.show = false;
        this.error = false;
        this.customers = [];
        return;
      }

      try {
        let data = await SearchCustomers.searchCustomers(
          this.searchValues,
          this.token
        );
        if (data.success == true) {
          this.customers = data.result;
          this.show = false;
          this.error = false;
        } else {
          this.show = false;
          this.customers = [];
          this.error = true;
        }
      } catch (err) {
        this.$bvToast.toast("" + err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.show = false;
        this.customers = [];
      }
    },

    tariffId(tariff) {
      if (tariff == 1) {
        this.quantityMinTariff = 0;
        return "Fontenária";
      } else if (tariff == 2) {
        this.quantityMinTariff = 5;
        return "Doméstica";
      } else if (tariff == 3) {
        this.quantityMinTariff = 0;
        return "Municipal";
      } else if (tariff == 4) {
        this.quantityMinTariff = 15;
        return "Comercial";
      } else if (tariff == 5) {
        this.quantityMinTariff = 15;
        return "Público";
      } else if (tariff == 6) {
        this.quantityMinTariff = 15;
        return "Industrial";
      }
    },

    async getAllProforma() {
      this.isLoading = true;
      try {
        const proforma = await ProformaService.getAllProforma(this.token);
        if (proforma.success === true) {
          this.$store.commit("SET_PROFORMAS", proforma.result)
          this.isLoading = false;
        } else {
          this.$bvToast.toast(proforma.message, {
            title: "Error!",
            variant: "info",
            solid: true,
          });
          this.isLoading = false;
          this.$store.commit("SET_PROFORMAS", [])
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.$store.commit("SET_PROFORMAS", [])
        this.isLoading = false;
      }
    },
  },
});
</script>
