<template>
  <div>
    <nav-bar />
    <b-row>
      <b-col>
        <b-card header-tag="header">
          <b-row>
            <b-col class="">
              <b-overlay :show="isLoading" rounded="lg" class="mb-2 bg-white">
                <table
                  class="table table-sm table-hover table-stripped"
                  v-if="proformas.length > 0"
                >
                  <thead>
                    <tr style="font-size: 10pt">
                      <th colspan="7">FACTURAS PROFORMA</th>
                      <th colspan="3" class="text-right">
                        {{ proformas.length }}
                      </th>
                    </tr>
                  </thead>
                  <thead>
                    <tr id="reportList" class="bg-dark text-white">
                      <th>F/P NR</th>
                      <th>Data de emissão</th>
                      <th>Nome do cliente</th>
                      <th>Telemóvel</th>
                      <th>Morada</th>
                      <th>Estado</th>
                      <th class="text-right">Montante</th>
                      <th class="text-center">Ver</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="proforma in proformas"
                      :key="proforma.id"
                      :class="{
                        'text-primary': proforma.status == -1,
                        'text-success': proforma.status == 1,
                        'bg-warning': proforma.id == currentProforma.id,
                      }"
                    >
                      <td class="align-middle">
                        {{ processInvoiceYear(proforma) }}
                      </td>
                      <td
                        class="align-middle"
                        v-date-format="proforma.createdAt"
                      >
                        }
                      </td>
                      <td class="align-middle">{{ proforma.customerName }}</td>
                      <td class="align-middle">
                        {{ proforma.customerPhoneNumber }}
                      </td>
                      <td class="align-middle">
                        {{ proforma.customerAddress }}
                      </td>
                      <td class="align-middle">
                        {{ getProformaStatus(proforma.status) }}
                      </td>
                      <th class="align-middle text-right" v-money-format="getTotalOfBill(proforma)"></th>
                      <td
                        class="align-middle text-center"
                        @click="previewProforma(proforma)"
                      >
                        <span class="h6"
                          ><b-icon variant="primary" icon="eye-fill"></b-icon
                        ></span>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <b-alert
                  class="text-center"
                  v-else
                  variant="danger"
                  show
                  dismissible
                  ><i class="fas fa-info"></i>
                  Proforma indisponíveis!
                </b-alert>
              </b-overlay>
              <!-- End Message Body Component -->
            </b-col>
          </b-row>
        </b-card>
      </b-col>
    </b-row>

    <b-modal
      hide-footer
      ref="preview-proforma"
      size="xl"
      :title="`Factura Proforma Nº: ${processInvoiceYear(currentProforma)}`"
    >
      <QuotationPreview
        :currentProforma="currentProforma"
      />
    </b-modal>
  </div>
</template>

<script>
import NavBar from "../components/Navbar";
import QuotationPreview from "../components/modals/QuotationPreview";
import { mapGetters } from "vuex";
import moment from "moment";

export default {
  name: "ProformaView",

  computed: {
    ...mapGetters(["user", "token", "proformas"]),
  },

  created() {
  },

  components: {
    NavBar,
    QuotationPreview,
  },

  data() {
    return {
      isLoading: false,
      currentProforma: {},
      vat: 0
    };
  },

  methods: {
    processInvoiceYear(proforma) {
      const year = moment(proforma.createdAt).year();
      return `${proforma.invoiceId}/SAAF/${year}`;
    },

    getProformaStatus(status) {
      if (status == 0) {
        return "Pendente";
      } else if (status == 1) {
        return "Pago";
      } else if (status == -1) {
        return "Parcelado";
      }
    },

    previewProforma(proforma) {
      this.currentProforma = proforma;
      this.$refs["preview-proforma"].show();
    },

    getTotalOfBill(proforma) {
      const year = moment(proforma.createdAt).year();
      const vat = year <= 2022 ? 0.17 : 0.16;

      const itemsList = JSON.parse(proforma.itemsDetails);
      const subtotal = itemsList.reduce(
        (sum, p) => sum + p.quantity * p.price,
        0
      );
      const total = subtotal * vat + subtotal;
      return  total;
    },
    
  },
};
</script>

<style lang="scss" scoped>
.table {
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;

  tr {
    cursor: pointer;
  }
}
</style>
