import axios from 'axios'
const url = 'api/cashierPayments'

/* eslint-disable no-async-promise-executor */

class CashierPayments {
    // Get User
    static getAllPayments(token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static getPaymentsByStaffId(staffId, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${staffId}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default CashierPayments