<template>
  <div class="simulator">
    <login-navbar />

    <b-row>
      <b-col lg="12" sm="12" md="12">
        <!-- form-login -->
        <form @submit.prevent="registerNewCustomer()" class="simulador">
          <div class="card">
            <div class="card-header text-center">
              {{ $t("water_simulator") }}
            </div>
            <div class="card-body">
              <b-overlay :show="isLoading" rounded="sm">
                <b-row>
                  <b-col
                    ><div class="form-group">
                      {{ $t("tarrif_type") }}
                      <b-form-select
                        size="sm"
                        v-model="tariffTypeId"
                        :options="tariffs"
                      ></b-form-select></div
                  ></b-col>
                </b-row>

                <b-row>
                  <b-col lg="6" sm="12" md="6"
                    ><b-form-group id="last">
                      <b-form-input
                        type="number"
                        step="any"
                        min="0"
                        v-model="last"
                        disabled
                        required
                        size="sm"
                        :placeholder="$t('last_reading')"
                      ></b-form-input> </b-form-group
                  ></b-col>
                  <b-col lg="6" sm="12" md="6"
                    ><b-form-group id="current">
                      <b-form-input
                        type="number"
                        step="any"
                        min="0"
                        v-model="current"
                        :disabled="tariffTypeId == 0"
                        required
                        size="sm"
                        placeholder="Quantidade"
                      ></b-form-input> </b-form-group
                  ></b-col>
                </b-row>

                <b-row>
                  <b-col
                    ><p
                      class="text-center text-secondary"
                      style="cursor: pointer"
                      @click="backToLogin()"
                    >
                      {{ $t("login") }}
                    </p></b-col
                  >
                  <b-col
                    ><b-button
                      @click="simulateBill()"
                      :disabled="
                        tariffTypeId == 0 ||
                        last.length == 0 ||
                        current.length == 0
                      "
                      block
                      size="sm"
                      variant="secondary"
                      >{{ $t("simulate_bill") }}</b-button
                    ></b-col
                  >
                </b-row>
              </b-overlay>
            </div>
          </div>
        </form>
      </b-col>
    </b-row>

    <b-row class="footer text-secondary">
      <b-col>Copyright ©2021 TSENANE, LDA. All rights reserved.</b-col>
    </b-row>

    <b-modal
      hide-footer
      size="lg"
      ref="simulator-result"
      :title="$t('simulator-result')"
    >
      <simulator-modal
        :purchaseDetails="purchaseDetail"
        :currentTarif="currentTariff"
      />
    </b-modal>
  </div>
</template>>

<script>
import LoginNavbar from "../components/LoginNavbar";
import TariffService from "../http-service/SimulatorService";
import BillingCalculations from "../calcs/BillingCalculations";
import SimulatorModal from "../components/modals/SimulatorModal";

export default {
  name: "Simulator",

  components: {
    LoginNavbar,
    SimulatorModal,
  },

  data() {
    return {
      tariffs: [
        { value: 1, text: "Fontenária" },
        { value: 2, text: "Doméstica" },
        { value: 3, text: "Municipal" },
        { value: 4, text: "Comercial" },
        { value: 5, text: "Público" },
        { value: 5, text: "Industrial" },
      ],

      tariffTypeId: 0,
      isLoading: false,
      currentTariff: "",
      last: 0,
      current: "",
      purchaseDetail: "",
    };
  },

  watch: {
    tariffTypeId: function () {
      this.getTariffById(this.tariffTypeId);
    },
  },

  methods: {
    backToLogin() {
      this.$router.push("/");
    },

    simulateBill() {
      const last = parseFloat(this.last);
      const current = parseFloat(this.current);
      const tariffId = this.tariffTypeId;

      if (current < last) {
        this.$bvToast.toast(
          `A leitura actual deve ser igual ou superior à anterior.`,
          {
            title: "Error",
            variant: "danger",
            solid: true,
          }
        );
      } else {
        const quantity = current - last;

        if (tariffId === 2 || tariffId === 3) {
          if (quantity <= 5) {
            const data = BillingCalculations.domesticBelowFive(
              quantity,
              this.currentTariff[0]
            );
            this.purchaseDetail = data;
            this.showDetails = true;
            this.$refs["simulator-result"].show();
          } else {
            const data = BillingCalculations.domesticAboveFive(
              quantity,
              this.currentTariff[0]
            );
            this.purchaseDetail = data;
            this.showDetails = true;
            this.$refs["simulator-result"].show();
          }
        } else if (tariffId === 1) {
          const data = BillingCalculations.fontCalc(
            quantity,
            this.currentTariff[0]
          );
          this.purchaseDetail = data;
          this.showDetails = true;
          this.$refs["simulator-result"].show();
        } else if (tariffId >= 4) {
          const data = BillingCalculations.calculatePublicComercialAndIndustrial(
            quantity,
            this.currentTariff[0]
          );
          this.purchaseDetail = data;
          this.showDetails = true;
          this.$refs["simulator-result"].show();
        }
      }
    },

    async getTariffById() {
      this.isLoading = true;
      try {
        const tariffs = await TariffService.getTarrifsByCategory(
          this.tariffTypeId
        );
        if (tariffs.success == true) {
          this.currentTariff = tariffs.result;
          // console.log(tariffs.result);
        } else {
          this.$bvToast.toast(`Tarifa não disponível.`, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
        }
        this.isLoading = false;
      } catch (err) {
        console.log(err.message);
        this.isLoading = false;
      }
    },
  },
};
</script>

<style scoped lang="scss">
.simulador {
  margin: auto;
  margin-top: 20px;
  width: 40%;
  // border: 2px solid #a8b893;
  display: block;
}

.footer {
  margin-top: 120px;
  text-align: center;
  font-size: 14px;
}
</style>