                </b-button>
              </b-col>
            </b-row>
          </div>
        </b-modal>

        <b-modal
          hide-footer
          ref="show-whatsapp-modal"
          title="Enviar factura via WhatsApp"
        >
          <b-icon variant="success" icon="whatsapp"></b-icon
          ><span class="ml-2">Número WhatsApp</span>
          <b-form-group id="whatsapp" label-for="whatsapp" class="mt-2">
            <b-form-input
              id="whatsapp"
              type="text"
              v-model="currentCustomer.phone"
              required
              size="sm"
              placeholder="Número WhatsApp"
            ></b-form-input>
          </b-form-group>
          <b-form-textarea
            id="textarea"
            v-model="whatsAppBill"
            placeholder="Enter something..."
            rows="3"
            max-rows="6"
          ></b-form-textarea>
          <b-row class="mt-2">
            <b-col>
              <b-button block size="sm" @click="cancelWhatsApp()">{{
                $t("cancel")
              }}</b-button>
            </b-col>
            <b-col>
              <b-button
                block
                size="sm"
                variant="success"
                :disabled="currentCustomer.phone.length < 12"
                @click="sendBillViaWhatsApp()"
                >Enviar</b-button
              >
            </b-col>
          </b-row>
        </b-modal>

        <b-modal
          hide-footer
          ref="show-email-modal"
          title="Enviar factura via E-mail"
          id="modal-scrollable"
          scrollable
        >
          <b-icon variant="success" icon="chat-left-fill"></b-icon
          ><span class="ml-2">E-mail do destinatário</span>
          <b-form-group id="email" label-for="email" class="mt-2">
            <b-form-input
              id="email"
              type="text"
              v-model="currentCustomer.email"
              required
              size="sm"
              placeholder="E-mail do destinatário"
            ></b-form-input>
          </b-form-group>
          <b-row>
            <b-col>{{ emailBillDetails }}</b-col>
          </b-row>
          <b-row class="mt-2">
            <b-col>
              <b-button block size="sm" @click="cancelEmail()">{{
                $t("cancel")
              }}</b-button>
            </b-col>
            <b-col>
              <b-button
                block
                size="sm"
                variant="success"
                :disabled="currentCustomer.phone.length < 12"
                @click="sendEmailBill()"
                >Enviar</b-button
              >
            </b-col>
          </b-row>
        </b-modal>

        <b-modal
          hide-footer
          ref="show-sms-modal"
          title="Enviar factura via SMS"
        >
          <b-icon variant="secondary" icon="phone-fill"></b-icon
          ><span class="ml-2">Número do destinatário</span>
          <b-form-group id="sms" label-for="sms" class="mt-2">
            <b-form-input
              id="sms"
              type="text"
              v-model="currentCustomer.phone"
              required
              size="sm"
              placeholder="Número do destinatário"
            ></b-form-input>
          </b-form-group>
          <b-form-textarea
            id="textarea"
            v-model="smsBody"
            placeholder="Enter something..."
            rows="3"
            max-rows="6"
          ></b-form-textarea>
          <b-row class="mt-2">
            <b-col>
              <b-button block size="sm" @click="cancelSms()">{{
                $t("cancel")
              }}</b-button>
            </b-col>
            <b-col>
              <b-button
                block
                size="sm"
                variant="success"
                :disabled="currentCustomer.phone.length != 12"
                @click="sendSmsBillViaTwilio()"
                >Enviar SMS</b-button
              >
            </b-col>
          </b-row>
        </b-modal>

        <b-modal hide-footer ref="add-new-debt" :title="$t('add_debts')">
          <small>{{ $t("amount_debt") }}</small>
          <b-form-group id="amount_debt" label-for="amount_debt">
            <b-form-input
              id="amount_debt"
              type="number"
              step="any"
              v-model="amountOfDebt"
              required
              min="1"
              size="sm"
              :placeholder="$t('amount_debt')"
            ></b-form-input>
          </b-form-group>
          <small>{{ $t("description") }}</small>
          <b-form-group id="description" label-for="description">
            <b-form-input
              id="description"
              type="text"
              v-model="debtDescription"
              required
              size="sm"
              :placeholder="$t('description')"
            ></b-form-input>
          </b-form-group>
          <b-button
            class="mt-3 sm"
            :disabled="!amountOfDebt || isLoading"
            block
            @click="submitDebt()"
            size="sm"
          >
            <template v-if="isLoading">
              A submeter
              <b-spinner small type="grow"></b-spinner>
            </template>
            <template v-else>Submeter</template>
          </b-button>
        </b-modal>
      </div>
    </b-container>
  </div>
</template>

<script>
import NavBar from "../components/Navbar";
import Vue from "vue";
import { mapGetters } from "vuex";

import printJS from "print-js";

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

import moment from "moment";
moment.locale("pt-br");
// SERVICES
import TariffService from "../http-service/TarrifService";
import WhatsAppCloudApi from "../http-service/WhatsAppCloudApi";
import SendBillByEmailService from "../http-service/SendBillByEmailService";
import ReadingService from "../http-service/ReadingService";
import DebtService from "../http-service/DebtService";
import PdfService from "../http-service/PdfService";
import MpesaService from "../http-service/MPesaService";
import CashOrPosService from "../http-service/CashOrPosService";
import CustomerBalance from "../http-service/CustomerBalance";
import CustomerService from "../http-service/CustomerService";
import MoneyFormat from "../calcs/MoneyFormat";

export default Vue.extend({
  name: "Customer",

  components: {
    "nav-bar": NavBar,
  },

  data() {
    return {
      readings: [],
      facturas: [],
      payments: [],
      isLoading: false,
      message: false,
      whatsAppBill: "",
      emailBillDetails: "",

      editCustomerValues: {},

      amountOfDebt: 0,
      debtDescription: "Taxa de religação de água",

      options: [
        { value: 1, text: "Boca do Caixa" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "Mpesa Open API" },
        { value: 8, text: "E-Mola" },
      ],

      purchaseDetail: "",
      currentTariff: "",
      quantity: "",
      readingDeletionId: 0,
      consumptionPeriod: moment(new Date()).format("MMMM Y").toUpperCase(),
      reading_date_taken: "",
      consumption: 0,
      last: 0,
      current: 0,
      updatedId: 0,
      waterMeterImageUrl: "",
      imageTemplate:
        "https://firebasestorage.googleapis.com/v0/b/tsenane-994c3.appspot.com/o/images%2F20200900627_4029b1a2-530b-49c8-9e4f-c5ba98fd7887?alt=media&token=74344817-5d8f-4deb-94a6-e1d64e8cbd6d",

      saldoDivida: 0,
      totalFacturasNormais: 0,

      amount2BeCredited: 0,
      paymentMethod: 1,
      phoneNumberOfPayment: "258",
      paymentReference: "",

      currentBalance: 0,
      smsBody: "",
    };
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "branches",
      "provinces",
      "districts",
      "currentCustomer",
      "currentBill",
      "company",
    ]),
  },

  watch: {
    current: function () {
      if (this.current < this.last) {
        this.consumption = 0;
        return;
      }
      this.consumption = parseFloat(this.current) - parseFloat(this.last);
    },

    paymentMethod: function () {
      if (this.paymentMethod == 7) {
        console.log("Initiating M-Pesa  transaction");
      }
    },
  },

  created() {
    this.getReadings();
    this.getDebts();
    this.getCustomerBalance();
  },

  methods: {
    editCustomer(customer) {
      this.$refs["edit-customer"].show();
      this.editCustomerValues = customer;
      console.log(this.editCustomerValues);
    },

    getVat(report) {
      const vat = report.year <= 2022 ? 0.75 * 0.17 : 0.75 * 0.16
      const vatValue = report.subTotal * vat
      return vatValue
    },

    getTotal(report) {
      const subTotal = report.subTotal + this.getVat(report)
      const forfeit = subTotal * report.forfeit
      const total = subTotal + forfeit

      return total
    },

    addDebts() {
      this.$refs["add-new-debt"].show();
    },

    async submitDebt() {
      this.show = true;

      if (this.amount <= 0) {
        this.$bvToast.toast("Valor da dívida inválido.", {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.show = false;
        return;
      }

      const passingValues = {
        amount: this.amountOfDebt,
        status: 0,
        description: this.debtDescription,
        barcode: this.currentCustomer.barcode,
        staffName: this.currentUser[0].name,
        branchId: this.currentCustomer.branchId,
      };

      try {
        const debt = await DebtService.insertDebt(passingValues, this.token);
        if (debt.success == true) {
          this.refreshPage();
        }
      } catch (err) {
        this.message = err.message;
      }
      this.show = false;
      this.refreshPage();
      this.$refs["add-new-debt"].hide();
      this.amountOfDebt = "";
      this.debtDescription = "";
    },

    async invalidateDebt() {
      this.isLoading = true;
      try {
        const invalidate = await DebtService.updateDebt(
          this.currentCustomer.barcode,
          this.token
        );
        if (invalidate.success == true) {
          this.isLoading = false;
          console.log(invalidate);
          this.refreshPage();
        } else {
          console.log(invalidate.message);
          this.isLoading = false;
        }
      } catch (err) {
        console.log(err);
        this.isLoading = false;
      }
    },

    getPendingBills() {
      const totalPending = this.facturas.filter((a) => {
        return a.status == 0;
      });
      // const numOfPendingBill = totalPending.length;
      let smsDetails = "";
      totalPending.forEach((item) => {
        smsDetails += item.billId + "/SAAF: " + item.total + "\n";
      });
      return smsDetails;
    },

    cancelSms() {
      this.$refs["show-sms-modal"].hide();
    },

    cancelEmail() {
      this.$refs["show-email-modal"].hide();
    },

    async sendEmailBill() {
      this.isLoading = true;

      const passingValues = {
        to: this.currentCustomer.email,
        emailBody: this.emailBillDetails,
      };

      try {
        const sendMail = await SendBillByEmailService.sendEmail(
          passingValues,
          this.token
        );
        if (sendMail.success == true) {
          this.isLoading = false;
          console.log(sendMail);
        } else {
          console.log(sendMail.message);
          this.isLoading = false;
        }
        this.$refs["show-email-modal"].hide();
      } catch (err) {
        console.log(err);
        this.isLoading = false;
        this.$refs["show-email-modal"].hide();
      }
    },

    openEmailModal() {
      this.$refs["show-email-modal"].show();
      this.emailBillDetails =
        "Olá, muito obrigado por se cadastrar na nossa plataforma.";
    },

    openWhatsAppModal() {
      this.$refs["show-whatsapp-modal"].show();
      let billDetails;
      billDetails = "*" + this.company[0].name + "*\n";
      billDetails += this.company[0].address + "\nNUIT: ";
      billDetails += this.company[0].nuit + "\nCel: ";
      billDetails += this.company[0].phone + "\n";
      billDetails += this.company[0].email + "\nWebsite: ";
      billDetails += this.company[0].website + "\n";
      billDetails += "===========================\n";
      billDetails += "EXMO SR(s): ";
      billDetails += this.currentCustomer.name + "\nEndereço: ";
      billDetails += this.currentCustomer.address + "\nContador: ";
      billDetails += this.currentCustomer.barcode + "\nCel +";
      billDetails += this.currentCustomer.phone + "\n";
      billDetails += this.tariffId(this.currentCustomer.tarrifTypeId) + "\n";
      billDetails += "===========================\n";
      billDetails += "*FACTURAS*\n";

      let billDetailsFacturas = [];

      const pendingBills = this.facturas.filter((a) => {
        return a.status == 0;
      });

      pendingBills.forEach((factura) => {
        let facturaDetail;
        facturaDetail = "--------------------------------------\n";
        facturaDetail += "*Factura/Recibo Nº: " + factura.billId + "*\n";
        facturaDetail += "Data emissão: " + factura.dataEmissao + "\n";
        facturaDetail += "Vencimento: " + factura.dataLimite + "\n";
        facturaDetail += "Leitura anterior: " + factura.last + " m^3\n";
        facturaDetail += "Leitura actual: " + factura.current + " m^3\n";
        facturaDetail +=
          "Consumo facturado: " + factura.totalUnities + " m^3\n";
        facturaDetail += "SubTotal: " + factura.subTotal + "\n";
        facturaDetail += "IVA (12%): " + factura.vat + "\n";
        facturaDetail += "Multa: " + factura.forfeit + "\n";
        facturaDetail +=
          "*Total da factura: " +
          MoneyFormat.formatMoney(factura.total) +
          "*\n";

        pendingBills.length - 1 == pendingBills.indexOf(factura)
          ? (facturaDetail += "===========================\n")
          : "";

        // console.log(pendingBills.length - 1, pendingBills.indexOf(factura));
        billDetailsFacturas.push({ factura: facturaDetail });
      });

      let dadosFacturas = "";

      billDetailsFacturas.forEach((bill) => {
        dadosFacturas += bill.factura;
      });

      let totals = "";
      totals +=
        "*Dívida anterior: " +
        MoneyFormat.formatMoney(this.saldoDivida) +
        "*\n";
      totals +=
        "*TOTAL A PAGAR: " +
        MoneyFormat.formatMoney(this.totalFacturasNormais + this.saldoDivida) +
        "*\n";
      totals += "===========================\n";

      let paymentMethodDetails = "CANAIS DE PAGAMENTO\n";
      paymentMethodDetails += "*BCI: Tsenane, Lda*\n";
      paymentMethodDetails += this.company[0].bankAccount + "\n";
      paymentMethodDetails += this.company[0].nib + "\n";
      paymentMethodDetails += "--------------------------------------\n";
      paymentMethodDetails += "*M-PESA*\n";
      paymentMethodDetails += "Digite: *150# > tecla de chamada\n";
      paymentMethodDetails += "6. Pagamentos\n";
      paymentMethodDetails += "7. Código de Serviço *901029*\n";
      paymentMethodDetails +=
        "Referência: " + this.currentCustomer.barcode + "\n";
      paymentMethodDetails +=
        "Valor da factura: *" +
        MoneyFormat.formatMoney(this.totalFacturasNormais + this.saldoDivida) +
        "*\n";
      paymentMethodDetails += "Digitar o PIN M-PESA\n";
      paymentMethodDetails += "Confirmar a transação e *JÁ ESTÁ*\n";
      paymentMethodDetails += "===========================\n\n";
      paymentMethodDetails += "*App Android:* " + "https://bit.ly/3aBHdtf";

      this.whatsAppBill =
        billDetails + dadosFacturas + totals + paymentMethodDetails;
    },

    async sendBillViaWhatsApp() {
      // console.log(this.whatsAppBill);
      this.isLoading = true;
      const passingValues = {
        phone: this.currentCustomer.phone,
        barcode: this.currentCustomer.barcode,
        message: this.whatsAppBill,
        staffName: this.user[0].name,
      };

      try {
        const whatsapp = WhatsAppCloudApi.sendWhatsAppMessage(
          passingValues,
          this.token
        );
        if (whatsapp.success == true) {
          this.isLoading = false;
          this.cancelWhatsApp();
          this.$bvToast.toast("Mensagem enviada com sucesso.", {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
        } else {
          this.isLoading = false;
          this.cancelWhatsApp();
          this.$bvToast.toast(whatsapp.message, {
            title: "Erro!",
            variant: "danger",
            solid: true,
          });
        }
      } catch (error) {
        console.log(error);
        this.isLoading = false;
        this.cancelWhatsApp();
        this.$bvToast.toast(error.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
      }
    },

    cancelWhatsApp() {
      this.$refs["show-whatsapp-modal"].hide();
    },

    async editCustomerFinal() {
      this.isLoading = true;

      try {
        const customers = await CustomerService.insertOrUpdateCustomer(
          this.editCustomerValues,
          this.token
        );
        if (customers.success == true) {
          // this.customers = customers.result;
          this.isLoading = false;
          this.$bvToast.toast(customers.message, {
            title: "Sucesso",
            variant: "success",
            solid: true,
          });
        } else {
          this.isLoading = false;
          this.$bvToast.toast(customers.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
        this.$refs["edit-customer"].hide();
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
        this.$refs["edit-customer"].hide();
      }
    },

    showCredentials() {
      this.$refs["show-credentials"].show();
    },

    refreshPage() {
      this.getReadings();
      this.getDebts();
      this.getCustomerBalance();
    },

    addNewPurchase() {
      this.$refs["new-reading"].show();
    },

    async generatePDF(fileName) {
      this.isLoading = true;

      const passingValues = {
        barcode: this.currentCustomer.barcode,
        customerName: this.currentCustomer.name,
        companyId: this.currentUser[0].companyId,
        tariffTypeId: this.currentCustomer.tarrifTypeId,
      };

      try {
        const generating = await PdfService.createPdfInvoice(
          passingValues,
          this.token
        );
        if (generating.success == true) {
          console.log(generating);
          this.downloadPdf(fileName);
        } else {
          this.$bvToast.toast(generating.message, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
        }

        this.isLoading = false;
      } catch (err) {
        this.isLoading = false;
        this.$bvToast.toast(err.message, {
          title: "Error",
          variant: "danger",
          solid: true,
        });
      }
    },

    async downloadPdf(fileURL) {
      try {
        await printJS({
          printable: `http://localhost:4000/uploads/tsenan/${fileURL}.pdf`,
          type: "pdf",
          showModal: true,
        });
      } catch (err) {
        console.log(err.message);
      }
    },

    async getReadings() {
      this.isLoading = true;

      try {
        const readings = await ReadingService.getReadingsByCustomer(
          this.currentCustomer.barcode,
          this.currentCustomer.tarrifTypeId,
          this.token
        );
        if (readings.facturas.length > 0) {
          this.readings = readings.result;
          this.totalFacturasNormais = readings.totalFacturas;
          this.last = readings.result[0].current;
          this.updateReadings(readings.facturas);
          this.facturas = readings.facturas;
        } else {
          console.log(readings.message);
        }
      } catch (err) {
        console.log(err);
      }
      this.isLoading = false;
    },

    async getDebts() {
      this.isLoading = true;

      try {
        const debts = await DebtService.getDebtByCustomerId(
          this.currentCustomer.barcode,
          this.token
        );
        if (debts.success == true) {
          this.saldoDivida = debts.totalDues == null ? 0 : debts.totalDues;
          this.payments = debts.payments;
        } else {
          this.saldoDivida = 0;
        }
      } catch (err) {
        console.log(err);
      }
      this.isLoading = false;
    },

    getPaymentMethod(method) {
      if (method == 1) {
        return "Boca do Caixa";
      } else if (method == 2) {
        return "Depósito Bancário";
      } else if (method == 3) {
        return "Transferência Bancária";
      } else if (method == 4) {
        return "POS";
      } else if (method == 5) {
        return "Cheque";
      } else if (method == 6) {
        return "SISTAFE";
      } else if (method == 7) {
        return "M-Pesa Open API";
      } else if (method == 8) {
        return "E-Mola";
      }
    },

    convertMonthToString(consumptionPeriod) {
      return moment(consumptionPeriod).format("MMMM Y").toUpperCase();
    },

    tariffId(tariff) {
      if (tariff == 1) {
        this.quantityMinTariff = 0;
        return "Tarifa Fontenária";
      } else if (tariff == 2) {
        this.quantityMinTariff = 5;
        return "Tarifa Doméstica";
      } else if (tariff == 3) {
        this.quantityMinTariff = 0;
        return "Tarifa Municipal";
      } else if (tariff == 4) {
        this.quantityMinTariff = 15;
        return "Tarifa Comercial";
      } else if (tariff == 5) {
        this.quantityMinTariff = 15;
        return "Tarifa Pública";
      } else if (tariff == 6) {
        this.quantityMinTariff = 15;
        return "Tarifa Industrial";
      } else if (tariff == 7) {
        this.quantityMinTariff = 1;
        return "Tarifa Toma de Água";
      }
    },

    async decidedToDelete() {
      this.isLoading = true;

      try {
        const deletion = await ReadingService.deleteReading(
          this.readingDeletionId,
          this.token
        );
        if (deletion.success == true) {
          this.$bvToast.toast(deletion.message, {
            title: "",
            variant: "success",
            solid: true,
          });
          this.getReadings();
        } else {
          this.$bvToast.toast(deletion.message, {
            title: "",
            variant: "danger",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "",
          variant: "danger",
          solid: true,
        });
      }
      this.$refs["delete-reading"].hide();
    },

    deleteReading(id) {
      this.readingDeletionId = id;
      this.$refs["delete-reading"].show();
    },

    cancelDeletion() {
      this.$refs["delete-reading"].hide();
    },

    async getTariffById() {
      this.isLoading = true;
      try {
        const tariffs = await TariffService.getTarrifsByCategory(
          this.currentCustomer.tarrifTypeId
        );
        if (tariffs.success == true) {
          this.currentTariff = tariffs.result;
        } else {
          this.$bvToast.toast(`Tarifa não disponível.`, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
        }
        this.isLoading = false;
      } catch (err) {
        this.isLoading = false;
      }
    },

    updateBill(bill) {
      this.$refs["update-reading"].show();
      this.updatedId = bill.id;
      this.last = bill.last;
      this.current = bill.current;
      this.consumption = bill.consumption;
      this.waterMeterImageUrl = bill.waterMeterImageUrl;
    },

    async updateReading() {
      const passingValues = {
        last: this.last,
        current: this.current,
        consumption: this.consumption,
        id: this.updatedId,
        updatedBy: this.user[0].name,
      };
      // console.log(passingValues);
      this.isLoading = true;

      try {
        const updateReading = await ReadingService.updateReading(
          passingValues,
          this.token
        );
        if (updateReading.success == true) {
          this.$bvToast.toast(updateReading.message, {
            title: "",
            variant: "success",
            solid: true,
          });
          this.getReadings();
        } else {
          this.$bvToast.toast(updateReading.message, {
            title: "",
            variant: "danger",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
      }
      this.$refs["update-reading"].hide();
      this.$emit("update-deletion");
      this.isLoading = false;
    },

    async updateReadings(readings) {
      const customerFacturas = readings.filter((a) => {
        return a.status == 0;
      });
      this.isLoading = true;

      customerFacturas.forEach((reading) => {
        const passingValues = {
          id: 0,
          readingId: reading.billId,
          barcode: this.currentCustomer.barcode,
          vat: reading.vat,
          subTotal: reading.subTotal,
          totalOfBill: reading.total,
        };

        try {
          const updateReading = ReadingService.updateReading(
            passingValues,
            this.token
          );
          if (updateReading == true) {
            console.log(reading.uniqueBillId + "Actualizado com sucesso");
          }
        } catch (err) {
          console.log(err.message);
        }
      });
    },

    async addNewReading() {
      const passingValues = {
        last: this.last,
        current: this.current,
        consumption: this.consumption,
        barcode: this.currentCustomer.barcode,
        status: 0,
        branchId: this.currentCustomer.branchId,
        staffName: this.user[0].name,
        tarrifTypeId: this.currentCustomer.tarrifTypeId,
        consumoFacturado: this.getConsumtionByTariff(
          this.consumption,
          this.currentCustomer.tarrifTypeId
        ),
        lat: this.currentCustomer.lat,
        lng: this.currentCustomer.lng,
      };
      // console.log(passingValues);
      this.isLoading = true;

      try {
        const newReading = await ReadingService.insertReadings(
          passingValues,
          this.token
        );
        if (newReading.success == true) {
          this.$bvToast.toast(newReading.message, {
            title: "",
            variant: "success",
            solid: true,
          });
        } else {
          this.$bvToast.toast(newReading.message, {
            title: "Duplicado",
            variant: "danger",
            solid: true,
          });
        }
        this.$refs["new-reading"].hide();
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.$refs["new-reading"].hide();
      }
      this.getReadings();
      this.isLoading = false;
    },

    getConsumtionByTariff(consumo, tariffType) {
      let consumption = 0;
      if (tariffType >= 4) {
        consumption = consumo <= 15 ? 15 : consumo;
      } else if (tariffType == 2 || tariffType == 3) {
        consumption = consumo <= 5 ? 5 : consumo;
      } else if (tariffType == 1) {
        consumption = consumo;
      }
      return consumption;
    },

    addBalance() {
      this.$refs["add-balance"].show();
    },

    async submitBalance() {
      this.isLoading = true;
      if (this.amount2BeCredited >= 10) {
        if (this.paymentMethod == 7) {
          // Process Payment Via M-Pesa Online
          const passingValues = {
            branchId: this.currentCustomer.branchId,
            barcode: this.currentCustomer.barcode,
            amount: this.amount2BeCredited,
            phoneNumber: this.phoneNumberOfPayment,
            transactionRef: Math.floor(Math.random() * 100000) + 1,
            thirdPartyRef: this.currentCustomer.barcode,
          };

          try {
            const submit = await MpesaService.c2b(passingValues, this.token);
            if (submit.success == true) {
              this.isLoading = false;
              this.$refs["add-balance"].hide();
              this.$bvToast.toast("Pagamento efectuado com sucesso.", {
                title: "Sucesso",
                variant: "success",
                solid: true,
              });
              console.log(submit);
            } else {
              this.$refs["add-balance"].hide();
              this.isLoading = false;
              this.$bvToast.toast("Houve um erro, tente mais tarde.", {
                title: "Erro",
                variant: "danger",
                solid: true,
              });
              console.log(submit);
            }
            this.isLoading = false;
          } catch (err) {
            this.isLoading = false;
            this.$refs["add-balance"].hide();
          }
          this.isLoading = false;
        } else {
          this.submitCashOrPos();
        }
      }
    },

    async submitCashOrPos() {
      this.isLoading = true;

      const passingValues = {
        branchId: this.currentCustomer.branchId,
        barcode: this.currentCustomer.barcode,
        customerId: this.currentCustomer.id,
        amount: this.amount2BeCredited,
        phoneNumber: this.currentCustomer.phone,
        transactionRef: Math.floor(Math.random() * 100000) + 1,
        staff: this.currentUser[0].name,
        provider: this.paymentMethod,
      };

      try {
        const cashOrPos = await CashOrPosService.insertBalance(
          passingValues,
          this.token
        );
        if (cashOrPos.success == true) {
          this.show = false;
          this.$refs["add-balance"].hide();
          this.$bvToast.toast(cashOrPos.message, {
            title: "Sucesso",
            variant: "success",
            solid: true,
          });
        }
      } catch (err) {
        this.show = false;
        this.$refs["add-balance"].hide();
        this.$bvToast.toast(err.message, {
          title: "Erro:",
          variant: "danger",
          solid: true,
        });
      }
      this.isLoading = false;
      this.$refs["add-balance"].hide();
    },

    async getCustomerBalance() {
      this.isLoading = true;
      try {
        const balance = await CustomerBalance.getBalanceByCustomer(
          this.currentCustomer.barcode,
          this.token
        );
        if (balance.success == true) {
          this.currentBalance = balance.currentBalance;
          this.customerCreditHistory = balance.result;
        } else {
          console.log(balance.message);
        }
      } catch (err) {
        console.log(err);
      }
    },

