import axios from 'axios'
const url = 'api/users'

/* eslint-disable no-async-promise-executor */

class UserService {
    // Get User
    static getUser(token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static getAllUsers(branchId, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${branchId}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static insertUser(user, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, user, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default UserService