              <b-form-select size="sm" v-model="diameter">
                <b-form-select-option value="1/2">1/2</b-form-select-option>
                <b-form-select-option value="3/4">3/4</b-form-select-option>
                <b-form-select-option value="1">1</b-form-select-option>
                <b-form-select-option value="1-1/4">1-1/4</b-form-select-option>
                <b-form-select-option value="1-1/2">1-1/2</b-form-select-option>
                <b-form-select-option value="2">2</b-form-select-option>
                <b-form-select-option value="3">3</b-form-select-option>
                <b-form-select-option value="4">4</b-form-select-option>
                <b-form-select-option value="6">6</b-form-select-option>
                <b-form-select-option value="8">8</b-form-select-option>
              </b-form-select>
            </b-form-group>
