              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" sm="12">
            <b-form-group label-for="taxa_vistoria_com_transporte">
              <b-form-input
                type="number"
                size="sm"
                step="any"
                :placeholder="$t('taxa_vistoria_com_transporte')"
                v-model="taxaVistoriaComTransporte"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" sm="12">
            <b-form-group label-for="taxa_subscricao_contrato">
              <b-form-input
                type="number"
                size="sm"
                step="any"
                :placeholder="$t('taxa_subscricao_contrato')"
                v-model="taxaSubscricaoContrato"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" sm="12">
            <b-form-group label-for="taxa_corte_religacao">
              <b-form-input
                type="number"
