                :placeholder="$t('taxa_corte_religacao')"
                v-model="taxaCorteReligacao"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" sm="12">
            <b-form-group label-for="taxa_afericao_contador">
              <b-form-input
