                :placeholder="$t('taxa_afericao_contador')"
                v-model="taxaAfericaoContador"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" md="12" sm="12">
            <b-form-group label-for="taxa_encargo_contador_danificado">
              <b-form-input
