</template>

<script>
import NavBar from "../components/Navbar";
import ProductService from "../http-service/ProductService";

import { mapGetters } from "vuex";

export default {
  name: "Products",

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company"]),
  },

  components: {
    "nav-bar": NavBar,
  },

  created() {
    this.getAllProducts();
  },

  data() {
    return {
      show: false,

      taxaDepositoGarantia: "",
      taxaVistoriaSemTransporte: "",
      taxaVistoriaComTransporte: "",
      taxaSubscricaoContrato: "",
      taxaCorteReligacao: "",
      taxaAfericaoContador: "",
      taxaEncargoContadorDanificado: "",
      taxaEncargoViolacaoInstalacao: "",
      taxaAluguerContador: "",
      diameter: "",

      products: [],
      productId: "",
    };
  },

  methods: {
    cancelDeletion() {
      this.$refs["delete-product"].hide();
      this.clearValues();
    },

    addNewProduct() {
      this.$refs["add-new-product"].show();
    },

    deleteProduct(product) {
      this.productId = product.id;
      this.$refs["delete-product"].show();
    },

    async decidedToDelete() {
      this.show = true;

      try {
        const deletion = await ProductService.deleteProduct(
          this.productId,
          this.token
        );
        if (deletion.success == true) {
          this.$bvToast.toast(deletion.message, {
            title: "",
            variant: "success",
            solid: true,
          });
        }
      } catch (err) {
        console.log(err);
      }
      this.getAllProducts();
      this.clearValues();
      this.show = false;
    },

    clearValues() {
      this.taxaDepositoGarantia = "";
      this.taxaVistoriaSemTransporte = "";
      this.taxaVistoriaComTransporte = "";
      this.taxaSubscricaoContrato = "";
      this.taxaCorteReligacao = "";
      this.taxaAfericaoContador = "";
      this.taxaEncargoContadorDanificado = "";
      this.taxaAluguerContador = "";
      this.diameter = "";
      this.productId = "";
      this.taxaEncargoViolacaoInstalacao = "";
      this.$refs["delete-product"].hide();
      this.show = false;
    },

    editProduct(product) {
      this.productId = product.id;
      this.taxaDepositoGarantia = product.taxaDepositoGarantia;
      this.taxaVistoriaSemTransporte = product.taxaVistoriaSemTransporte;
      this.taxaVistoriaComTransporte = product.taxaVistoriaComTransporte;
      this.taxaSubscricaoContrato = product.taxaSubscricaoContrato;
      this.taxaCorteReligacao = product.taxaCorteReligacao;
      this.taxaAfericaoContador = product.taxaAfericaoContador;
      this.taxaEncargoViolacaoInstalacao =
        product.taxaEncargoViolacaoInstalacao;
      this.taxaEncargoContadorDanificado =
        product.taxaEncargoContadorDanificado;
      this.taxaAluguerContador = product.taxaAluguerContador;
      this.diameter = product.diameter;
      this.$refs["add-new-product"].show();
    },

    async getAllProducts() {
      this.show = true;
      try {
        const allProducts = await ProductService.getAllProducts(this.token);
        if (allProducts.success == true) {
          this.products = allProducts.result;
          console.log(allProducts);
          this.show = false;
        } else {
          this.show = false;
          this.products = [];
        }
      } catch (err) {
        console.log(err);
        this.$bvToast.toast(err, {
          title: "",
          variant: "danger",
          solid: true,
        });
        this.show = false;
        this.products = [];
      }
    },

    async saveProduct() {
      this.show = true;
      const passingValues = {
        productId: this.productId,
        taxaDepositoGarantia: this.taxaDepositoGarantia,
        taxaVistoriaSemTransporte: this.taxaVistoriaSemTransporte,
        taxaVistoriaComTransporte: this.taxaVistoriaComTransporte,
        taxaSubscricaoContrato: this.taxaSubscricaoContrato,
        taxaCorteReligacao: this.taxaCorteReligacao,
        taxaAfericaoContador: this.taxaAfericaoContador,
        taxaEncargoContadorDanificado: this.taxaEncargoContadorDanificado,
        taxaEncargoViolacaoInstalacao: this.taxaEncargoViolacaoInstalacao,
        taxaAluguerContador: this.taxaAluguerContador,
        diameter: this.diameter,
      };

      try {
        const product = await ProductService.insertOrUpdateProduct(
          passingValues,
          this.token
        );
        if (product.success == true) {
          this.$bvToast.toast(product.message, {
            title: "",
            variant: "success",
            solid: true,
          });
          this.$refs["add-new-product"].hide();
        } else {
          this.$bvToast.toast(product.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
        this.clearValues();
        this.getAllProducts();
      } catch (err) {
        this.clearValues();
        console.log(err);
      }
    },
  },
};
</script>

