class BillingCalculations {

    static domesticBelowFive(quantity, tariff) {

        const subTotal = parseFloat(tariff.serviceAvailabilityFee) + parseFloat(tariff.socialTire);
        const tax = (0.75 * 0.17) * subTotal;
        const total = subTotal + tax;

        const purchaseInformation = [{
            id: 0,
            subTotal,
            tax,
            total,
            serviceAvailabilityFee: tariff.serviceAvailabilityFee,
            totalUnities: quantity,
        }];

        return purchaseInformation;
    }

    static domesticAboveFive(qty, tariff) {
        const quantity = parseFloat(qty);

        let baseEscDois = 0;
        let baseEscTres = 0;
        let baseEscUm = 5;

        if (quantity == 6) {
            baseEscDois = 1;
        } else if (quantity == 7) {
            baseEscDois = 2;
        } else if (quantity > 7) {
            baseEscTres = quantity - 7;
            baseEscDois = 2;
        }

        const taxaFixa = parseFloat(tariff.serviceAvailabilityFee);
        const escalaoUm = parseFloat(tariff.socialTire);
        const escalaoDois = parseFloat(tariff.firstSeven) * baseEscDois;
        const escalaoTres = parseFloat(tariff.aboveSeven) * baseEscTres;
        const subTotal = taxaFixa + escalaoUm + escalaoDois + escalaoTres;
        const tax = (0.75 * 0.17) * subTotal;
        const total = subTotal + tax;

        const purchaseInformation = [{
            id: 2,
            escalaoUm,
            escalaoDois,
            escalaoTres,
            subTotal,
            total,
            tax,
            fixedTax: tariff.serviceAvailabilityFee,
            base: [
                {
                    baseEscUm,
                    baseEscDois,
                    baseEscTres,
                }
            ]
        }];
        // console.log(purchaseInformation)
        return purchaseInformation;
    }

    static fontCalc(quantity, tariff) {
        const subTotal = parseFloat(quantity) * parseFloat(tariff.pricePerMeterCubic);
        const tax = (0.75 * 0.17) * subTotal;
        const total = tax + subTotal;

        const purchaseInformation = [{
            id: 1,
            quantity,
            subTotal,
            tax,
            total,
            fixedTax: tariff.serviceAvailabilityFee,
            base: []
        }];

        return purchaseInformation;
    }

    static calculatePublicComercialAndIndustrial(quantity, tariff) {
        const qty = parseFloat(quantity);
        const escalaoUm = parseFloat(tariff.firstFifteen) + parseFloat(tariff.serviceAvailabilityFee)
        let baseEscDois = 0;

        if (quantity > 15) {
            baseEscDois = qty - 15;
        }
        const escalaoDois = baseEscDois * parseFloat(tariff.aboveFifteen);

        const subTotal = escalaoUm + escalaoDois;
        const tax = (0.75 * 0.17) * subTotal;
        const total = tax + subTotal;

        const purchaseInformation = [{
            id: 4,
            escalaoUm,
            escalaoDois,
            base: [
                {
                    baseEscDois
                }
            ],
            fixedTax: tariff.serviceAvailabilityFee,
            subTotal,
            tax,
            total,
        }];

        return purchaseInformation;
    }

}

export default BillingCalculations;