<template>
  <div>
    <nav-bar />
    <pulse-loader class="text-center mt-4" v-if="isLoading" />
    <b-container class="bv-example-row mt-2" v-else>
      <div>
        <b-row>
          <b-col lg="10" md="8" sm="6">
            <h5 class="text-white">
              <small>{{ $t("tariff_management") }}</small>
            </h5>
          </b-col>
          <b-col>
            <b-button
              v-b-modal.modal-center
              variant="secondary"
              @click="openModalTarrif()"
              size="sm"
              block
              >{{ $t("new_tariff") }}&nbsp;&nbsp;&nbsp;&nbsp;
              <i class="fas fa-plus"></i>
            </b-button>
          </b-col>
        </b-row>
        <hr class="bg-info mt-2" />
        <b-row class="mt-2">
          <tarrif-card
            v-for="tarrif in tarrifs"
            :key="tarrif.id"
            :token="token"
            :tarrifs="tarrif"
          />
        </b-row>
      </div>
    </b-container>

    <b-modal hide-footer ref="add-tarrif" :title="$t('new_tariff')">
      <b-form-group id="add-tarif" label-for="add-tarif">
        <b-row>
          <b-col>
            <small>{{ $t("system") }}</small>
            <b-form-select size="sm" v-model="tarrifTypeId" class="mb-3">
              <b-form-select-option
                v-for="tarrif in tarrifs"
                :key="tarrif.id"
                :value="tarrif.id"
                >{{ tarrif.name }}</b-form-select-option
              >
            </b-form-select>
          </b-col>
        </b-row>

        <div v-if="tarrifTypeId === 1">
          <small>
            {{ $t("price_per_m_3") }}
            <sup>3</sup>
          </small>
          <b-form-group id="pricePerMeterCubic" label-for="pricePerMeterCubic">
            <b-form-input
              type="number"
              min="10"
              step="any"
              v-model="pricePerMeterCubic"
              required
              size="sm"
              placeholder="Preço"
            ></b-form-input>
          </b-form-group>
        </div>

        <div v-if="tarrifTypeId === 2 || tarrifTypeId === 3">
          <b-row>
            <b-col>
              <small>{{ $t("fixed_tax") }}</small>
              <b-form-group
                id="serviceAvailabilityFee"
                label-for="serviceAvailabilityFee"
              >
                <b-form-input
                  type="number"
                  min="50"
                  step="any"
                  v-model="serviceAvailabilityFee"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <small> {{ $t("minimum_consuption") }} m<sup>3</sup> </small>
              <b-form-group id="socialTire" label-for="socialTire">
                <b-form-input
                  type="number"
                  min="0"
                  step="any"
                  v-model="socialTire"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <small> {{ $t("first_five") }} m<sup>3</sup> </small>
              <b-form-group label-for="firstFive">
                <b-form-input
                  id="firstFive"
                  type="number"
                  min="50"
                  step="any"
                  v-model="firstFive"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row>
            <b-col>
              <small> {{ $t("first_seven") }} m<sup>3</sup> </small>
              <b-form-group id="firstSeven" label-for="firstSeven">
                <b-form-input
                  type="number"
                  min="0"
                  step="any"
                  v-model="firstSeven"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <small> {{ $t("above_seven") }} m<sup>3</sup> </small>
              <b-form-group id="aboveSeven" label-for="aboveSeven">
                <b-form-input
                  type="number"
                  min="0"
                  step="any"
                  v-model="aboveSeven"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>

        <div v-if="tarrifTypeId >= 4">
          <b-row>
            <b-col>
              <small>{{ $t("fixed_tax") }}</small>
              <b-form-group
                id="serviceAvailabilityFee"
                label-for="serviceAvailabilityFee"
              >
                <b-form-input
                  type="number"
                  min="150"
                  step="any"
                  v-model="serviceAvailabilityFee"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <small> {{ $t("first_fifteen") }} m<sup>3</sup> </small>
              <b-form-group id="firstFifteen" label-for="firstFifteen">
                <b-form-input
                  type="number"
                  min="0"
                  step="any"
                  v-model="firstFifteen"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <small> {{ $t("above_fifteen") }}m<sup>3</sup> </small>
              <b-form-group id="aboveFifteen" label-for="aboveFifteen">
                <b-form-input
                  type="number"
                  min="0"
                  step="any"
                  v-model="aboveFifteen"
                  required
                  size="sm"
                  placeholder="Preço"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>

        <b-button
          class="mt-3 sm"
          :disabled="isLoading == true || !tarrifTypeId"
          block
          @click="insertTarrif()"
          size="sm"
        >
          <template v-if="isLoading">
            {{ $t("updating") }}
            <b-spinner small type="grow"></b-spinner>
          </template>
          <template v-else>{{ $t("submit") }}</template>
        </b-button>
      </b-form-group>
    </b-modal>
  </div>
</template>

<script>
import PulseLoader from "vue-spinner/src/PulseLoader.vue";
import NavBar from "../components/Navbar";
import TarrifCard from "../components/TarrifCard";

import TarrifService from "../http-service/TarrifService";

import { mapGetters } from "vuex";

export default {
  name: "Tarrifs",

  watch: {
    tarrifType: function () {
      return this.tarrifType === 1 ? true : false;
    },
  },

  computed: {
    ...mapGetters(["user", "token"]),
  },

  created() {
    this.$store.commit("SET_CLEAR_CURRENT_VENDING");
    this.fetchTarrifs();
  },

  components: {
    NavBar,
    PulseLoader,
    TarrifCard,
  },

  data() {
    return {
      currentUser: [],
      isLoading: false,
      tarrifs: [],
      tarrifTypeId: 0,
      pricePerMeterCubic: 0,
      socialTire: 0,
      serviceAvailabilityFee: 0,
      firstSeven: 0,
      firstFive: 0,
      aboveSeven: 0,
      firstFifteen: 0,
      aboveFifteen: 0,
    };
  },

  methods: {
    async insertTarrif() {
      const passingValues = {
        tarrifTypeId: this.tarrifTypeId,
        pricePerMeterCubic: this.pricePerMeterCubic,
        socialTire: this.socialTire,
        serviceAvailabilityFee: this.serviceAvailabilityFee,
        firstFive: this.firstFive,
        firstSeven: this.firstSeven,
        aboveSeven: this.aboveSeven,
        firstFifteen: this.firstFifteen,
        aboveFifteen: this.aboveFifteen,
      };

      this.isLoading = true;
      try {
        const tarrifs = await TarrifService.insertTarrifs(
          passingValues,
          this.token
        );

        if (tarrifs.success == true) {
          this.tarrifs = tarrifs.result;
          this.isLoading = false;
          this.fetchTarrifs();
          this.$bvToast.toast("Tarifa inserida com sucesso.", {
            title: "Sucesso",
            variant: "success",
            solid: true,
          });
          this.$refs["add-tarrif"].hide();
        } else {
          this.$bvToast.toast(tarrifs.message, {
            title: "Sucesso",
            variant: "success",
            solid: true,
          });
          this.$refs["add-tarrif"].hide();
        }
      } catch (err) {
        this.isLoading = false;
        this.$bvToast.toast("" + err, {
          title: "Sucesso",
          variant: "danger",
          solid: true,
        });
        this.$refs["add-tarrif"].hide();
      }
    },

    openModalTarrif() {
      this.$refs["add-tarrif"].show();
    },

    async fetchTarrifs() {
      this.isLoading = true;
      try {
        const tarrifs = await TarrifService.getAllTarrifs(this.token);
        this.tarrifs = tarrifs.result;
        this.isLoading = false;
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Sucesso",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },
  },
};
</script>