import axios from "axios";
const url = "api/reading";

/* eslint-disable no-async-promise-executor */

class ReadingService {
  // Get User
  static getReadings(branchId, token) {
    const authString = `Bearer ` + token;

    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(`${url}?branchId=${branchId}`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  // Get Users By Company
  static getReadingsByCustomer(customerId, tariffType, token) { 
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(`${url}/${customerId}/${tariffType}`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  // Get Users By Company
  static deleteReading(readingId, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.delete(`${url}/${readingId}`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static updateReading(reading, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.put(url, reading, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static validateReading(reading, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.patch(url, reading, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static insertReadings(reading, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, reading, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default ReadingService;
