<template>
  <div>
    <nav-bar />
    <b-container fluid class="bv-example-row">
      <div class="container-group">
        <div class="card">
          <div class="card-body">
            <!-- Reports -->
            <b-overlay :show="isLoading" rounded="sm">
              <b-row>
                <b-col>
                  <b-row class="mb-1">
                    <b-col
                      ><b-form-select
                        v-model="selectedPaymentMethod"
                        :options="options"
                      ></b-form-select
                    ></b-col>
                    <b-col>
                      <b-form-datepicker
                        placeholder="M"
                        block
                        size="sm"
                        id="from-datepicker"
                        v-model="fromDate"
                      ></b-form-datepicker
                    ></b-col>
                    <b-col
                      ><b-form-datepicker
                        :placeholder="$t('to')"
                        block
                        size="sm"
                        id="to-datepicker"
                        v-model="toDate"
                      ></b-form-datepicker
                    ></b-col>
                    <b-col cols="1"
                      ><b-button
                        @click="searchPayments()"
                        :disabled="(!toDate && !fromDate) || isLoading"
                        size="sm"
                        variant="success"
                        block
                        ><b-icon icon="search"></b-icon> </b-button
                    ></b-col>
                    <b-col cols="1"
                      ><b-button
                        @click="generateExcelReport()"
                        :disabled="reports.length == 0"
                        size="sm"
                        variant="success"
                        block
                        >Excel <b-icon icon="file-excel-fill"></b-icon> </b-button
                    ></b-col>
                    <b-col cols="1"
                      ><b-button
                        @click="generatePDFReport()"
                        :disabled="reports.length == 0"
                        size="sm"
                        variant="success"
                        block
                        >Pdf <b-icon icon="printer-fill"></b-icon> </b-button
                    ></b-col>
                  </b-row>

                  <table
                    class="table table-sm table-hover table-stripped"
                    v-if="reports.length > 0"
                  >
                    <thead>
                      <tr style="font-size:12pt">
                        <th colspan="7">TOTAL COLETADO</th>
                        <th
                          colspan="3"
                          class="text-right"
                          v-money-format="totalRecebido"
                        ></th>
                      </tr>
                    </thead>
                    <thead>
                      <tr id="reportList" class="bg-dark text-white">
                        <th>{{ $t("name") }}</th>
                        <th>{{ $t("id_range") }}</th>
                        <th>Factura/Recibo nº</th>
                        <th>{{ $t("date") }}</th>
                        <th>Data de confirmação na loja</th>
                        <th>{{ $t("payment_reference") }}</th>
                        <th>{{ $t("phone") }}</th>
                        <th>{{ $t("cashier") }}</th>
                        <th>{{ $t("payment_method") }}</th>
                        <th class="text-right">{{ $t("total_paid") }}</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="report in reports" :key="report.id">
                        <td>{{ report.customerName }}</td>
                        <td>{{ report.barcode }}</td>
                        <td>{{ report.billId }}/SAF</td>
                        <td>{{ report.dateOfPayment }}</td>
                        <td>{{ report.createdAt }}</td>
                        <td>{{ report.reference.toUpperCase() }}</td>
                        <td>{{ report.phoneNumber }}</td>
                        <td>{{ report.staffName }}</td>
                        <td>{{ getPaymentMethod(report.paymentMethod) }}</td>
                        <td class="text-right">
                          {{ report.totalPaid.toFixed(2) }} MZN
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <b-alert
                    class="text-center"
                    v-if="reports.length === 0"
                    variant="danger"
                    show
                    dismissible
                    ><i class="fas fa-info"></i>
                    {{ $t("unavailable_purchases") }}
                  </b-alert>
                </b-col>
              </b-row>
            </b-overlay>
          </div>
        </div>
      </div>
    </b-container>
  </div>
</template>

<script>
import PaymentService from "../http-service/PaymentService";
// import MonthlyReportsService from '../http-service/MonthlyReportsService'

import NavBar from "../components/Navbar";
import Vue from "vue";
import { mapGetters } from "vuex";
import moment from "moment";

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

import exportFromJSON from "export-from-json"

export default Vue.extend({
  name: "Reports",

  created() {
    this.fromDate = moment().format("YYYY-MM-DD");
    this.toDate = moment().format("YYYY-MM-DD");
    this.searchPayments();
  },

  mounted() {},

  components: {
    NavBar,
  },

  data() {
    return {
      reports: [],
      isLoading: false,
      fromDate: "",
      toDate: "",
      selectedPaymentMethod: 0,

      options: [
        { value: 0, text: "Meio de pagamento" },
        { value: 1, text: "Boca de Caixa" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "M-Pesa Open-Api" },
        { value: 8, text: "E-Mola" },
      ],

      totalRecebido: 0,
      value: "",
    };
  },

  computed: {
    ...mapGetters(["user", "token", "user", "branches", "currentBranch"]),
  },

  watch: {},

  methods: {
    async searchPayments() {
      const passingValues = {
        paymentMethod: this.selectedPaymentMethod,
        from: this.fromDate,
        to: this.toDate,
      };
      this.isLoading = true;

      try {
        const purchase = await PaymentService.getAllPayments(
          passingValues,
          this.token
        );
        if (purchase.success == false) {
          this.reports = [];
          this.totalRecebido = 0;
        } else {
          this.reports = purchase.result;
          this.totalRecebido = purchase.totalRecebido;
        }
        this.isLoading = false;
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Error:",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },

    generateExcelReport() {
      const body = [];

      for (let report of this.reports) {
        const row = {
          "Data de pagamento": report.dateOfPayment,
          "Data de confirmação": report.createdAt,
          "Factura/Recibo": `${report.billId}/SAF`,
          "Nome do cliente": report.customerName,
          "Referencia": report.reference.toUpperCase(),
          "Modo de pagamento": this.getPaymentMethod(report.paymentMethod),
          "Total Pago": report.totalPaid.toFixed(2),
          "Caixa": report.staffName
        }
        body.push(row)
      }
      const fileName = "RelatórioDeRecebimentos_" + this.fromDate + "_A_" + this.toDate;
      const data = body;
      const exportType = "xls";

      exportFromJSON({ data, fileName, exportType });      
    },

    async generatePDFReport() {
      const body = [];

      for (let report of this.reports) {
        const rows = [];

        rows.push({ text: report.createdAt, style: "estilo" });
        rows.push({
          text: `${report.billId}/SAF`,
          style: "estilo",
        });
        rows.push({ text: report.customerName, style: "estilo" });
        rows.push({ text: report.phoneNumber, style: "estilo" });
        rows.push({ text: report.reference.toUpperCase(), style: "estilo" });
        rows.push({
          text: this.getPaymentMethod(report.paymentMethod),
          style: "estilo",
        });
        rows.push({ text: report.totalPaid.toFixed(2), style: "estilo" });

        body.push(rows);
      }

      const docDefinition = {
        content: [
          {
            columns: [
              {
                text: `RELATÓRIO DE PAGAMENTOS\n Data ${this.fromDate} à ${this.toDate}\n Total coletado: ${this.totalRecebido} MZN\n\n`,
                style: "header",
              },
            ],
          },
          {
            table: {
              body: [
                [
                  { text: "Data", style: "columnsTitle" },
                  { text: "Factura/Recibo", style: "columnsTitle" },
                  { text: "Cliente", style: "columnsTitle" },
                  { text: "Telefone", style: "columnsTitle" },
                  { text: "Referência", style: "columnsTitle" },
                  { text: "Método de Pagamento", style: "columnsTitle" },
                  { text: "Valor Pago", style: "columnsTitle" },
                ],
                ...body,
              ],
            },
          },
        ],
        styles: {
          header: { fontSize: 10, bold: true },
          columnsTitle: {
            fontSize: 8,
            bold: true,
            fillColor: "#7159c1",
            color: "#FFFFFF",
          },
          estilo: {
            fontSize: 8,
          },
        },
      };
      pdfMake.createPdf(docDefinition).open();
    },

    getPaymentMethod(method) {
      if (method == 1) {
        return "Boca do Caixa";
      } else if (method == 2) {
        return "Depósito Bancário";
      } else if (method == 3) {
        return "Transferência Bancária";
      } else if (method == 4) {
        return "POS";
      } else if (method == 5) {
        return "Cheque";
      } else if (method == 6) {
        return "SISTAFE";
      } else if (method == 7) {
        return "M-Pesa Open-Api";
      } else if (method == 8) {
        return "E-Mola";
      }
    },
  },
});
</script>

<style lang="scss" scoped>
.container-group {
  overflow: hidden auto;
  margin-left: -15px;
  width: calc(100% + 30px);
  height: calc(100vh - 55px);
  .container {
    font-size: 10pt;
    padding-top: 0px;
    padding-bottom: 15px;
  }
}

.table {
  font-size: 10px;
  text-align: left;

  tr {
    cursor: pointer;
  }
}
</style>