import axios from "axios";
const url = "api/search_customer";

/* eslint-disable no-async-promise-executor */

class SearchCustomers {
  // Get User
  static getCustomers(token) {
    const authString = `Bearer ` + token;

    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(`${url}?prepaid=1`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  // Get Users By Company
  static searchCustomers(search, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(`${url}/${search}/1`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  // Create or Update User
  static insertOrUpdateCustomer(customer, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, customer, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default SearchCustomers;
