<template>
  <div class="login-body">
    <login-navbar />
    <!--<hr class="bg-info mt-0" />-->
    <b-row>
      <b-col lg="12" sm="12" md="12">
        <!-- form-login -->
        <form @submit.prevent="registerNewCustomer()" class="form-login">
          <div class="card">
            <div class="card-header text-center">
              {{ $t("new_customer") }}
            </div>
            <div class="card-body">
              <div class="form-group">
                <input
                  :disabled="loading"
                  v-model="name"
                  required
                  type="text"
                  size="sm"
                  :placeholder="$t('name')"
                  class="form-control"
                />
              </div>
              <b-row>
                <b-col lg="6" sm="12" md="6"
                  ><b-form-group id="email">
                    <b-form-input
                      type="email"
                      v-model="email"
                      :disabled="loading"
                      required
                      size="sm"
                      :placeholder="$t('email')"
                    ></b-form-input> </b-form-group
                ></b-col>
                <b-col lg="6" sm="12" md="6"
                  ><b-form-group id="password">
                    <b-form-input
                      type="password"
                      v-model="password"
                      :disabled="loading"
                      required
                      size="sm"
                      :placeholder="$t('password')"
                    ></b-form-input> </b-form-group
                ></b-col>
              </b-row>

              <b-row>
                <b-col lg="6" sm="12" md="6">
                  <b-form-group id="phone">
                    <b-form-input
                      type="number"
                      v-model="phone"
                      :disabled="loading"
                      required
                      size="sm"
                      :placeholder="$t('phone')"
                    ></b-form-input> </b-form-group
                ></b-col>
                <b-col lg="6" sm="12" md="6"
                  ><b-form-group id="nuit">
                    <b-form-input
                      type="number"
                      v-model="nuit"
                      :disabled="loading"
                      required
                      size="sm"
                      :placeholder="$t('nuit')"
                    ></b-form-input> </b-form-group
                ></b-col>
              </b-row>

              <b-row>
                <b-col lg="6" sm="12" md="6"
                  ><b-form-group id="identification">
                    <b-form-input
                      type="text"
                      :disabled="loading"
                      v-model="id_number"
                      required
                      size="sm"
                      :placeholder="$t('identification')"
                    ></b-form-input> </b-form-group
                ></b-col>
                <b-col lg="6" sm="12" md="6"
                  ><b-form-group id="address">
                    <b-form-input
                      type="text"
                      :disabled="loading"
                      v-model="address"
                      required
                      size="sm"
                      :placeholder="$t('address')"
                    ></b-form-input> </b-form-group
                ></b-col>
              </b-row>

              <b-row>
                <b-col lg="6" sm="12" md="6"
                  ><small>{{ $t("province") }}</small>
                  <b-form-select
                    size="sm"
                    :disabled="loading"
                    v-model="provinceId"
                    class="mb-3"
                  >
                    <b-form-select-option
                      v-for="province in provinces"
                      :key="province.id"
                      :value="province.id"
                      :disabled="loading"
                      >{{ province.name }}</b-form-select-option
                    >
                  </b-form-select></b-col
                >
                <b-col lg="6" sm="12" md="6"
                  ><small>{{ $t("district") }}</small>
                  <b-form-select
                    size="sm"
                    :disabled="loading"
                    v-model="districtId"
                    class="mb-3"
                  >
                    <b-form-select-option
                      v-for="district in selectedDistricts"
                      :key="district.id"
                      :value="district.id"
                      :disabled="provinceId <= 0"
                      >{{ district.name }}</b-form-select-option
                    >
                  </b-form-select></b-col
                >
              </b-row>

              <b-row>
                <b-col
                  ><small>{{ $t("neighbor") }}</small>
                  <b-form-select
                    size="sm"
                    :disabled="loading"
                    v-model="neighborhood"
                    class="mb-3"
                  >
                    <b-form-select-option
                      v-for="neighbor in selectedneighbors"
                      :key="neighbor.id"
                      :value="neighbor.id"
                      :disabled="provinceId <= 0"
                      >{{ neighbor.name }}</b-form-select-option
                    >
                  </b-form-select></b-col
                >
                <b-col
                  ><div class="form-group">
                    <small>{{ $t("tarrif_type") }}</small>
                    <b-form-select
                      size="sm"
                      :disabled="loading"
                      v-model="tarrifTypeId"
                      :options="tariffs"
                    ></b-form-select></div
                ></b-col>
              </b-row>

              <b-row>
                <b-col
                  ><p
                    @click="backToLogin()"
                    class="btn btn-primary btn-sm w-100"
                  >
                    <template>
                      {{ $t("login") }}
                    </template>
                  </p></b-col
                >
                <b-col
                  ><button
                    class="btn btn-success btn-sm w-100"
                    :disabled="loading"
                  >
                    <template v-if="loading">
                      {{ $t("submiting") }}
                      <b-spinner small type="grow"></b-spinner>
                    </template>
                    <template v-else>{{ $t("submit") }}</template>
                  </button></b-col
                >
              </b-row>
            </div>
          </div>
        </form>
      </b-col>
    </b-row>
    <b-row class="footer">
      <b-col
        >Copyright ©2021 Water & Energy Resources, LDA. All rights
        reserved.</b-col
      >
    </b-row>
  </div>
</template>

<script>
import Vue from "vue";
// import * as firebase from "firebase";
// import "firebase/auth";
import { mapGetters } from "vuex";
import LoginNavbar from "../components/LoginNavbar";
import ProvinceService from "../http-service/ProvinceService";
import JWTService from "../http-service/JwtService";
import CustomerService from "../http-service/CustomerService";

// import BillingCalculations from "../calcs/BillingCalculations";

export default Vue.extend({
  computed: {
    ...mapGetters(["user"]),
    nextRoute() {
      return this.$route.query.redirect || "/dashboard";
    },
  },

  components: {
    LoginNavbar,
  },

  data() {
    return {
      tariffs: [
        { value: 1, text: "Fontenária" },
        { value: 2, text: "Doméstica" },
        { value: 3, text: "Municipal" },
        { value: 4, text: "Comércio & Público" },
        { value: 5, text: "Indústria" },
      ],
      name: "",
      phone: "",
      nuit: "",
      address: "",
      password: "",
      tarrifTypeId: "",
      email: "",
      id_number: "",
      neighborhood: "",
      error: null,
      loading: false,
      provinces: [],
      districts: [],

      districtId: 0,
      provinceId: 0,
      id: 0,
      selectedDistricts: "",
    };
  },

  watch: {
    provinceId: function () {
      this.selectedDistricts = this.districts.filter((a) => {
        return a.provinceId === this.provinceId;
      });
    },
  },

  created() {
    this.getProvinces();
  },

  methods: {
    backToLogin() {
      this.$router.push("/");
    },

    async registerUserToDatabase(passingValues) {
      try {
        const userToken = await JWTService.getApiKey(passingValues.uid);
        if (userToken.success == true) {
          const token = userToken.token;
          const register = await CustomerService.insertOrUpdateCustomer(
            passingValues,
            token
          );
          if (register.success == true) {
            this.$bvToast.toast("Pré registo efectuado com sucesso", {
              title: "Sucesso",
              variant: "success",
              solid: true,
            });
            this.loading = false;
            // firebase.auth().signOut();
            this.$router.replace("/");
          } else {
            this.$bvToast.toast(register.message, {
              title: "Error",
              variant: "danger",
              solid: true,
            });
            this.loading = false;
          }
        } else {
          this.$bvToast.toast(userToken.message, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
          this.loading = false;
        }
      } catch (err) {
        console.log(err);
        this.loading = false;
      }
    },

    async getProvinces() {
      this.loading = true;
      try {
        let data = await ProvinceService.getProvinces();
        if (data.success == true) {
          this.provinces = data.result;
          // console.log(this.provinces);
          this.getDistricts();
        } else {
          this.loading = false;
          // console.log(data.message);
        }
      } catch (err) {
        this.error = err.message;
        this.loading = false;
      }
    },

    async getDistricts() {
      this.loading = true;
      try {
        let data = await ProvinceService.getDistricts(1);
        if (data.success == true) {
          this.districts = data.result;
          // console.log(this.districts);
          this.loading = false;
        } else {
          this.loading = false;
          // console.log(data.message);
        }
      } catch (err) {
        this.error = err.message;
        this.loading = false;
      }
    },
  },
});
</script>

