import axios from 'axios'
const url = 'api/batchInvoice'

/* eslint-disable no-async-promise-executor */

class BatchPdfInvoice {
    // Get Users By Company
    static getInvoice(uniqueBillId, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${uniqueBillId}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
    
    static generatePDF(barcode, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}?barcode=${barcode}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static createPdfInvoice(billInformation, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, billInformation, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default BatchPdfInvoice