<template>
  <div class="quotation">
    <b-card class="mb-0">
      <b-row>
        <b-col cols="8"><b class="text-black">Novos clientes</b></b-col>
        <b-col
          class="text-right"
          style="cursor: pointer"
          @click="refreshPreRegistration()"
        >
          <b-button pill variant="success" size="sm"
            ><b-icon variant="light" icon="arrow-clockwise"></b-icon
          ></b-button>
        </b-col>
      </b-row>
      <hr class="bg-primary mt-1 mb-1 w-1" />
      <table
        v-if="pendingRegistrations.length > 0"
        class="table table-sm table-hover mt-0"
        style="width: 100%"
      >
        <tbody>
          <tr
            style="font-size: 9pt; cursor: pointer"
            v-for="registration in pendingRegistrations"
            :key="registration.id"
          >
            <td>{{ registration.name }}</td>
            <td>
              <b-row>
                <b-col
                  @click="deletePreregistration(registration)"
                  class="text-center cursor-pointer"
                >
                  <b-icon variant="danger" size="lg" icon="trash"></b-icon>
                </b-col>
                <b-col
                  @click="opendQuotationForm(registration)"
                  class="text-center"
                >
                  <b-icon variant="primary" size="lg" icon="eye"></b-icon>
                </b-col>
              </b-row>
            </td>
          </tr>
        </tbody>
      </table>
    </b-card>

    <b-modal
      hide-footer
      ref="quotation"
      size="xl"
      :title="`Factura Proforma Nº: ${invoiceId}/SAAF/${new Date().getFullYear()}`"
    >
      <b-overlay :show="isLoading" rounded="sm">
        <b-row>
          <b-col lg="4">
            <small><b>Nome do cliente</b></small>
            <b-form-group label-for="name">
              <b-form-input
                id="name"
                type="text"
                :disabled="isLoading"
                v-model="customerName"
                required
                size="sm"
                placeholder="Nome do cliente"
              ></b-form-input>
            </b-form-group>
            <small><b>Endereço</b></small>
            <b-form-group label-for="address">
              <b-form-input
                id="address"
                type="text"
                :disabled="isLoading"
                v-model="customerAddress"
                required
                size="sm"
                placeholder="Endereço"
              ></b-form-input>
            </b-form-group>
            <small><b>Telemóvel</b></small>
            <b-form-group label-for="phone">
              <b-form-input
                id="phone"
                type="number"
                :disabled="isLoading"
                v-model="customerPhoneNumber"
                required
                size="sm"
                placeholder="Telemóvel"
              ></b-form-input>
            </b-form-group>
            <small><b>NUIT</b></small>
            <b-form-group label-for="nuit">
              <b-form-input
                id="nuit"
                type="number"
                :disabled="isLoading"
                v-model="customerNuit"
                required
                size="sm"
                placeholder="NUIT"
              ></b-form-input>
            </b-form-group>
            <small><b>DETALHES DA COMPRA</b></small>
            <hr class="bg-primary" />
            <b-form-select
              size="sm"
              v-model="productName"
              :options="productList"
            >
            </b-form-select>
            <small><b>Preço</b></small>
            <b-form-group label-for="price">
              <b-form-input
                id="price"
                type="number"
                step="any"
                min="1"
                v-model="price"
                :disabled="isLoading"
                required
                size="sm"
                placeholder="Item"
              ></b-form-input>
            </b-form-group>
            <small><b>Quantidade</b></small>
            <b-form-group label-for="quantity">
              <b-form-input
                id="quantity"
                type="number"
                min="1"
                :disabled="isLoading"
                v-model="quantity"
                required
                size="sm"
                placeholder="Item"
              ></b-form-input>
            </b-form-group>
            <b-button
              @click="addProdutToCart()"
              :disabled="isLoading"
              block
              size="sm"
              ><b-icon icon="cart-plus-fill"></b-icon>
              Adicionar ao carrinho
            </b-button>
          </b-col>
          <b-col>
            <b-row>
              <b-col><b>Previsualização</b></b-col>
              <b-col class="text-right"
                ><b>Data de emissão: {{ dateOfQuote }}</b></b-col
              >
            </b-row>
            <hr class="bg-primary mt-1 mb-1" />

            <table class="table table-sm table-hover">
              <thead>
                <tr class="" style="font-size: 9pt">
                  <th class="text-center">Deletar</th>
                  <th colspan="2">Descrição do Produto</th>
                  <th>Preço Unitário</th>
                  <th>Quantidade</th>
                  <th class="text-right">Subtotal</th>
                </tr>
                <tr
                  v-for="(item, index) in products"
                  :key="index"
                  style="font-size: 8pt"
                >
                  <td @click="deleteItemInCart(item)" class="text-center">
                    <b-icon variant="danger" icon="trash-fill"></b-icon>
                  </td>
                  <td colspan="2">{{ item.item }}</td>
                  <td v-money-format="item.price"></td>
                  <td>{{ item.quantity }}</td>
                  <td
                    class="text-right"
                    v-money-format="item.price * item.quantity"
                  ></td>
                </tr>
              </thead>

              <tbody>
                <tr>
                  <td colspan="6"><br /><br /></td>
                </tr>
                <tr style="font-size: 10pt">
                  <th colspan="3" class="">Subtotal</th>
                  <th
                    colspan="3"
                    class="text-right"
                    v-money-format="calculateSubTotal()"
                  ></th>
                </tr>
                <tr style="font-size: 10pt">
                  <th colspan="3" class="">IVA - {{ vat * 100 }}%</th>
                  <th
                    colspan="3"
                    class="text-right"
                    v-money-format="vat * calculateSubTotal()"
                  ></th>
                </tr>
                <tr style="font-size: 10pt">
                  <th colspan="3" class="">TOTAL A PAGAR</th>
                  <th
                    colspan="3"
                    class="text-right"
                    v-money-format="
                      calculateSubTotal() + calculateSubTotal() * vat
                    "
                  ></th>
                </tr>
                <tr>
                  <td colspan="3">
                    <b-button
                      @click="clearInvoice()"
                      block
                      size="sm"
                      variant="danger"
                      :disabled="isLoading"
                      ><b-icon icon="trash-fill"></b-icon>
                      Cancelar
                    </b-button>
                  </td>
                  <td colspan="3" class="text-right cursor-pointer">
                    <b-button
                      @click="insertProforma()"
                      :disabled="isLoading || products.length <= 0"
                      block
                      size="sm"
                      variant="success"
                      ><b-icon icon="printer-fill"></b-icon>
                      Salvar e imprimir
                    </b-button>
                  </td>
                </tr>
              </tbody>
            </table>
          </b-col>
        </b-row>
      </b-overlay>
    </b-modal>

    <b-modal hide-footer ref="delete-quotation" :title="$t('delete')">
      <p class="text-danger text-center">{{ $t("decide_delete") }}</p>
      <b-row>
        <b-col>
          <b-button block size="sm" @click="cancelDeletion()">{{
            $t("cancel")
          }}</b-button>
        </b-col>
        <b-col>
          <b-button
            block
            size="sm"
            variant="danger"
            @click="decideToDeleteQuotation()"
            >{{ $t("delete") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import Vue from "vue";
import { mapGetters } from "vuex";

import moment from "moment";
moment.locale("pt-br");

// HTTP SERVICES
import PreRegistration from "../../http-service/PreRegistration";
import ProformaService from "../../http-service/ProformaService";

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

export default Vue.extend({
  name: "Quotation",

  props: ["pendingRegistrations", "invoiceId"],

  data() {
    return {
      isLoading: false,
      currentQuotation: {},
      invalidationQuotationId: 0,

      products: [],
      productId: 0,
      item: "",
      productName: 0,
      price: 0,
      quantity: 1,
      total: 0,
      vat: 0.16,
      subTotalVat: 0,
      dateOfQuote: moment(new Date()).format("YYYY-MM-DD"),

      // PROFORMA CUSTOMER DETAILS
      customerName: "",
      customerPhoneNumber: "",
      customerAddress: "",
      customerNuit: "",

      productList: [
        { value: 0, text: "Seleccione um item", price: 0.0 },
        { value: 1, text: "Kit de ligação de água doméstico", price: 2852.0 },
        {
          value: 2,
          text: "Kit de ligação de água público, comercial e industrial",
          price: 5500.0,
        },
        { value: 3, text: "Supervisão técnica", price: 1165.09 },
        {
          value: 4,
          text: "Abertura de cabocos e assentamento da tubagem",
          price: 30.0,
        },
        { value: 5, text: "Tubo copoleno 3/4 barra azul", price: 45.0 },
        { value: 6, text: 'Tubo copoleno 1" barra azul', price: 55.0 },
        { value: 7, text: "Tubo copoleno 50mm barra azul", price: 90.0 },
        { value: 8, text: "Tampão de 50mm", price: 500.0 },
        { value: 9, text: "Tê de redução de (63/50)mm", price: 950.0 },
        { value: 10, text: "Contador pré-pago", price: 9623.93 },
      ],
      termsAndConditions: `Todos os materiais e equipamentos fornecidos pela TSENANE, LDA observam os melhores padrões de qualidade, tem garantia de 12 meses contra defeito de fabrico e se a observarem as  recomendações  de Instalação e do transporte.`,
    };
  },

  created() {
    // console.log(this.pendiRegistrations)
  },

  watch: {
    productName: function () {
      const selectedItem = this.productList.filter((a) => {
        return a.value == this.productName;
      });
      this.item = selectedItem[0].text;
      this.price = selectedItem[0].price;
    },
  },

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company"]),
  },

  methods: {
    opendQuotationForm(customer) {
      this.currentQuotation = customer;

      this.customerName = customer.name;
      this.customerPhoneNumber = customer.phoneNumber;
      this.customerAddress = customer.address;
      this.customerNuit = customer.nuit;

      this.$refs["quotation"].show();
    },

    refreshPreRegistration() {
      this.$emit("update-quotation");
    },

    deletePreregistration(quotation) {
      this.invalidationQuotationId = quotation.id;
      this.$refs["delete-quotation"].show();
    },
    cancelDeletion() {
      this.invalidationQuotationId = 0;
      this.$refs["delete-quotation"].hide();
    },

    async decideToDeleteQuotation() {
      this.isLoading = true;

      try {
        const deletePreRegistration =
          await PreRegistration.deletePreRegistration(
            this.invalidationQuotationId,
            this.token
          );
        if (deletePreRegistration.success == true) {
          this.$bvToast.toast("Item excluído com sucesso!", {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
          this.$emit("update-quotation");
        } else {
          this.$bvToast.toast(deletePreRegistration.message, {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
      }
      this.$refs["delete-quotation"].hide();
    },

    addProdutToCart() {
      if (this.item == "") {
        this.$bvToast.toast("Digite o nome do produto por favor!", {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        return;
      } else if (this.price < 10) {
        this.$bvToast.toast("Digite um preço válido!", {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        return;
      } else if (this.quantity < 1) {
        this.$bvToast.toast("Digite uma quantidade válida!", {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        return;
      }

      this.products.push({
        id: this.productId == 0 ? 1 : this.productId++,
        item: this.item,
        quantity: this.quantity,
        price: this.price,
      });
      this.item = "";
      this.quantity = 1;
      this.price = 0;
      this.productName = 0;
    },

    calculateSubTotal() {
      const subtotal = this.products.reduce(
        (sum, p) => sum + p.quantity * p.price,
        0
      );
      this.subTotalVat = subtotal * this.vat;
      return subtotal;
    },

    clearInvoice() {
      this.products = [];
      this.$refs["quotation"].hide();
    },

    deleteItemInCart(item) {
      const index = this.products.indexOf(item);
      this.products.splice(index, 1);
    },

    async saveProforma() {
      const body = [];

      for (let row of this.products) {
        const rows = [];

        // rows.push({ text: row.id, fontSize: 8 })
        rows.push({ text: row.item, fontSize: 9 });
        rows.push({
          text: parseFloat(row.price).toFixed(2),
          fontSize: 9,
          alignment: "right",
        });
        rows.push({ text: row.quantity, fontSize: 9, alignment: "right" });
        rows.push({
          text: (parseFloat(row.quantity) * parseFloat(row.price)).toFixed(2),
          fontSize: 9,
          alignment: "right",
        });

        body.push(rows);
      }

      let docDefinition = {
        // watermark: { text: this.company[0].name, opacity: 0.08 },
        header: [
          {
            image: await this.getBase64ImageFromURL(
              "https://saaf-tsenane.herokuapp.com/img/logo_12.2b7a27d2.jpeg"
            ),
            width: 300,
            height: 200,
            margin: [30, 0, 0, 0],
          },
        ],
        footer: {
          columns: [
            {
              text: "Documento processado por computador",
              alignment: "left",
              fontSize: 8,
              margin: [30, 0, 0, 0],
            },
            {
              text: "TSENANE Software",
              alignment: "right",
              fontSize: 8,
              margin: [0, 0, 30, 0],
            },
          ],
        },

        content: [
          {
            columns: [
              [
                {
                  text:
                    "Factura Proforma Nº: " +
                    this.invoiceId +
                    "/SAAF/" +
                    new Date().getFullYear(),
                  fontSize: 10,
                  bold: true,
                  alignment: "right",
                },
                {
                  text: `\nData de emissão: ${moment().format("DD-MM-YYYY")}`,
                  fontSize: 8,
                  alignment: "right",
                },
                {
                  text: `Validade da proposta: 14 dias`,
                  fontSize: 8,
                  alignment: "right",
                },
              ],
            ],
          },
          {
            text: "\n\n\n\n\n\n\n\n\n\n",
          },
          {
            text: `Exmo(s) Sr(s): ${this.customerName}`,
            bold: true,
            fontSize: 12,
          },
          {
            text: `Endereço: ${this.customerAddress}`,
            style: "customerStyle",
          },
          {
            text: `Telefone: ${this.customerPhoneNumber}`,
            style: "customerStyle",
          },
          {
            text: `NUIT: ${this.customerNuit}`,
            style: "customerStyle",
          },
          {
            text: `E-mail: ${this.currentQuotation.email}`,
            style: "customerStyle",
          },
          {
            text: "\n\n",
          },
          {
            table: {
              widths: ["*", "auto", "auto", "auto"],
              body: [
                [
                  { text: "Descrição do produto", style: "columnsTitle" },
                  {
                    text: "Preço unitário",
                    style: "columnsTitle",
                    alignment: "center",
                  },
                  { text: "Qtd", style: "columnsTitle", alignment: "center" },
                  {
                    text: "Subtotal",
                    style: "columnsTitle",
                    alignment: "right",
                  },
                ],
                ...body,
                [{ text: "\n\n\n\n\n\n\n", colSpan: 4 }, {}, {}, {}],
                [
                  { text: "Subtotal", colSpan: 3, style: "columnsSubTotals" },
                  {},
                  {},
                  {
                    text: this.calculateSubTotal().toFixed(2),
                    style: "columnsSubTotals",
                    alignment: "right",
                  },
                ],
                [
                  { text: "IVA - 16%", colSpan: 3, style: "columnsSubTotals" },
                  {},
                  {},
                  {
                    text: (this.calculateSubTotal() * this.vat).toFixed(2),
                    style: "columnsSubTotals",
                    alignment: "right",
                  },
                ],
                [
                  { text: "Total a pagar", colSpan: 3, style: "columnsTotal" },
                  {},
                  {},
                  {
                    text:
                      (
                        this.calculateSubTotal() * this.vat +
                        this.calculateSubTotal()
                      ).toFixed(2) + " MZN",
                    style: "columnsTotal",
                    alignment: "right",
                  },
                ],
              ],
            },
            layout: "lightHorizontalLines",
          },
          {
            text: "\n",
          },
          {
            text: "Detalhes sobre meio de pagamentos:\n",
            style: "paymentMethodStyle",
          },
          {
            ul: [
              { text: "BANCO: BCI", fontSize: 9 },
              { text: "TITULAR: " + this.company[0].name, fontSize: 9 },
              { text: this.company[0].bankAccount2, fontSize: 9 },
              { text: this.company[0].nib2, fontSize: 9 },
            ],
          },
          {
            text: "\n",
          },
          {
            text: "Termos comerciais da proposta:\n",
            style: "paymentMethodStyle",
          },
          {
            ul: [
              {
                text: this.termsAndConditions,
                fontSize: 9,
                italics: "Courier-Oblique",
              },
              {
                text: "A empresa reserva-se o direito de efectuar o débito de juros caso este documento não seja liquidado até ao seu vencimento.",
                fontSize: 9,
                alignment: "left",
                italics: "Courier-Oblique",
              },
            ],
          },
        ],
        styles: {
          paymentMethodStyle: {
            bold: true,
            fontSize: 10,
          },
          sectionHeader: {
            bold: true,
            fontSize: 10,
            margin: [0, 15, 0, 15],
          },
          columnsTitle: {
            fontSize: 10,
            bold: true,
            color: "#000000",
          },
          columnsSubTotals: {
            fontSize: 10,
            bold: true,
            color: "#000000",
          },
          columnsTotal: {
            fontSize: 12,
            bold: true,
            color: "#000000",
          },
          companyDetailsStyle: {
            fontSize: 9,
            bold: true,
            color: "#000000",
          },
          customerStyle: {
            fontSize: 10,
            bold: false,
          },
        },
      };

      // pdfMake.createPdf(docDefinition).download(`proforma${this.currentQuotation.name}.pdf`);
      pdfMake.createPdf(docDefinition).open();
      this.products = [];
      this.$emit("update-quotation");
    },

    getBase64ImageFromURL(url) {
      return new Promise((resolve, reject) => {
        let img = new Image();
        img.setAttribute("crossOrigin", "anonymous");

        img.onload = () => {
          let canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;

          let ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);

          let dataURL = canvas.toDataURL("image/png");

          resolve(dataURL);
        };

        img.onerror = (error) => {
          reject(error);
        };

        img.src = url;
      });
    },

    async insertProforma() {
      if (this.customerName == "" || this.customerAddress == "") {
        this.$bvToast.toast(
          "Digite o nome do cliente e um endereço válido por favor!",
          {
            title: "Nome | Endereço",
            variant: "danger",
            solid: true,
          }
        );
        return;
      }
      this.isLoading = true;

      const passingValues = {
        invoiceId: this.invoiceId,
        preRegistrationId: this.currentQuotation.id,
        customerName: this.customerName,
        customerAddress: this.customerAddress,
        customerPhoneNumber: this.customerPhoneNumber,
        customerNuit: this.customerNuit,
        customerEmail: this.currentQuotation.customerEmail,
        staffName: this.user[0].name,
        itemsDetails: JSON.stringify(this.products),
      };

      try {
        const proforma = await ProformaService.insertProforma(
          passingValues,
          this.token
        );
        if (proforma.success == true) {
          this.$bvToast.toast(proforma.message, {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
          this.$refs["quotation"].hide();
          this.$emit("update-quotation");
          this.saveProforma();
        } else {
          this.$bvToast.toast(proforma.message, {
            title: "Erro!",
            variant: "danger",
            solid: true,
          });
          this.$refs["quotation"].hide();
        }
      } catch (error) {
        this.$bvToast.toast(error.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.$refs["quotation"].hide();
      }
      this.isLoading = false;
    },
  },
});
</script>
