import axios from 'axios'
const url = 'api/search_by_id_range'

/* eslint-disable no-async-promise-executor */

class SearchCustomerBySearialNumber {
    static searchCustomers(search, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}/${search}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static searchCustomersByLatLng(searchValues, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, searchValues, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default SearchCustomerBySearialNumber