<template>
  <b-col lg="4" md="12" sm="12" class="mb-1" v-if="tarrifsById">
    <b-card class="mb-1">
      <b-overlay :show="show" rounded="sm">
        <div v-if="tarrifs.id === 1 || tarrifs.id === 3">
          <table class="table table-sm bg-white">
            <thead>
              <tr class="bg-secondary text-white" style="font-size: 10pt">
                <th colspan="2">{{ tarrifs.name }}</th>
              </tr>
              <tr v-if="!isLoading" style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>
                    {{ $t("price_per_m_3") }}
                    <sup>3</sup>
                  </small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].pricePerMeterCubic" />
                </th>
              </tr>
            </thead>
          </table>
        </div>

        <div v-if="tarrifs.id === 2 || tarrifs.id === 3">
          <table class="table table-sm bg-white mt-2">
            <thead v-if="!isLoading">
              <tr class="bg-secondary text-white" style="font-size: 10pt">
                <th colspan="2">{{ tarrifs.name }}</th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>{{ $t("fixed_tax") }}</small>
                </th>
                <th class="text-right">
                  <small
                    v-money-format="tarrifsById[0].serviceAvailabilityFee"
                  />
                </th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>
                    {{ $t("minimum_consuption") }} m
                    <sup>3</sup>
                  </small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].socialTire" />
                </th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>{{ $t("first_five") }}</small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].firstFive" />
                </th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>{{ $t("first_seven") }}</small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].firstSeven" />
                </th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>{{ $t("above_seven") }}</small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].aboveSeven" />
                </th>
              </tr>
            </thead>
          </table>
        </div>

        <div v-if="tarrifs.id >= 4">
          <table class="table table-sm bg-white mt-2">
            <thead v-if="!isLoading">
              <tr class="bg-secondary text-white" style="font-size: 10pt">
                <th colspan="2">{{ tarrifs.name }}</th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>
                    {{ $t("first_fifteen") }}
                    m<sup>3</sup>
                  </small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].firstFifteen" />
                </th>
              </tr>
              <tr style="cursor: pointer; font-size: 10pt">
                <th width="60%">
                  <small>
                    {{ $t("above_fifteen") }}
                    m<sup>3</sup>
                  </small>
                </th>
                <th class="text-right">
                  <small v-money-format="tarrifsById[0].aboveFifteen" />
                </th>
              </tr>
            </thead>
          </table>
        </div>
      </b-overlay>
    </b-card>
  </b-col>
</template>

<script>
import TarrifService from "../http-service/TarrifService";

import { mapGetters } from "vuex";

export default {
  name: "TarrifCard",

  props: ["tarrifs"],

  data() {
    return {
      tarrifsById: [],
      isLoading: false,
      show: false,
    };
  },

  computed: {
    ...mapGetters(["user", "token"]),
  },

  created() {
    this.gettarrifByCategory();
  },

  methods: {
    async gettarrifByCategory() {
      this.isLoading = true;
      try {
        const tarrifsById = await TarrifService.getTarrifsByCategory(
          this.tarrifs.id,
          this.token
        );
        this.tarrifsById = tarrifsById.result;
        this.isLoading = false;
        // console.log(tarrifsById);
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Sucesso",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },
  },
};
</script>

