import axios from 'axios'
const url = 'api/prepayVending'

/* eslint-disable no-async-promise-executor */

class PrepayVendingService {
    // Get User
    static getAllCustomers(params, token) {
        const queryUrl = `${url}`
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(queryUrl, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static getPurchases(barcode, token) {
        const queryUrl = `${url}/${barcode}`
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(queryUrl, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    static previewPurchase(purchase, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, purchase, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default PrepayVendingService