import axios from 'axios'
const url = 'api/branch'

/* eslint-disable no-async-promise-executor */

class Branches {
    // Get User
    static getBranch(searchValue, token) {

        const authString = `Bearer ` + token

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, searchValue, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static getAllBranches(params, token) {
        const allBranchesUrl = `${url}/${params.companyId}/${params.branchId}/${params.userRole}`
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${allBranchesUrl}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Get Users By Company
    static deleteBranch(id, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.delete(`${url}/${id}`, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }

    // Create or Update Branch
    static insertOrUpdateBranch(branch, token) {
        const authString = `Bearer ` + token
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, branch, { headers: { Authorization: authString } })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    }
}

export default Branches