import axios from "axios";
const url = "api/whatsapp";

/* eslint-disable no-async-promise-executor */

class WhatsAppCloudApi {
  static sendSms(smsBody, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, smsBody, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static sendWhatsAppMessage(whatsAppMessage, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, whatsAppMessage, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default WhatsAppCloudApi;
