          style="border-radius: 40%"
          width="40"
        />&nbsp;&nbsp;<span v-if="company[0]">{{ company[0].name }}</span>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav style="font-size: 10pt">
          <b-nav-item to="/branch"
            ><b-icon icon="people-fill"></b-icon>
            {{ $t("zones").toUpperCase() }}</b-nav-item
          >
          <b-nav-item to="/settings"
            ><b-icon icon="tools"></b-icon>
            {{ $t("settings").toUpperCase() }}</b-nav-item
          >
          <b-nav-item to="/relatorios"
            ><b-icon icon="bar-chart-steps"></b-icon>
