<script>
import { mapGetters } from "vuex";
import i18n from "@/plugins/i18n";

export default {
  name: "Navbar",

  computed: {
    ...mapGetters([
      "user",
      "branches",
      "token",
      "currentUser",
      "company",
      "proformas",
    ]),
  },

  created() {
  },

  data() {
    return {
      languages: [
        { flag: "mz", language: "pt", title: "PT" },
        { flag: "gb", language: "en", title: "EN" },
      ],
    };
  },

  methods: {
    gotToCustomersPanel() {
      this.$store.commit("SET_CURRENT_BRANCH", this.branches[0]);
      this.$router.push("/branch");
    },

    changeLocale(lang) {
      i18n.locale = lang;
    },

    logout() {
      localStorage.removeItem("MatiAppTokenJwt");
      this.$router.replace("/");
    },
  },
};
</script>
