<template>
  <div class="quotation">
    <b-overlay :show="isLoading" rounded="sm">
      <b-row>
        <b-col lg="4">
          <small><b>Nome do cliente</b></small>
          <b-form-group label-for="name">
            <b-form-input
              id="name"
              type="text"
              disabled
              v-model="customerName"
              required
              size="sm"
              placeholder="Nome do cliente"
            ></b-form-input>
          </b-form-group>
          <small><b>Endereço</b></small>
          <b-form-group label-for="address">
            <b-form-input
              id="address"
              type="text"
              disabled
              v-model="customerAddress"
              required
              size="sm"
              placeholder="Endereço"
            ></b-form-input>
          </b-form-group>
          <small><b>Telemóvel</b></small>
          <b-form-group label-for="phone">
            <b-form-input
              id="phone"
              type="number"
              disabled
              v-model="customerPhoneNumber"
              required
              size="sm"
              placeholder="Telemóvel"
            ></b-form-input>
          </b-form-group>
          <small><b>NUIT</b></small>
          <b-form-group label-for="nuit">
            <b-form-input
              id="nuit"
              type="number"
              disabled
              v-model="customerNuit"
              required
              size="sm"
              placeholder="NUIT"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <!-- RIGHT DIV -->
        <b-col>
          <b-row>
            <b-col><b>Dados da factura</b></b-col>
            <b-col class="text-right"
              ><b>Data de emissão: {{ dateOfQuote }}</b></b-col
            >
            <b-col cols="2">
              <b-button
                @click="saveProforma()"
                :disabled="isLoading || itemsList.length <= 0"
                block
                size="sm"
                class="mb-2"
                variant="secondary"
                ><span>Baixar </span><b-icon icon="file-pdf-fill"></b-icon>
              </b-button>
            </b-col>
          </b-row>

          <table class="table table-sm table-hover">
            <thead>
              <tr class="" style="font-size: 9pt">
                <th class="text-center">Ordem</th>
                <th colspan="2">Descrição do Produto</th>
                <th>Preço Unitário</th>
                <th>Quantidade</th>
                <th class="text-right">Subtotal</th>
              </tr>
              <tr
                v-for="(item, index) in itemsList"
                :key="index"
                style="font-size: 9pt"
              >
                <td class="text-center">
                  {{ index + 1 }}
                </td>
                <td colspan="2">{{ item.item }}</td>
                <td v-money-format="item.price"></td>
                <td>{{ item.quantity }}</td>
                <td
                  class="text-right"
                  v-money-format="item.price * item.quantity"
                ></td>
              </tr>
            </thead>

            <tbody>
              <tr>
                <td colspan="6"><br /><br /></td>
              </tr>
              <tr style="font-size: 10pt">
                <th colspan="3" class="">Subtotal</th>
                <th
                  colspan="3"
                  class="text-right"
                  v-money-format="calculateSubTotal()"
                ></th>
              </tr>
              <tr style="font-size: 10pt">
                <th colspan="3" class="">IVA - {{ vat * 100 }}%</th>
                <th
                  colspan="3"
                  class="text-right"
                  v-money-format="vat * calculateSubTotal()"
                ></th>
              </tr>
              <tr style="font-size: 10pt">
                <th colspan="3" class="">TOTAL A PAGAR</th>
                <th
                  colspan="3"
                  class="text-right"
                  v-money-format="
                    calculateSubTotal() + calculateSubTotal() * vat
                  "
                ></th>
              </tr>
              <tr style="font-size: 10pt" class="text-danger">
                <th colspan="3" class="">DÍVIDA</th>
                <th
                  colspan="3"
                  class="text-right"
                  v-money-format="saldoDevedor"
                ></th>
              </tr>
            </tbody>
          </table>
        </b-col>
      </b-row>
      <hr class="bg-dark mt-0 mb-1" />
      <!-- PROFORMA PAYMENT SECTION -->
      <h6 class="text-dark" style="font-size: 10pt">
        <b>Adicionar pagamento</b>
      </h6>
      <b-row>
        <b-col>
          <b-form-select
            :disabled="currentProforma.status == 1"
            v-model="paymentMethod"
            :options="options"
            size="sm"
          ></b-form-select
        ></b-col>
        <b-col>
          <b-form-group label-for="amountPaid">
            <b-form-input
              id="amountPaid"
              type="number"
              step="any"
              :disabled="currentProforma.status == 1"
              v-model="amount2Paid"
              required
              min="10"
              size="sm"
              placeholder="Valor a pagar"
            ></b-form-input> </b-form-group
        ></b-col>
        <b-col>
          <b-form-group label-for="reference">
            <b-form-input
              id="paymentReference"
              type="text"
              :disabled="currentProforma.status == 1"
              v-model="paymentReference"
              required
              size="sm"
              placeholder="Referência de pagamento"
            ></b-form-input> </b-form-group
        ></b-col>
        <b-col class="text-right"
          ><b-button
            @click="addPayment()"
            :disabled="currentProforma.status == 1"
            pill
            variant="success"
            block
            size="sm"
            ><span>Submeter pagamento </span
            ><b-icon variant="light" icon="telegram"></b-icon></b-button
        ></b-col>
      </b-row>
      <h6 class="text-dark" style="font-size: 10pt">
        <b>Pagamentos relacionados</b>
      </h6>
      <table
        class="table table-sm table-hover table-stripped"
        v-if="currentProformaTransactions.length > 0"
      >
        <thead>
          <tr style="font-size: 9pt" class="bg-secondary text-light">
            <th>Data de pagamento</th>
            <th>Referência</th>
            <th>Método de pagamento</th>
            <th>Telemóvel</th>
            <th>Valor pago</th>
            <th>Técnico</th>
          </tr>
          <tr
            style="font-size: 9pt"
            v-for="transaction in currentProformaTransactions"
            :key="transaction.id"
          >
            <td v-date-format="transaction.createdAt"></td>
            <td>{{ transaction.paymentReference }}</td>
            <td>{{ getPaymentMethod(transaction.paymentMethod) }}</td>
            <td>{{ transaction.phoneNumber }}</td>
            <td v-money-format="transaction.amountPaid"></td>
            <td>{{ transaction.staffId }}</td>
          </tr>
        </thead>
      </table>
      <p class="text-left" style="font-size: 9pt">
        <b>Técnico:</b> <em>{{ currentProforma.staffName }}</em>
      </p>
    </b-overlay>
  </div>
</template>

<script>
import Vue from "vue";
import { mapGetters } from "vuex";

import ProformaPaymentService from "../../http-service/ProformaPaymentService ";
import ProformaService from "../../http-service/ProformaService";

import moment from "moment";
moment.locale("pt-br");

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

export default Vue.extend({
  name: "QuotationPreview",

  props: ["currentProforma"],

  data() {
    return {
      isLoading: false,

      itemsList: [],
      productId: 0,
      item: "",
      productName: 0,
      price: 0,
      quantity: 1,
      total: 0,
      vat: 0,
      subTotalVat: 0,
      dateOfQuote: "",

      // PROFORMA CUSTOMER DETAILS
      customerName: "",
      customerPhoneNumber: "",
      customerAddress: "",
      customerNuit: "",
      termsAndConditions: `Todos os materiais e equipamentos fornecidos pela TSENANE, LDA observam os melhores padrões de qualidade, tem garantia de 12 meses contra defeito de fabrico e se a observarem as  recomendações  de Instalação e do transporte.`,

      options: [
        { value: null, text: "Método de pagamento" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "MPESA Open API" },
        { value: 8, text: "E-Mola" },
      ],
      paymentMethod: null,
      amount2Paid: 0,
      paymentReference: "",
      currentProformaTransactions: [],
      paymentCleared: false,
      saldoDevedor: 0,
    };
  },

  created() {
    this.populateDataInFields();
    this.itemsList = JSON.parse(this.currentProforma.itemsDetails);
    this.processInvoiceYear();
    this.dateOfQuote = moment(this.currentProforma.createdAt).format(
      "DD-MM-YYYY"
    );
  },

  watch: {
    amount2Paid: function () {
      if (this.amount2Paid.length == 0) return
      this.saldoDevedorOperations();
    },
  },

  computed: {
    ...mapGetters(["user", "token", "currentUser", "company"]),
  },

  methods: {
    async getProformaTransactions() {
      this.isLoading = true;

      try {
        const transactions = await ProformaPaymentService.getPaymentsById(
          this.currentProforma.invoiceId,
          this.token
        );
        if (transactions.success === true) {
          this.currentProformaTransactions = transactions.result;
          this.saldoDevedorOperations();
        } else {
          this.currentProformaTransactions = [];
        }
        this.isLoading = false;
      } catch (err) {
        console.log(err);
        this.currentProformaTransactions = [];
        this.isLoading = false;
      }
    },

    populateDataInFields() {
      this.customerName = this.currentProforma.customerName;
      this.customerPhoneNumber = this.currentProforma.customerPhoneNumber;
      this.customerAddress = this.currentProforma.customerAddress;
      this.customerNuit = this.currentProforma.customerNuit;
      this.getProformaTransactions();
    },

    processInvoiceYear() {
      const year = moment(this.currentProforma.createdAt).year();
      this.vat = year <= 2022 ? 0.17 : 0.16;
    },

    calculateSubTotal() {
      const subtotal = this.itemsList.reduce(
        (sum, p) => sum + p.quantity * p.price,
        0
      );
      this.subTotalVat = subtotal * this.vat;
      return subtotal;
    },

    saldoDevedorOperations() {
      const amountPaidTotal = this.currentProformaTransactions.reduce(
        (sum, p) => sum + p.amountPaid,
        0
      );

      const totalProforma =
        this.calculateSubTotal() * this.vat + this.calculateSubTotal();

      this.saldoDevedor =
        totalProforma - amountPaidTotal - parseFloat(this.amount2Paid);
      this.paymentCleared = parseInt(this.saldoDevedor) <= 0 ? true : false;

      // console.log(this.saldoDevedor, this.paymentCleared);
    },

    getPaymentMethod(method) {
      if (method == 1) {
        return "Boca do Caixa";
      } else if (method == 2) {
        return "Depósito Bancário";
      } else if (method == 3) {
        return "Transferência Bancária";
      } else if (method == 4) {
        return "POS";
      } else if (method == 5) {
        return "Cheque";
      } else if (method == 6) {
        return "SISTAFE";
      } else if (method == 7) {
        return "M-Pesa Open API";
      } else if (method == 8) {
        return "E-Mola";
      }
    },

    async updateProformaStatus() {
      const passingValues = {
        status: this.paymentCleared === true ? 1 : -1,
        invoiceId: this.currentProforma.invoiceId,
      };
      this.isLoading = true;

      try {
        const updateStatus = await ProformaPaymentService.updatePaymentStatus(
          passingValues,
          this.token
        );
        if (updateStatus.success === true) {
          this.getAllProforma();
          this.getProformaTransactions();
        } else {
          console.log(updateStatus.message);
          this.isLoading = false;
        }
      } catch (err) {
        console.log(err.message);
        this.isLoading = false;
      }
    },

    async getAllProforma() {
      this.isLoading = true;
      try {
        const proforma = await ProformaService.getAllProforma(this.token);
        if (proforma.success === true) {
          this.$store.commit("SET_PROFORMAS", proforma.result);
          this.isLoading = false;
        } else {
          this.$bvToast.toast(proforma.message, {
            title: "Error!",
            variant: "info",
            solid: true,
          });
          this.isLoading = false;
          this.$store.commit("SET_PROFORMAS", []);
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.$store.commit("SET_PROFORMAS", []);
        this.isLoading = false;
      }
    },

    async addPayment() {
      if (this.paymentMethod == null) {
        this.$bvToast.toast("Seleccione o modo de pagamento por favor!", {
          title: "Aviso!",
          variant: "warning",
          solid: true,
        });
        return;
      }

      if (this.amount2Paid < 10) {
        this.$bvToast.toast("Digite um montante válido", {
          title: "Aviso!",
          variant: "warning",
          solid: true,
        });
        return;
      }

      if (this.paymentReference.length <= 4) {
        this.$bvToast.toast("Digite uma referência válida.", {
          title: "Aviso!",
          variant: "warning",
          solid: true,
        });
        return;
      }
      this.isLoading = true;
      const passingValues = {
        invoiceId: this.currentProforma.invoiceId,
        paymentMethod: this.paymentMethod,
        paymentReference: this.paymentReference,
        phoneNumber: "N/A",
        amountPaid: this.amount2Paid,
        staffId: this.user[0].name,
      };

      try {
        const insertPayment = await ProformaPaymentService.insertPayment(
          passingValues,
          this.token
        );
        if (insertPayment.success === true) {
          this.$bvToast.toast(insertPayment.message, {
            title: "Sucesso!",
            variant: "success",
            solid: true,
          });
          this.updateProformaStatus();
        } else {
          this.$bvToast.toast(insertPayment.message, {
            title: "Erro!",
            variant: "warning",
            solid: true,
          });
        }
        this.isLoading = false;
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },

    processInvoiceId(proforma) {
      const year = moment(proforma.createdAt).year();
      return `${proforma.invoiceId}/SAAF/${year}`;
    },

    async saveProforma() {
      const body = [];

      for (let row of this.itemsList) {
        const rows = [];

        // rows.push({ text: row.id, fontSize: 8 })
        rows.push({ text: row.item, fontSize: 9 });
        rows.push({
          text: parseFloat(row.price).toFixed(2),
          fontSize: 9,
          alignment: "right",
        });
        rows.push({ text: row.quantity, fontSize: 9, alignment: "right" });
        rows.push({
          text: row.quantity * row.price,
          fontSize: 9,
          alignment: "right",
        });

        body.push(rows);
      }

      let docDefinition = {
        // watermark: { text: this.company[0].name, opacity: 0.08 },
        header: [
          {
            image: await this.getBase64ImageFromURL(
              "https://saaf-tsenane.herokuapp.com/img/logo_12.2b7a27d2.jpeg"
            ),
            width: 300,
            height: 200,
            margin: [30, 0, 0, 0],
          },
        ],
        footer: {
          columns: [
            {
              text: "Documento processado por computador",
              alignment: "left",
              fontSize: 8,
              margin: [30, 0, 0, 0],
            },
            {
              text: "TSENANE Software",
              alignment: "right",
              fontSize: 8,
              margin: [0, 0, 30, 0],
            },
          ],
        },

        content: [
          {
            columns: [
              [
                {
                  text:
                    "Factura Proforma Nº: " +
                    this.processInvoiceId(this.currentProforma),
                  fontSize: 10,
                  bold: true,
                  alignment: "right",
                },
                {
                  text: `\nData de emissão: ${moment(
                    this.currentProforma.createdAt
                  ).format("DD-MM-YYYY")}`,
                  fontSize: 8,
                  alignment: "right",
                },
                {
                  text: `Validade da proposta: 14 dias`,
                  fontSize: 8,
                  alignment: "right",
                },
              ],
            ],
          },
          {
            text: "\n\n\n\n\n\n\n\n\n\n",
          },
          {
            text: `Exmo(s) Sr(s): ${this.customerName}`,
            bold: true,
            fontSize: 12,
          },
          {
            text: `Endereço: ${this.customerAddress}`,
            style: "customerStyle",
          },
          {
            text: `Telefone: ${this.customerPhoneNumber}`,
            style: "customerStyle",
          },
          {
            text: `NUIT: ${this.customerNuit}`,
            style: "customerStyle",
          },
          {
            text: "\n\n",
          },
          {
            table: {
              widths: ["*", "auto", "auto", "auto"],
              body: [
                [
                  { text: "Descrição do produto", style: "columnsTitle" },
                  {
                    text: "Preço unitário",
                    style: "columnsTitle",
                    alignment: "center",
                  },
                  { text: "Qtd", style: "columnsTitle", alignment: "center" },
                  {
                    text: "Subtotal",
                    style: "columnsTitle",
                    alignment: "right",
                  },
                ],
                ...body,
                [{ text: "\n\n\n\n\n\n\n", colSpan: 4 }, {}, {}, {}],
                [
                  { text: "Subtotal", colSpan: 3, style: "columnsSubTotals" },
                  {},
                  {},
                  {
                    text: this.calculateSubTotal().toFixed(2),
                    style: "columnsSubTotals",
                    alignment: "right",
                  },
                ],
                [
                  {
                    text: "IVA - " + this.vat * 100 + "%",
                    colSpan: 3,
                    style: "columnsSubTotals",
                  },
                  {},
                  {},
                  {
                    text: (this.calculateSubTotal() * this.vat).toFixed(2),
                    style: "columnsSubTotals",
                    alignment: "right",
                  },
                ],
                [
                  { text: "Total a pagar", colSpan: 3, style: "columnsTotal" },
                  {},
                  {},
                  {
                    text:
                      (
                        this.calculateSubTotal() * this.vat +
                        this.calculateSubTotal()
                      ).toFixed(2) + " MZN",
                    style: "columnsTotal",
                    alignment: "right",
                  },
                ],
              ],
            },
            layout: "lightHorizontalLines",
          },
          {
            text: "\n",
          },
          {
            text: "Detalhes sobre meio de pagamentos:\n",
            style: "paymentMethodStyle",
          },
          {
            ul: [
              { text: "BANCO: BCI", fontSize: 9 },
              { text: "TITULAR: " + this.company[0].name, fontSize: 9 },
              { text: this.company[0].bankAccount2, fontSize: 9 },
              { text: this.company[0].nib2, fontSize: 9 },
            ],
          },
          {
            text: "\n",
          },
          {
            text: "Termos comerciais da proposta:\n",
            style: "paymentMethodStyle",
          },
          {
            ul: [
              {
                text: this.termsAndConditions,
                fontSize: 9,
                italics: "Courier-Oblique",
              },
              {
                text: "A empresa reserva-se o direito de efectuar o débito de juros caso este documento não seja liquidado até ao seu vencimento.",
                fontSize: 9,
                alignment: "left",
                italics: "Courier-Oblique",
              },
            ],
          },
        ],
        styles: {
          paymentMethodStyle: {
            bold: true,
            fontSize: 10,
          },
          sectionHeader: {
            bold: true,
            fontSize: 10,
            margin: [0, 15, 0, 15],
          },
          columnsTitle: {
            fontSize: 10,
            bold: true,
            color: "#000000",
          },
          columnsSubTotals: {
            fontSize: 10,
            bold: true,
            color: "#000000",
          },
          columnsTotal: {
            fontSize: 12,
            bold: true,
            color: "#000000",
          },
          companyDetailsStyle: {
            fontSize: 9,
            bold: true,
            color: "#000000",
          },
          customerStyle: {
            fontSize: 10,
            bold: false,
          },
        },
      };

      // pdfMake.createPdf(docDefinition).download(`proforma${this.currentQuotation.name}.pdf`);
      pdfMake.createPdf(docDefinition).open();
    },

    getBase64ImageFromURL(url) {
      return new Promise((resolve, reject) => {
        let img = new Image();
        img.setAttribute("crossOrigin", "anonymous");

        img.onload = () => {
          let canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;

          let ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);

          let dataURL = canvas.toDataURL("image/png");

          resolve(dataURL);
        };

        img.onerror = (error) => {
          reject(error);
        };

        img.src = url;
      });
    },
  },
});
</script>
