<template>
  <div>
    <b-row>
      <b-col lg="10">
        <h5 class="text-white">{{ $t("staff_management") }}</h5>
      </b-col>
      <b-col>
        <b-button
          v-b-modal.modal-center
          variant="success"
          block
          @click="openModalUser()"
          size="sm"
          >{{ $t("new_staff") }}
          &nbsp;&nbsp;&nbsp;&nbsp;
          <b-icon icon="person-plus-fill"></b-icon
        ></b-button>
      </b-col>
    </b-row>
    <hr class="bg-info mt-2" />
    <b-row>
      <b-col>
        <table class="table table-sm table-stripped bg-white mt-1">
          <thead>
            <tr class="primary">
              <th>
                <small>
                  <b>ID</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("name") }}</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("email") }}</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("code") }}</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("phone") }}</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("neighbor") }}</b>
                </small>
              </th>
              <th>
                <small>
                  <b>{{ $t("user_role") }}</b>
                </small>
              </th>
              <th class="text-center">
                <small>
                  <b>{{ $t("actions") }}</b>
                </small>
              </th>
            </tr>
          </thead>
          <thead>
            <tr v-for="user in companyStaff" :key="user.id">
              <td>
                <small>{{ user.id }}</small>
              </td>
              <td>
                <small>{{ user.name }}</small>
              </td>
              <td>
                <small>{{ user.email }}</small>
              </td>
              <td>
                <small>{{ user.code }}</small>
              </td>
              <td>
                <small>{{ user.phoneNumber }}</small>
              </td>
              <td>
                <small>{{ user.branchId }}</small>
              </td>
              <td>
                <small>{{
                  user.userRole == 1 ? $t("admin") : $t("normal")
                }}</small>
              </td>
              <td class="text-center">
                <b-button
                  v-b-modal.modal-center
                  variant="secondary"
                  @click="editUser(user)"
                  size="sm"
                >
                  <small>{{ $t("edit") }}</small> </b-button
                >&nbsp;&nbsp;&nbsp;&nbsp;
                <b-button
                  disabled
                  v-b-modal.modal-center
                  variant="warning"
                  @click="deactivateUser(user)"
                  size="sm"
                >
                  <small>{{
                    user.status == 1 ? $t("active") : $t("inactive")
                  }}</small>
                </b-button>
              </td>
            </tr>
          </thead>
        </table>
      </b-col>
    </b-row>
    <b-modal hide-footer ref="add-new-user" :title="$t('new_staff')">
      <small>{{ $t("select") + " " + $t("neighbor") }}</small>
      <b-form-select size="sm" v-model="selectedSubBranch" class="mb-3">
        <b-form-select-option
          v-for="branch in branches"
          :key="branch.id"
          :value="branch.id"
          >{{ branch.name }}</b-form-select-option
        >
      </b-form-select>

      <small>{{ $t("staff_name") }}</small>
      <b-form-group id="userName" label-for="userName">
        <b-form-input
          id="userName"
          type="text"
          v-model="userName"
          required
          size="sm"
          :placeholder="$t('staff_name')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("phone") }}</small>
      <b-form-group id="userPhone" label-for="userPhone">
        <b-form-input
          id="userPhone"
          type="number"
          v-model="userPhoneNumber"
          required
          size="sm"
          :placeholder="$t('phone')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("email") }}</small>
      <b-form-group id="email" label-for="email">
        <b-form-input
          id="email"
          type="email"
          v-model="emailId"
          required
          size="sm"
          :placeholder="$t('email')"
        ></b-form-input>
      </b-form-group>

      <small>{{ $t("auth_code") }}</small>
      <b-form-group id="userCode" label-for="userCode">
        <b-form-input
          id="userCode"
          type="number"
          size="sm"
          disabled
          v-model="code"
        ></b-form-input>
      </b-form-group>

      <div>
        <small>{{ $t("user_role") }}</small>
        <b-form-select size="sm" v-model="userRole" class="mb-3">
          <b-form-select-option value="1">{{
            $t("admin")
          }}</b-form-select-option>
          <b-form-select-option value="2">{{
            $t("cashier")
          }}</b-form-select-option>
          <b-form-select-option value="3">{{
            $t("help_desk")
          }}</b-form-select-option>
        </b-form-select>
      </div>

      <b-button
        class="mt-3 sm"
        :disabled="
          selectedSubBranch <= 0 ||
          userName.length <= 0 ||
          userPhoneNumber.length < 9 ||
          isLoading == true
        "
        block
        @click="addNewUser()"
        size="sm"
      >
        <template v-if="isLoading">
          {{ $t("submiting") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("submit") }}</template>
      </b-button>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import UserService from "../http-service/UserService";

export default {
  name: "Colaborator",

  computed: {
    ...mapGetters([
      "user",
      "companyStaff",
      "provinces",
      "districts",
      "branches",
      "currentUser",
      "token",
    ]),
  },

  created() {},

  data() {
    return {
      userName: "",
      userUid: "",
      userPhoneNumber: "",
      emailId: "",
      userRole: 2,
      userStatus: 1,
      selectedSubBranch: "",
      code: Math.floor(Math.random() * 1000000) + 1,
      isLoading: false,
      isEditing: false,

      currentDeletion: "",
    };
  },

  methods: {
    editUser(user) {
      this.openModalUser();
      this.isEditing = true;
      this.userName = user.name;
      this.code = user.code;
      this.userRole = user.userRole;
      this.userPhoneNumber = user.phoneNumber;
      this.emailId = user.email;
      this.selectedSubBranch = user.branchId;
    },

    openModalUser() {
      this.$refs["add-new-user"].show();
    },

    async addNewUser() {
      const passingValues = {
        name: this.userName,
        phoneNumber: this.userPhoneNumber,
        email: this.emailId,
        userRole: this.userRole,
        status: this.userStatus,
        branchId: this.selectedSubBranch,
        companyId: this.currentUser[0].companyId,
        code: this.code,
        password: 123456,
      };

      this.isLoading = true;

      try {
        const user = await UserService.insertUser(passingValues, this.token);
        this.$bvToast.toast(this.userName + " foi cadastrado com sucesso.", {
          title: "Sucesso",
          variant: "success",
          solid: true,
        });
        this.$refs["add-new-user"].hide();
        this.clearFields();
        this.isLoading = false;
        this.$emit("update-users");
        console.log(user);
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
        this.$emit("getMembers");
      }
    },

    clearFields() {
      this.userName = "";
      this.phoneNumber = "";
      this.userRole = "";
      this.userPhoneNumber = "";
      this.selectedSubBranch = "";
      this.emailId = "";
      this.code = Math.floor(Math.random() * 1000000) + 1;
    },
  },
};
</script>
