import Vue from "vue";
import VueRouter from "vue-router";
import store from "@/store";

// import Todo from '../views/Todo.vue'

import Login from "../views/Login.vue";
import Dashboard from "../views/Dashboard.vue";
import Settings from "../views/Settings.vue";
import CustomerView from "../views/CustomerView.vue";
import Cashier from "../views/Cashier.vue";
import Support from "../views/Support.vue";
import PreRegistration from "../views/PreRegistration.vue";
import Simulator from "../views/Simulator.vue";

import Profile from "../views/Profile.vue";
import Payments from "../views/Payments.vue";
import BranchPanel from "../views/BranchPanel.vue";
import BranchPanelNew from "../views/BranchPanelNew.vue";
import Tarrifs from "../views/Tarrifs.vue";
import Products from "../views/Products.vue";
import Relatorios from "../views/Relatorios.vue";
import CashierNew from "../views/CashierNew.vue";
import Proforma from "../views/Proforma.vue";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/simulator",
      name: "Simulator",
      component: Simulator,
    },
    {
      path: "/pre_registration",
      name: "PreRegistration",
      component: PreRegistration,
    },
    {
      path: "/",
      name: "Login",
      component: Login,
    },
    {
      path: "/register",
      name: "Register",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "register" */ "../views/Register.vue"),
    },
    {
      path: "/dashboard",
      name: "Dashboard",
      component: Dashboard,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/relatorios",
      name: "Relatorios",
      component: Relatorios,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/settings",
      name: "Settings",
      component: Settings,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/customerview",
      name: "CustomerView",
      component: CustomerView,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/support",
      name: "Support",
      component: Support,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/cashier",
      name: "Cashier",
      component: Cashier,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/cashierview",
      name: "CashierNew",
      component: CashierNew,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/payments",
      name: "Payments",
      component: Payments,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/profile",
      name: "Profile",
      component: Profile,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/branch",
      name: "BranchPanel",
      component: BranchPanel,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/branchPanel",
      name: "BranchPanelNew",
      component: BranchPanelNew,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/tarrifs",
      name: "Tarrifs",
      component: Tarrifs,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/products",
      name: "Products",
      component: Products,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/proforma",
      name: "Proforma",
      component: Proforma,
      meta: {
        requiresAuth: true,
      },
    },
  ],
});

router.beforeEach((to, _, next) => {
  if (to.matched.some((routerRecord) => routerRecord.meta.requiresAuth)) {
    if (!store.state.user) {
      next({
        path: "/",
        query: { redirect: to.fullPath },
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
