import axios from "axios";
const url = "api/reportNotes";

/* eslint-disable no-async-promise-executor */

class ReportNotesService {
  // Get User
  static getAllService(values, token) {
    const authString = `Bearer ` + token;

    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(
          `${url}?month=${values.month}&year=${values.year}`,
          { headers: { Authorization: authString } }
        );
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static deleteReportNote(billId, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.delete(`${url}/${billId}`, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static insertReportNote(reportNote, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url, reportNote, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  static getDebtsByCategory(tariffId, token) {
    const authString = `Bearer ` + token;
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.put(url, tariffId, {
          headers: { Authorization: authString },
        });
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default ReportNotesService;
