<template>
  <div class="validation-component">
    <b-row>
      <b-col class="d-flex w-100 justify-content-between text-secondary">
        <b
          ><b-icon icon="person-fill" font-scale="1.5"></b-icon>
          {{ currentCustomer.name }}</b
        >
        <b
          ><b-icon icon="speedometer2" font-scale="1.5"></b-icon>
          {{ currentCustomer.barcode }}</b
        >
      </b-col>
      <b-col lg="2" class="text-right text-secondary" style="cursor: pointer"
        ><b-icon icon="whatsapp" variant="success" font-scale="1"></b-icon
      ></b-col>
    </b-row>
    <hr class="bg-dark mb-0 mt-0" />
    <b-row>
      <b-col cols="4">
        <table
          class="table table-sm table-bordered mt-1"
          style="font-size: 10pt"
        >
          <thead>
            <tr class="text-danger">
              <th>TOTAL DA DÍVIDA</th>
              <th class="text-right" v-money-format="saldoDivida"></th>
            </tr>
          </thead>
        </table>

        <small>Valor recebido</small>
        <b-form-group id="value" label-for="value">
          <b-form-input
            id="value"
            type="number"
            v-model="value"
            min="180.40"
            max="10000"
            step="any"
            required
            size="sm"
            placeholder="Montante a pagar"
          ></b-form-input>
        </b-form-group>

        <small>Percentagem da dívida (%)</small>
        <b-form-group id="debtPercentage" label-for="debtPercentage">
          <b-form-input
            id="debtPercentage"
            type="number"
            v-model="debtPercentage"
            min="0"
            max="100"
            required
            size="sm"
            placeholder="Percentagem da dívida"
          ></b-form-input>
        </b-form-group>

        <table class="table table-sm table-bordered" style="font-size: 10pt">
          <thead>
            <tr v-if="debtRefunded > 0">
              <td>Reembolso da dívida:</td>
              <td class="text-right" v-money-format="debtRefunded"></td>
            </tr>
            <tr v-if="debtAfterPurchase > 0">
              <td>Dívida após a compra:</td>
              <td class="text-right" v-money-format="debtAfterPurchase"></td>
            </tr>
            <tr v-if="amountValue > 0">
              <td>Valor de compra de água</td>
              <td class="text-right" v-money-format="amountValue"></td>
            </tr>
          </thead>
        </table>

        <b-button
          class="mt-2 mb-2 sm"
          :disabled="value <= 0 || isLoading == true"
          block
          variant="success"
          @click="previewPurchase()"
          size="sm"
        >
          <template v-if="isLoading">
            {{ $t("submiting") }}
            <b-spinner small type="grow"></b-spinner>
          </template>
          <template v-else>{{ $t("submit") }}</template>
        </b-button>
      </b-col>

      <b-col>
        <h5 class="text-dark mb-1 mt-1">Histórico de compras</h5>
        <b-overlay :show="isLoading" rounded="sm">
          <b-alert variant="danger" show v-if="purchases.length == 0">
            <b-icon
              icon="exclamation-circle-fill"
              variant="warning"
              font-scale="1.5"
            ></b-icon>
            <b class="text-danger"
              >&nbsp;&nbsp;&nbsp;<small
                >Sem nenhum registo de compra para este cliente.</small
              ></b
            >
          </b-alert>

          <table class="table table-sm mt-1" style="font-size: 10pt" v-else>
            <thead>
              <tr class="text-dark" style="font-size: 8pt">
                <th>Recibo</th>
                <th>Montante</th>
                <th>Volume</th>
                <th>Recarga</th>
                <th>Acções</th>
              </tr>
              <tr
                class="text-dark"
                style="font-size: 8pt"
                v-for="bill in purchases"
                :key="bill.id"
              >
                <td>{{ bill.receiptNumber }}</td>
                <td v-money-format="convertMoney(bill.totalPaid)"></td>
                <td>{{ bill.totalUnities }} m <sup>3</sup></td>
                <td>{{ bill.token }}</td>
                <td></td>
              </tr>
            </thead>
          </table>
        </b-overlay>
      </b-col>
    </b-row>

    <b-modal hide-footer ref="show-error-message" :title="messageInfo">
      <p class="text-danger" style="font-size: 12pt" v-if="errorMessage">
        {{ errorMessage }}
      </p>

      <div class="mt-1" v-else>
        <b-row style="font-size: 10pt">
          <b-col>TOTAL PAGO: <b v-money-format="value"></b></b-col>
          <b-col
            >VOLUME:
            <b>{{ purchasePreview.totalUnities }} m<sup>3</sup></b></b-col
          >
        </b-row>
        <hr class="bg-dark mt-1 mb-1" />

        <b-row>
          <b-col
            ><small
              >Pagamento da dívida <b>{{ (debtPercentage / 100) * 100 }}%</b>
            </small>
            <b-form-group label-for="debit_balance">
              <b-form-input
                type="number"
                size="sm"
                step="any"
                v-model="debtRefunded"
                disabled
              ></b-form-input> </b-form-group
          ></b-col>
          <b-col
            ><small>Valor da compra</small>
            <b-form-group label-for="amount_received">
              <b-form-input
                type="number"
                size="sm"
                step="any"
                disabled
                :min="0"
                v-model="amountValue"
              ></b-form-input> </b-form-group
          ></b-col>
        </b-row>
        <b-row>
          <b-col
            ><small>{{ $t("payment_method") }}</small>
            <b-form-select
              v-model="paymentMethod"
              :options="options"
              size="sm"
            ></b-form-select
          ></b-col>
          <b-col
            ><small>{{ $t("payment_reference") }}</small>
            <b-form-group label-for="payment_reference">
              <b-form-input
                type="text"
                size="sm"
                :disabled="paymentMethod == 1 || paymentMethod == 7"
                v-model="paymentReference"
              ></b-form-input> </b-form-group
          ></b-col>
        </b-row>
        <b-row>
          <b-col v-if="paymentMethod == 7">
            <b-form-group label-for="notificationNumber">
              <b-form-input
                type="number"
                size="sm"
                v-model="notificationNumber"
                placeholder="Telemóvel a debitar"
              ></b-form-input> </b-form-group
          ></b-col>
          <b-col
            ><b-button
              @click="makePurchase()"
              :disabled="isLoading"
              block
              size="sm"
              variant="success"
              >{{ $t("pay_now") }}</b-button
            ></b-col
          >
        </b-row>
      </div>
    </b-modal>
  </div>
</template>
<script>
import PrepayVendingService from "../../http-service/PrepayVendingService";
import DebtService from "../../http-service/DebtService";

import { mapGetters } from "vuex";
import moment from "moment";
moment.locale("pt-br");

export default {
  name: "Validation-component",

  components: {},

  data() {
    return {
      messageInfo: "Previsualização da compra",
      isLoading: false,
      filteredBills: [],
      value: 0,
      debtPercentage: 20,
      saldoDivida: 0,
      debtAfterPurchase: 0,
      purchases: [],
      payments: [],

      errorMessage: "",
      purchasePreview: {},

      paymentMethod: "",
      paymentReference: null,
      amountValue: 0,
      debtRefunded: 0,
      notificationNumber: 258,
      options: [
        { value: 1, text: "Boca do Caixa" },
        { value: 2, text: "Depósito Bancário" },
        { value: 3, text: "Transferência Bancária" },
        { value: 4, text: "POS" },
        { value: 5, text: "Cheque" },
        { value: 6, text: "SISTAFE" },
        { value: 7, text: "MPESA Open API" },
        { value: 8, text: "E-Mola" },
      ],
    };
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "company",
      "currentCustomer",
    ]),
  },

  watch: {
    currentCustomer: function () {
      this.getDebts();
      this.getPurchases();
    },

    paymentMethod: function () {
      if (this.paymentMethod == 7) {
        console.log("Hello from mpesa payment");
      }
    },

    value: function () {
      this.processPurchase();
    },

    debtPercentage: function () {
      this.processPurchase();
    },
  },

  created() {
    this.getDebts();
    this.getPurchases();
  },

  methods: {
    async getPurchases() {
      this.isLoading = true;
      try {
        const purchases = await PrepayVendingService.getPurchases(
          this.currentCustomer.barcode,
          this.token
        );
        if (purchases.success == true) {
          this.purchases = purchases.result;
          this.isLoading = false;
        } else {
          this.purchases = [];
          this.isLoading = false;
        }
      } catch (err) {
        console.log(err);
        this.purchases = [];
        this.isLoading = false;
      }
    },

    processPurchase() {
      if (this.saldoDivida > 0) {
        this.debtRefunded = this.value * (this.debtPercentage / 100);
        this.amountValue = this.value - this.debtRefunded;
        this.debtAfterPurchase = this.saldoDivida - this.debtRefunded;
      } else {
        this.debtRefunded = 0;
        this.amountValue = this.value;
        this.debtAfterPurchase = this.saldoDivida - this.debtRefunded;
      }
    },

    async previewPurchase() {
      this.isLoading = true;
      const passingValues = {
        valueOfWater: this.amountValue,
        meter_id: this.currentCustomer.barcode,
        preview: 1,
      };

      try {
        const preview = await PrepayVendingService.previewPurchase(
          passingValues,
          this.token
        );
        if (preview.success == true) {
          // console.log(preview);
          const resultCode = preview.result.result_code;
          if (resultCode == "03") {
            this.messageInfo = "Erro!";
            this.errorMessage = "O valor da compra é menor que o mínimo!";
          } else if (resultCode == "02") {
            this.messageInfo = "Erro de servidor!";
            this.errorMessage = "Houve um erro, volte a tentar mais tarde!";
          } else if (resultCode == "00") {
            this.messageInfo = "Previsualização da compra";
            this.purchasePreview = {
              value: preview.result.result.value,
              totalUnities: preview.result.result.unit,
            };
          }
          this.$refs["show-error-message"].show();
        } else {
          this.$refs["show-error-message"].show();
        }
        this.isLoading = false;
      } catch (err) {
        this.messageInfo = "Erro!";
        console.log(err);
        this.isLoading = false;
      }
    },

    async makePurchase() {
      this.isLoading = true;
      const passingValues = {
        debtRefunded: this.debtRefunded,
        debtAfterPurchase: this.debtAfterPurchase,
        debtBeforePurchase: this.saldoDivida,
        notificationNumber: this.notificationNumber,
        paymentMethod: this.paymentMethod,
        paymentReference: this.paymentReference,
        staffName: this.user[0].name,
        valueOfWater: this.amountValue,
        totalPaid: this.value,
        meter_id: this.currentCustomer.barcode,
        preview: 2,
      };

      try {
        const purchase = await PrepayVendingService.previewPurchase(
          passingValues,
          this.token
        );
        if (purchase.success == true) {
          console.log(purchase);
          this.getPurchases();
          this.$refs["show-receipt-modal"].show();
        } else {
          console.log(purchase);
          this.messageInfo = "Erro!";
          this.errorMessage = purchase.message;
          this.$refs["show-error-message"].show();
        }
        this.isLoading = false;
      } catch (err) {
        this.messageInfo = "Erro!";
        this.errorMessage = err.message;
        this.$refs["show-error-message"].show();
        this.isLoading = false;
      }
    },

    convertMonthToString(consumptionPeriod) {
      return moment(consumptionPeriod).format("MMMM Y").toUpperCase();
    },

    convertMoney(value) {
      return parseFloat(value);
    },

    async getDebts() {
      this.isLoading = true;

      try {
        const debts = await DebtService.getDebtByCustomerId(
          this.currentCustomer.barcode,
          this.token
        );
        if (debts.success == true) {
          this.saldoDivida = debts.totalDues == null ? 0 : debts.totalDues;
          this.payments = debts.payments;
        } else {
          this.saldoDivida = 0;
        }
      } catch (err) {
        console.log(err);
      }
      this.isLoading = false;
    },
  },
};
</script>
