import Vue from "vue";
import Vuex from "vuex";

// import Accounts from '../http-service/Accounts'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: [],
    token: "",
    companyStaff: [],
    company: [],
    currentUser: [],
    branches: [],
    provinces: [],
    districts: [],
    currentBranch: {},
    currentCustomer: {},
    currentBill: [],
    currentTarif: {},
    tariffs: [],
    currentMissingBill: {},
    availableCustomerBalance: 0,
    customerTransaction: [],
    customerReadings: [],
    customerPastDues: [],
    currentBranchUserList: [],
    meters: [],
    notifications: [],
    proformas: [],
  },

  mutations: {
    initializeStore(state) {
      const userToken = localStorage.getItem("MatiAppTokenJwt");
      state.token = userToken != null ? userToken : "";
    },

    updateUser(state, user) {
      state.user = user;
    },

    updateToken(state, token) {
      localStorage.setItem("MatiAppTokenJwt", token);
      state.token = token;
    },

    SET_CURRENT_USER(state, currentUser) {
      state.currentUser = currentUser;
    },

    SET_BRANCHES(state, branches) {
      state.branches = branches;
    },

    SET_COMPANY_STAFF(state, companyStaff) {
      state.companyStaff = companyStaff;
    },

    SET_PROVINCES(state, provinces) {
      state.provinces = provinces;
    },

    SET_DISTRICTS(state, districts) {
      state.districts = districts;
    },

    SET_CURRENT_BRANCH(state, currentBranch) {
      state.currentBranch = currentBranch;
    },

    SET_CURRENT_CUSTOMER(state, currentCustomer) {
      state.currentCustomer = currentCustomer;
    },

    SET_CURRENT_BILL(state, currentBill) {
      state.currentBill = currentBill;
    },

    SET_CURRENT_TARIF(state, currentTarif) {
      state.currentTarif = currentTarif;
    },

    SET_CLEAR_CURRENT_VENDING(state) {
      state.currentTarif = {};
      state.currentBill = [];
      state.currentCustomer = {};
    },

    SET_CLEAR_CURRENT_MISSING_BILL(state, currentMissingBill) {
      state.currentMissingBill = currentMissingBill;
    },

    SET_CUSTOMER_BALANCE(state, availableCustomerBalance) {
      state.availableCustomerBalance = availableCustomerBalance;
    },

    SET_CUSTOMER_TRANSACTIONS(state, customerTransaction) {
      state.customerTransaction = customerTransaction;
    },

    SET_CUSTOMER_READINGS(state, customerReadings) {
      state.customerReadings = customerReadings;
    },

    SET_CUSTOMER_PAST_DUES(state, customerPastDues) {
      state.customerPastDues = customerPastDues;
    },

    SET_CURRENT_BRANCH_USER_LIST(state, currentBranchUserList) {
      state.currentBranchUserList = currentBranchUserList;
    },

    SET_TARIFFS(state, tariffs) {
      state.tariffs = tariffs;
    },

    SET_METERS(state, meters) {
      state.meters = meters;
    },

    SET_COMPANY(state, company) {
      state.company = company;
    },

    SET_NOTIFICATIONS(state, notifications) {
      state.notifications = notifications;
    },

    SET_PROFORMAS(state, proformas) {
      state.proformas = proformas;
    },
  },

  getters: {
    user: (state) => state.user,
    token: (state) => state.token,
    currentUser: (state) => state.currentUser,
    branches: (state) => state.branches,
    companyStaff: (state) => state.companyStaff,
    provinces: (state) => state.provinces,
    districts: (state) => state.districts,
    currentBranch: (state) => state.currentBranch,
    currentCustomer: (state) => state.currentCustomer,
    currentBill: (state) => state.currentBill,
    currentTarif: (state) => state.currentTarif,
    currentMissingBill: (state) => state.currentMissingBill,
    availableCustomerBalance: (state) => state.availableCustomerBalance,
    customerTransaction: (state) => state.customerTransaction,
    customerReadings: (state) => state.customerReadings,
    customerPastDues: (state) => state.customerPastDues,
    currentBranchUserList: (state) => state.currentBranchUserList,
    tariffs: (state) => state.tariffs,
    meters: (state) => state.meters,
    company: (state) => state.company,
    notifications: (state) => state.notifications,
    proformas: (state) => state.proformas,
  },

  actions: {},

  modules: {},
});
