<template>
  <div class="login-body">
    <login-navbar v-on:login-user="opnenLoginModal()" />

    <b-modal hide-footer ref="user-login" :title="$t('authentication')">
        <div class="card">
          <div class="card-body">
            <b-alert
              v-if="message"
              :class="{
                'bg-success': loading == true,
                'bg-danger': loading == false,
              }"
              class="text-center text-white"
              show
              dismissible
              >{{ message }}</b-alert
            >
            <div class="form-group">
              <input
                :disabled="loading"
                v-model="email"
                required
                type="text"
                placeholder="E-mail"
                class="form-control"
              />
            </div>
            <div class="form-group">
              <input
                :disabled="loading"
                v-model="password"
                required
                type="password"
                :placeholder="$t('password')"
                class="form-control"
              />
            </div>
            <b-row>
              <b-col
                ><p
                  class="btn btn-success btn-sm w-100"
                  @click="customerLogin()"
                >
                  <template v-if="loading">
                    {{ $t("processing") }}
                    <b-spinner small type="grow"></b-spinner>
                  </template>
                  <template v-else>{{ $t("customer-login") }}</template>
                </p></b-col
              >
              <b-col
                ><button
                  class="btn btn-secondary btn-sm w-100"
                  :disabled="loading"
                  @click="loginUser()"
                >
                  <template v-if="loading">
                    {{ $t("processing") }}
                    <b-spinner small type="grow"></b-spinner>
                  </template>
                  <template v-else>{{ $t("login") }}</template>
                </button></b-col
              >
            </b-row>
          </div>
        </div>
    </b-modal>
    <b-row class="footer text-secondary">
      <b-col>Copyright ©2021 TSENANE, LDA. All rights reserved.</b-col>
    </b-row>

    <b-modal hide-footer ref="recover-password" :title="$t('forgot-password')">
      <p>{{ $t("registered_email") }}</p>
      <b-form-group id="email" label-for="email">
        <b-form-input
          id="email"
          type="text"
          v-model="email"
          required
          min="1"
          size="sm"
          :placeholder="$t('email')"
        ></b-form-input>
      </b-form-group>
      <b-button
        class="mt-3 sm"
        variant="secondary"
        :disabled="loading"
        block
        @click="recover()"
        size="sm"
      >
        <template v-if="loading">
          {{ $t("submiting") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("send_email_link") }}</template>
      </b-button>
    </b-modal>
  </div>
</template>

<script>
import Vue from "vue";

import { mapGetters } from "vuex";
import LoginNavbar from "../components/LoginNavbar";

import UserLoginService from "../http-service/UserLoginService";
import JwtService from "../http-service/JwtService";
import ProvinceService from "../http-service/ProvinceService";
import UserService from "../http-service/UserService";
import BrancheService from "../http-service/BranchesService";
import CompanyService from "../http-service/CompanyService";
import CustomerService from "../http-service/CustomerService"

export default Vue.extend({
  computed: {
    ...mapGetters([
      "user",
      "token",
      "currentUser",
      "branches",
      "provinces",
      "districts",
      "tariffs",
    ]),
    nextRoute() {
      let panel;
      if (this.user[0].userRole == 1) {
        panel = "/dashboard";
      } else if (this.user[0].userRole == 2) {
        panel = "/manager";
      } else if (this.user[0].userRole == 3) {
        panel = "/cashier";
      }
      return panel;
    },
  },

  components: {
    LoginNavbar,
  },

  created() {
    const userToken = localStorage.getItem("MatiAppTokenJwt");
    if (!userToken) {
      this.checkAuth();
    } else {
      this.fetchCurrentUser();
      this.getProvinces();
    }
  },

  data() {
    return {
      email: "",
      password: "",
      error: null,
      loading: false,
      tariff_type: 1,
      current: 0,
      last: 0,
      message: null,
    };
  },

  watch: {
    user(auth) {
      if (auth) {
        this.$router.replace(this.nextRoute);
      }
    },
  },

  methods: {
    recoverPassword() {
      this.$refs["recover-password"].show();
    },

    opnenLoginModal() {
      this.$refs["user-login"].show();
    },

    async customerLogin() {
      this.loading = true;

      const passingValues = {
        phoneNumber: this.email,
        password: this.password,
      };
      
      try {
        const customerLogin = CustomerService.customerLogin(passingValues);
        // console.log(customerLogin)
        if (customerLogin.success == true) {
           this.message = "Login efectuado com sucesso.";
        } else {
          this.message = customerLogin.message;
          this.loading = false
        }
        
      } catch (err) {
        this.loading = false;
        this.message = err.message;
        this.loading = false;
        this.password = "";
      }

    },

    async loginUser() {
      this.loading = true;
      const passingValues = {
        email: this.email,
        password: this.password,
      };

      try {
        const login = await UserLoginService.userLogin(passingValues);
        this.loading = false;
        this.password = "";
        if (login.success == true) {
          this.checkAuth(login.result);
        } else {
          this.message = login.message;
        }
      } catch (err) {
        this.loading = false;
        this.message = err.message;
        this.loading = false;
        this.password = "";
      }
    },

    async checkAuth(user) {
      this.isLoading = true;
      try {
        let data = await JwtService.getApiKey(user[0].uid);
        if (data.success == true) {
          this.$store.commit("updateToken", data.token);
          this.fetchCurrentUser();
        } else {
          this.isLoading = false;
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
      }
    },

    async fetchCurrentUser() {
      try {
        let data = await UserService.getUser(this.token);
        if (data.success == true) {
          this.$store.commit("updateUser", data.result);
          this.$store.commit("SET_CURRENT_USER", data.result);
          this.$store.commit("SET_TARIFFS", data.tariffs);
          this.getBranches();
        } else {
          this.isLoading = false;
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
      }
    },

    async getProvinces() {
      this.isLoading = true;
      try {
        let data = await ProvinceService.getProvinces();
        if (data.success == true) {
          this.$store.commit("SET_PROVINCES", data.result);
          this.getDistricts();
        } else {
          this.isLoading = false;
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
      }
    },

    async getDistricts() {
      this.isLoading = true;
      try {
        let data = await ProvinceService.getDistricts(1);
        if (data.success == true) {
          this.$store.commit("SET_DISTRICTS", data.result);
          this.getBranches();
          this.isLoading = false;
        } else {
          this.isLoading = false;
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
      }
    },

    async getBranches() {
      const passingValues = {
        userRole: this.user[0].userRole,
        branchId: this.user[0].branchId,
        companyId: this.user[0].companyId,
      };

      try {
        let braches = await BrancheService.getAllBranches(
          passingValues,
          this.token
        );
        if (braches.success == true) {
          this.$store.commit("SET_BRANCHES", braches.result);
          this.getCompanyDetails();
          this.isLoading = false;
        } else {
          this.isLoading = false;
          this.$bvToast.toast(braches.message, {
            title: "Erro!",
            variant: "danger",
            solid: true,
          });
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
      }
    },

    async getCompanyDetails() {
      try {
        const company = await CompanyService.getCompanyDetail(
          this.user[0].companyId,
          this.token
        );
        if (company.success == true) {
          this.$store.commit("SET_COMPANY", company.result);
          // console.log(company[0]);
        } else {
          console.log(company.message);
        }
      } catch (error) {
        console.log(error.message);
      }
    },
  },
});
</script>

<style scoped lang="scss">
.form-login {
  margin: auto;
  margin-top: 20px;
  width: 40%;
}

.footer {
  margin-top: 120px;
  text-align: center;
  font-size: 14px;
}
</style>
