<template>
  <div>
    <b-row>
      <b-col lg="6" sm="12" md="12">
        <b-card>
          <b-row>
            <b-col class="text-center">
              <b-form-group id="searchBranch" label-for="searchBranch">
                <b-form-input
                  type="text"
                  v-model="searchBranch"
                  :placeholder="$t('search_neighborhoods')"
                  :disabled="user[0].userRole > 1"
                  size="sm"
                  @keyup="searchBranches()"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col
              lg="4"
              sm="12"
              md="6"
              @click="openBranchModal()"
              class="text-primary text-right"
              style="cursor: pointer"
              ><b-icon icon="plus"></b-icon> Novo bairro</b-col
            >
          </b-row>
          <table
            class="table table-sm table-hover table-stripped bg-white mt-0"
          >
            <thead class="bg-secondary text-white">
              <tr>
                <th>{{ $t("neighbor") }} [ {{ branches.length }} ]</th>
                <th>{{ $t("created_at") }}</th>
                <th class="text-center">{{ $t("actions") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="branch in branchesLocal"
                :key="branch.id"
                style="cursor: pointer"
              >
                <td>
                  <small>{{ branch.name }}</small>
                </td>
                <td>
                  <small v-date-format="branch.createdAt" />
                </td>
                <td>
                  <b-row>
                    <b-col class="text-danger" @click="deleteBranch(branch)"
                      ><b-icon icon="trash"></b-icon
                    ></b-col>
                    <b-col class="text-primary" @click="editBranch(branch)"
                      ><b-icon icon="pen"></b-icon
                    ></b-col>
                    <b-col class="text-primary" @click="checkUsers(branch)"
                      ><b-icon icon="people-fill"></b-icon
                    ></b-col>
                  </b-row>
                </td>
              </tr>
            </tbody>
          </table>
        </b-card>
      </b-col>
      <b-col
        ><b-overlay :show="show" rounded="sm"
          ><staff-list-by-store
            v-on:update-users="checkUsers(currentBranch)" /></b-overlay
      ></b-col>
    </b-row>

    <b-modal hide-footer ref="add-new-branch" :title="$t('new')">
      <small>{{ $t("province") }}</small>
      <b-form-select
        size="sm"
        :disabled="isEditing"
        v-model="provinceId"
        class="mb-3"
      >
        <b-form-select-option
          v-for="province in provinces"
          :key="province.id"
          :value="province.id"
          :disabled="isLoading"
          >{{ province.name }}</b-form-select-option
        >
      </b-form-select>

      <small>{{ $t("district") }}</small>
      <b-form-select
        size="sm"
        :disabled="isEditing"
        v-model="districtId"
        class="mb-3"
      >
        <b-form-select-option
          v-for="district in selectedDistricts"
          :key="district.id"
          :value="district.id"
          :disabled="provinceId <= 0"
          >{{ district.name }}</b-form-select-option
        >
      </b-form-select>

      <small>{{ $t("neighbor") }}</small>
      <b-form-group id="branchName" label-for="branchName">
        <b-form-input
          id="branchName"
          type="text"
          v-model="branchName"
          size="sm"
          :placeholder="$t('neighbor')"
        ></b-form-input>
      </b-form-group>

      <b-button
        v-if="!isEditing"
        class="mt-3 sm"
        :disabled="branchName.length == 0 || provinceId <= 0 || districtId <= 0"
        block
        @click="addNewBranch()"
        size="sm"
      >
        <template v-if="isLoading">
          {{ $t("updating") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("submit") }}</template>
      </b-button>
      <b-button
        v-else
        class="mt-3 sm"
        :disabled="branchName.length == 0"
        block
        @click="addNewBranch()"
        size="sm"
      >
        <template v-if="isLoading">
          {{ $t("saving") }}
          <b-spinner small type="grow"></b-spinner>
        </template>
        <template v-else>{{ $t("update") }}</template>
      </b-button>
    </b-modal>

    <b-modal hide-footer ref="delete-branch" :title="$t('delete')">
      <p class="text-danger text-center">{{ $t("decide_delete") }}</p>
      <b-row>
        <b-col>
          <b-button block size="sm" @click="cancelDeletion()">{{
            $t("cancel")
          }}</b-button>
        </b-col>
        <b-col>
          <b-button
            block
            size="sm"
            variant="danger"
            @click="decidedToDelete()"
            >{{ $t("delete") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import BrancheService from "../http-service/BranchesService";
import SearchBranches from "../http-service/SearchBranches";
import UserService from "../http-service/UserService";

import StaffListByStore from "../components/settings/StaffListByStore";

export default {
  name: "BranchTable",

  components: {
    "staff-list-by-store": StaffListByStore,
  },

  watch: {
    provinceId: function () {
      this.selectedDistricts = this.districts.filter((a) => {
        return a.provinceId === this.provinceId;
      });
    },
  },

  computed: {
    ...mapGetters([
      "user",
      "companyStaff",
      "provinces",
      "districts",
      "branches",
      "currentUser",
      "token",
      "currentBranch",
    ]),
  },

  created() {
    this.branchesLocal = this.branches;
    this.checkUsers(this.branches[0]);
    // console.log(this.branches[0].id);
  },

  data() {
    return {
      show: false,
      // New Branch
      saveBranchOrEdit: "Novo Bairro",
      provinceId: 0,
      districtId: 0,
      id: 0,
      selectedDistricts: "",
      branchName: "",
      isLoading: false,
      isEditing: false,
      branchesLocal: [],

      // Delete Branches
      currentDeletion: "",

      // Search
      searchBranch: "",
    };
  },

  methods: {
    async checkUsers(branch) {
      this.show = true;
      this.$store.commit("SET_CURRENT_BRANCH", branch);
      try {
        const usersByBranch = await UserService.getAllUsers(
          branch.id,
          this.token
        );
        if (usersByBranch.success == true) {
          this.$store.commit(
            "SET_CURRENT_BRANCH_USER_LIST",
            usersByBranch.result
          );
        } else {
          this.$store.commit("SET_CURRENT_BRANCH_USER_LIST", []);
        }
        this.show = false;
      } catch (err) {
        this.$store.commit("SET_CURRENT_BRANCH_USER_LIST", []);
        console.log(err);
        this.show = false;
      }
    },

    async searchBranches() {
      if (this.searchBranch == null || this.searchBranch == "") {
        this.branchesLocal = this.branch;
        return;
      }

      try {
        const searchResult = await SearchBranches.search(
          this.searchBranch,
          this.token
        );
        this.branchesLocal = searchResult.result;
        // console.log(this.branchesLocal);
      } catch (err) {
        this.branchesLocal = this.branch;
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
      }
    },

    editBranch(branch) {
      this.openBranchModal();
      this.saveBranchOrEdit = "Alterar " + branch.name;
      this.isEditing = true;
      this.branchName = branch.name;
      this.id = branch.id;
    },

    deleteBranch(branch) {
      this.$refs["delete-branch"].show();
      this.currentDeletion = branch.id;
    },

    cancelDeletion() {
      this.$refs["delete-branch"].hide();
    },

    async decidedToDelete() {
      try {
        const deletion = await BrancheService.deleteBranch(
          this.currentDeletion,
          this.token
        );
        console.log(deletion);
        this.$bvToast.toast("Deletado com sucesso!", {
          title: "Deletado",
          variant: "success",
          solid: true,
        });
        this.$refs["delete-branch"].hide();
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.$refs["delete-branch"].hide();
      }
      this.$emit("update-branches");
    },

    async addNewBranch() {
      if (this.branchName.length <= 3) {
        this.$bvToast.toast(
          "Nome do Bairro deve ter pelo menos 3 caracteres!",
          {
            title: "Aviso!",
            variant: "warning",
            solid: true,
          }
        );
      } else {
        const passingValues = {
          id: this.id == 0 ? 0 : this.id,
          companyId: this.currentUser[0].companyId,
          provinceId: this.provinceId,
          districtId: this.districtId,
          name: this.branchName,
        };

        this.isLoading = true;

        try {
          const branch = await BrancheService.insertOrUpdateBranch(
            passingValues,
            this.token
          );
          if (branch.success == true) {
            this.$bvToast.toast(this.branchName + " foi gravado com sucesso.", {
              title: "Sucesso",
              variant: "success",
              solid: true,
            });
            this.$store.commit("SET_BRANCHES", branch.result);
            this.$emit("update-branches");
            this.$refs["add-new-branch"].hide();
            this.clearFields();
            this.isLoading = false;
          } else {
            this.$bvToast.toast(branch.message, {
              title: "Erro",
              variant: "danger",
              solid: true,
            });
          }
        } catch (err) {
          this.$bvToast.toast(err.message, {
            title: "Erro",
            variant: "danger",
            solid: true,
          });
          this.isLoading = false;
          this.$refs["add-new-branch"].hide();
          this.$emit("update-branches");
        }
      }
    },

    openBranchModal() {
      this.isEditing = false;
      this.$refs["add-new-branch"].show();
    },

    clearFields() {
      // Company Fields
      this.provinceId = 0;
      this.districtId = 0;
      this.branchName = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.table {
  font-size: 12pt;
  text-align: left;
  th {
    font-size: 12px;
  }
}
</style>
