<template>
  <div>
    <nav-bar />
    <b-container fluid class="bv-example-row">
      <div class="container-group">
        <div class="card">
          <div class="card-body"> 
            <b-row class="mt-0">
              <b-col lg="2" md="4" sm="12">
                <b-row>
                  <b-col
                    lg="12"
                    sm="12"
                    md="12"
                    style="cursor: pointer;"
                    class="mt-0 text-primary"
                    @click="selectPanel(1)"
                  >
                    <b-icon icon="map"></b-icon>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    Bairros
                  </b-col>
                  <b-col lg="12" sm="12" md="12" class="mt-1">
                    <router-link to="/tarrifs"
                      ><b-icon icon="tools"></b-icon>
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      {{ $t("tarrif") }}</router-link
                    >
                  </b-col>
                  <b-col
                    lg="12"
                    sm="12"
                    md="12"
                    style="cursor: pointer;"
                    class="mt-1 text-primary"
                    @click="selectPanel(3)"
                  >
                    <b-icon icon="gear-fill" aria-hidden="true"></b-icon>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    {{ $t("company_detail") }}
                  </b-col>
                </b-row>
              </b-col>
              <b-col class="mt-0" lg="10" md="8" sm="12">
                <b-row v-if="isLoading">
                  <pulse-loader class="text-center mt-4" />
                </b-row>
                <b-row v-else>
                  <b-col v-if="currentPanel == 2">
                    <colaborator-table v-on:update-users="getMembers()" />
                  </b-col>
                  <b-col v-else-if="currentPanel == 1">
                    <branch-table v-on:update-branches="getBranches()" />
                  </b-col>
                  <b-col v-else-if="currentPanel == 3">
                    <company-table v-on:update-company="getCompanyDetails()" />
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </div>
        </div>
      </div>
    </b-container>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import PulseLoader from "vue-spinner/src/PulseLoader.vue";
import NavBar from "../components/Navbar";

import ColaboratorTable from "../components/ColaboratorTable";
import BranchTable from "../components/BranchTable";
import CompanyTable from "../components/settings/CompanyTable";
import UserService from "../http-service/UserService";
import BrancheService from "../http-service/BranchesService";

export default {
  components: {
    NavBar,
    PulseLoader,
    ColaboratorTable,
    BranchTable,
    CompanyTable,
  },

  watch: {
    provinceId: function () {
      this.selectedDistricts = this.districts.filter((a) => {
        return a.provinceId === this.provinceId;
      });
    },
  },

  computed: {
    ...mapGetters([
      "user",
      "companyStaff",
      "provinces",
      "districts",
      "branches",
      "currentUser",
      "token",
    ]),
  },

  async created() {
    this.$store.commit("SET_CLEAR_CURRENT_VENDING");
    this.getMembers();
  },

  data() {
    return {
      userName: "",
      userUid: "",
      userPhoneNumber: "",
      userRole: 2,
      userStatus: 1,
      selectedSubBranch: "",
      code: Math.floor(Math.random() * this.userPhoneNumber) + 1,
      isLoading: false,

      currentPanel: 1,
    };
  },

  methods: {
    selectPanel(id) {
      if (id == 1) {
        this.currentPanel = 1;
      } else if (id == 2) {
        this.currentPanel = 2;
      } else if (id == 3) {
        this.currentPanel = 3;
      }
    },

    clearFields() {
      this.userName = "";
      this.phoneNumber = "";
      this.userRole = "";
      this.userPhoneNumber = "";
      this.selectedSubBranch = "";
      this.code = Math.floor(Math.random() * 1000000) + 1;
    },

    async getMembers() {
      try {
        const staffMembers = await UserService.getAllUsers(
          this.currentUser[0].companyId,
          this.token
        );
        this.$store.commit("SET_COMPANY_STAFF", staffMembers.result);
      } catch (err) {
        console.log(err);
      }
    },

    async getBranches() {
      this.isLoading = true;
      const passingValues = {
        companyId: this.currentUser[0].companyId,
        userRole: this.currentUser[0].userRole,
        branchId: this.currentUser[0].branchId,
      };
      try {
        let branches = await BrancheService.getAllBranches(
          passingValues,
          this.token
        );
        if (branches.success == true) {
          this.$store.commit("SET_BRANCHES", branches.result);
          this.isLoading = false;
        } else {
          this.isLoading = false;
        }
      } catch (err) {
        this.error = err.message;
        this.isLoading = false;
        this.$bvToast.toast(err.message, {
          title: "Erro!",
          variant: "danger",
          solid: true,
        });
      }
    },

    async getComanyDetails() {
      alert("Hello from company details");
    },

    getMeters() {
      console.log("Get meters");
    },
  },
};
</script>

