<script>
import NavBar from "../components/Navbar";
import CustomerService from "../http-service/CustomerService";
import MeterService from "../http-service/MetersService";
import ReadingListService from "../http-service/ReadingListService";
import DebtService from "../http-service/DebtService";
import { mapGetters } from "vuex";

// PDF Generator
import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

import exportFromJSON from "export-from-json";
import PdfService from "../http-service/PdfService";
import SmsService from "../http-service/SmsService";

export default {
  name: "BranchPanel",

  components: {
    NavBar,
  },

  watch: {
    branchId: function () {
      const branchCurrent = this.branches.filter((a) => {
        return a.id === this.branchId;
      });
      this.$store.commit("SET_CURRENT_BRANCH", branchCurrent[0]);
      this.getCustomers();
      this.getPendingBills();
      this.getAllDebts();
      this.fetchAllMeters();
    },
  },

  computed: {
    ...mapGetters([
      "user",
      "token",
      "branches",
      "currentUser",
      "currentBranch",
      "tariffs",
      "meters",
    ]),
  },

  created() {
    this.$store.commit("SET_CURRENT_BRANCH", this.branches[0]);
    this.branchId = this.branches[0].id;
    this.getPendingBills();
    this.getAllDebts();
    this.getCustomers();
    this.fetchAllMeters();
  },

  data() {
    return {
      show: false,
      branchId: "",
      customers: [],
      isLoading: false,
      name: "",
      phone: "",
      nuit: "",
      address: "",
      email: "",
      meeter_id: "",
      barcode: "",
      tariffTypeId: "",
      meter_type: 1,
      selectedMeter: {},

      editCustomerValues: {},
      facturasIrregulares: [],
      numberPendingBills: 0,
      totalPendingBill: 0,
      listaDeDividas: [],
      totalDividasMultas: 0,

      smsDetailsTextLocal: "",
      phoneNumberSMS: "258",
      currentCustomer: {},
    };
  },

  methods: {
    getNumberOfPendingBills(barcode) {
      return this.facturasIrregulares.filter((a) => {
        return a.barcode === barcode;
      });
    },

    generatePDF(customer) {
      const divida =
        this.getTDbt(customer.barcode) + this.dividaMlt(customer.barcode);

      const customerReadings = this.facturasIrregulares.filter((a) => {
        return a.status === 0 && a.barcode === customer.barcode;
      });

      const readingDetails =
        customerReadings[
          customerReadings.length > 0 ? customerReadings.length - 1 : 0
        ];

      this.currentCustomer = customer;

      let detalhesSMS = "\n";
      detalhesSMS += "FACTURA NR: " + readingDetails.receiptNumber + "\n";
      detalhesSMS += "Paga via M-Pesa:\n";
      detalhesSMS += "*150# OK\n";
      detalhesSMS += "6. Pagamentos\n";
      detalhesSMS += "7. Codigo Servico: 901029\n";
      detalhesSMS += "Referencia: " + customer.barcode + "\n";
      detalhesSMS += "VALOR: " + divida.toFixed(2) + " MZN\n";
      detalhesSMS += "Digitar PIN e JA ESTA\n";

      // console.log(detalhesSMS);
      this.smsDetailsTextLocal = detalhesSMS;
      this.$refs["send-sms"].show();
      this.phoneNumberSMS = customer.phone;
    },

    async sendSMS() {
      if (this.phoneNumberSMS.length != 12) {
        this.$bvToast.toast("Número de destinatário inválido.", {
          title: "Erro!",
          variant: "warning",
          solid: true,
        });
        return;
      }
      const passingValues = {
        number: this.phoneNumberSMS,
        smsBody: this.smsDetailsTextLocal,
        barcode: this.currentCustomer.barcode,
      };
      this.isLoading = true;

      try {
        const sendSms = await SmsService.sendSmsViaTextLocal(
          passingValues,
          this.token
        );
        if (sendSms.success == true) {
          console.log(sendSms.message);
        } else {
          console.log(sendSms.message);
        }
        this.isLoading = false;
        this.$refs["send-sms"].hide();
      } catch (err) {
        this.isLoading = false;
        this.$refs["send-sms"].hide();
      }
    },

    getTDbt(barcode) {
      const pendingBills = this.facturasIrregulares.filter((a) => {
        return a.barcode === barcode;
      });
      // console.log(pendingBills)
      // this.numberPendingBills = pendingBills.length;
      const totalDivida = pendingBills.reduce(function (
        acumulador,
        valorAtual
      ) {
        return (
          acumulador +
          valorAtual.totalOfBill +
          valorAtual.totalOfBill * valorAtual.forfeit
        );
      },
      0);

      return parseFloat(totalDivida);
    },

    meterType(meterId) {
      if (meterId == 1) {
        return "Pós-pago";
      } else if (meterId == 2) {
        return "Pré-pago";
      }
    },

    tariffId(tariff) {
      if (tariff == 1) {
        return "Fontenária";
      } else if (tariff == 2) {
        return "Doméstica";
      } else if (tariff == 3) {
        return "Municipal";
      } else if (tariff == 4) {
        return "Comercial";
      } else if (tariff == 5) {
        return "Pública";
      } else if (tariff == 6) {
        return "Industrial";
      } else if (tariff == 7) {
        return "Toma de Água";
      }
    },

    dividaMlt(barcode) {
      const pendingMultas = this.listaDeDividas.filter((a) => {
        return a.barcode === barcode && a.status === 0;
      });
      // this.numberPendingBills = pendingBills.length;
      const totalDividasMultas = pendingMultas.reduce(function (
        acumulador,
        valorAtual
      ) {
        return acumulador + valorAtual.amount;
      },
      0);

      return parseFloat(totalDividasMultas);
    },

    async getAllDebts() {
      this.show = true;

      try {
        const branchDebts = await DebtService.getAllDebts(
          this.currentBranch.id,
          this.token
        );
        if (branchDebts.success == true) {
          this.listaDeDividas = branchDebts.result;
          this.totalDividasMultas = this.listaDeDividas.reduce(function (
            acumulador,
            valorAtual
          ) {
            return acumulador + valorAtual.amount;
          },
          0);
        } else {
          this.listaDeDividas = [];
        }
      } catch (err) {
        console.log(err.message);
      }

      this.show = false;
    },

    async getPendingBills() {
      this.show = true;
      const passingValues = {
        branchId: this.currentBranch.id,
      };

      try {
        const pendingBillsByBranch = await ReadingListService.getReadingList(
          passingValues,
          this.token
        );
        if (pendingBillsByBranch.success == true) {
          // console.log(pendingBillsByBranch.result);
          this.facturasIrregulares = pendingBillsByBranch.result;

          this.totalPendingBill = this.facturasIrregulares.reduce(function (
            acumulador,
            valorAtual
          ) {
            return (
              acumulador +
              valorAtual.totalOfBill +
              valorAtual.totalOfBill * valorAtual.forfeit
            );
          },
          0);
        } else {
          // console.log(pendingBillsByBranch.message);
          this.facturasIrregulares = [];
        }
      } catch (err) {
        console.log(err.message);
        this.facturasIrregulares = [];
      }
      this.show = false;
    },

    async printListOfReading() {
      this.show = true;
      const passingValues = {
        branchId: this.currentBranch.id,
        branchName: this.currentBranch.name,
      };

      try {
        const generateList = await ReadingListService.getReadingList(
          passingValues,
          this.token
        );
        if (generateList.success == true) {
          console.log(generateList);
        } else {
          this.$bvToast.toast(generateList.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast(err.message, {
          title: "",
          variant: "danger",
          solid: true,
        });
      }
      this.show = false;
    },

    openVendingPanel(currentCustomer) {
      this.customers = [];
      this.$store.commit("SET_CURRENT_CUSTOMER", currentCustomer);
      this.$router.push("/customerview");
    },

    printPDFInvoice() {
      this.show = true;
      this.customers.forEach((customer) => {
        const passingValues = {
          barcode: customer.barcode,
          customerName: customer.name,
          companyId: this.currentUser[0].companyId,
          tariffTypeId: customer.tarrifTypeId,
          branchName: this.currentBranch.name,
        };

        try {
          const generating = PdfService.createPdfInvoice(
            passingValues,
            this.token
          );
          if (generating.success == true) {
            console.log(generating);
            // this.downloadPdf(fileName);
          } else {
            this.$bvToast.toast(generating.message, {
              title: "Error",
              variant: "danger",
              solid: true,
            });
          }

          this.show = false;
        } catch (err) {
          this.show = false;
          this.$bvToast.toast(err.message, {
            title: "Error",
            variant: "danger",
            solid: true,
          });
        }
      });
    },

    async fetchAllMeters() {
      try {
        const meters = await MeterService.getAllMeters(
          this.token,
          this.currentBranch.id
        );
        this.$store.commit("SET_METERS", meters.result);
        this.isLoading = false;
      } catch (err) {
        console.log(err.message);
        this.isLoading = false;
        this.$store.commit("SET_METERS", null);
      }
    },

    listItems() {
      this.itemsForList = this.customers.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },

    editCustomer(customer) {
      this.$refs["edit-customer"].show();
      this.editCustomerValues = customer;
    },

    async editCustomerFinal() {
      this.isLoading = true;

      try {
        const customers = await CustomerService.insertOrUpdateCustomer(
          this.editCustomerValues,
          this.token
        );
        if (customers.success == true) {
          // this.customers = customers.result;
          this.isLoading = false;
          this.$bvToast.toast(
            this.editCustomerValues.name + " actualizado com sucesso",
            {
              title: "",
              variant: "success",
              solid: true,
            }
          );
          this.getCustomers();
        } else {
          this.customers = customers.result;
          this.isLoading = false;
          this.$bvToast.toast(customers.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
          this.getCustomers();
        }
        this.$refs["edit-customer"].hide();
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
        this.$refs["edit-customer"].hide();
      }
    },

    async saveCustomer() {
      const uid = Math.floor(Math.random() * 1000000) + 1;

      const passingValues = {
        branchId: this.currentBranch.id,
        meeter_type: this.meter_type,
        tarrifTypeId: this.tariffTypeId,
        barcode: this.barcode,
        meeter_id: this.meeter_id,
        email: this.email,
        address: this.address,
        nuit: this.nuit,
        phone: this.phone,
        name: this.name,
        status: 1,
        uid: this.currentBranch.id + this.meeter_id + uid,
        created_by: this.user[0].name,
      };

      this.isLoading = true;
      // console.log(passingValues);

      try {
        const customer = await CustomerService.insertOrUpdateCustomer(
          passingValues,
          this.token
        );
        this.isLoading = false;
        if (customer.success == true) {
          this.$bvToast.toast(this.name + " cadastrado com sucesso", {
            title: "Sucesso",
            variant: "success",
            solid: true,
          });
          this.clearValues();
          this.getCustomers();
        } else {
          this.$bvToast.toast(customer.message, {
            title: "",
            variant: "warning",
            solid: true,
          });
        }
      } catch (err) {
        this.$bvToast.toast("" + err, {
          title: "Erro",
          variant: "danger",
          solid: true,
        });
        this.isLoading = false;
      }
    },

    clearValues() {
      this.meeter_type = 1;
      this.tarrifTypeId = "";
      this.barcode = "";
      this.meeter_id = "";
      this.email = "";
      this.address = "";
      this.nuit = "";
      this.phone = "";
      this.name = "";

      this.$refs["add-customer"].hide();
    },

    openAddNewCustomerModal() {
      this.$refs["add-customer"].show();
    },

    async getCustomers() {
      this.isLoading = true;
      try {
        const customers = await CustomerService.getCustomerByBranch(
          this.currentBranch.id,
          this.token
        );
        this.customers = customers.result;
        this.listItems();
        this.fetchAllMeters();
      } catch (err) {
        this.customers = [];
        this.isLoading = false;
      }
    },

    printExcelShet() {
      const body = [];

      for (let customer of this.customers) {
        const row = {
          "Nome completo": customer.name,
          Contador: customer.barcode,
          Telemóvel: customer.phone.substring(3),
          "Facturas Pendentes": this.getNumberOfPendingBills(customer.barcode)
            .length,
          "Montante em dívida": this.getTDbt(customer.barcode).toFixed(2),
          Observação: "",
        };
        if (this.getNumberOfPendingBills(customer.barcode).length >= 1) {
          body.push(row);
        }
      }
      const fileName = "Devedores_Bairro_" + this.currentBranch.name;
      const data = body;
      const exportType = "xls";

      exportFromJSON({ data, fileName, exportType });
    },

    printList() {
      if (!this.customers) {
        this.$bvToast.toast("Clientes indisponíveis.", {
          title: "",
          variant: "warning",
          solid: true,
        });
      } else {
        const body = [];

        for (let customer of this.customers) {
          const rows = [];

          rows.push({ text: customer.name, style: "estilo" });
          rows.push({ text: customer.barcode, style: "estilo" });
          rows.push({
            text: this.getNumberOfPendingBills(customer.barcode).length,
            style: "estilo",
          });
          rows.push({
            text: this.getTDbt(customer.barcode).toFixed(2),
            style: "estilo",
          });
          rows.push({ text: "", style: "estilo" });
          rows.push({ text: "", style: "estilo" });
          rows.push({ text: "", style: "estilo" });
          body.push(rows);
        }
        // console.log(body)
        // PDFmake Functionality
        // const currentDate = `NOVEMBRO DE ${new Date().getFullYear()}`;
        const docDefinition = {
          content: [
            {
              columns: [
                {
                  text: `LISTA DE CLIENTES DO BAIRRO ${this.currentBranch.name.toUpperCase()} [ ${
                    this.customers.length
                  } ]`,
                  style: "header",
                },
              ],
            },
            {
              table: {
                body: [
                  [
                    { text: "Nome do cliente", style: "columnsTitle" },
                    { text: "Contador", style: "columnsTitle" },
                    { text: "F/Pendentes", style: "columnsTitle" },
                    { text: "Dívida", style: "columnsTitle" },
                    { text: "Anterior", style: "columnsTitle" },
                    { text: "Actual", style: "columnsTitle" },
                    { text: "Observação", style: "columnsTitle" },
                  ],
                  ...body,
                ],
              },
            },
          ],
          styles: {
            header: { fontSize: 9, bold: true },
            columnsTitle: {
              fontSize: 9,
              bold: true,
              fillColor: "#7159c1",
              color: "#FFFFFF",
            },
            estilo: {
              fontSize: 8,
            },
          },
        };
        pdfMake.createPdf(docDefinition).open();
      }
    },

    saveCustomerBill() {
      const customers = this.customers.filter((a) => {
        return a.status === 1;
      });

      customers.forEach((customer) => {
        const customerReadings = this.facturasIrregulares.filter((a) => {
          return a.status === 0 && a.barcode === customer.barcode;
        });
        this.generateBill(customer, customerReadings);
      });
    },

    async generateBill(customer, customerReadings) {
      const readingDetails =
        customerReadings[
          customerReadings.length > 0 ? customerReadings.length - 1 : 0
        ];

      if (customerReadings.length > 0) {
        const body = [];

        for (let row of customerReadings) {
          const rows = [];

          rows.push({
            text: row.receiptNumber + "/SAF/" + row.year,
            fontSize: 8,
          });
          rows.push({ text: row.dataEmissao, fontSize: 8 });
          rows.push({ text: row.dataLimite, fontSize: 8 });
          rows.push({ text: row.consumoFacturado, fontSize: 8 });
          rows.push({ text: row.subTotal, fontSize: 8 });
          rows.push({ text: row.vat, fontSize: 8 });
          rows.push({ text: row.forfeit, fontSize: 8 });
          rows.push({ text: row.totalOfBill, fontSize: 8 });

          body.push(rows);
        }

        let docDefinition = {
          // watermark: { text: this.company[0].name, opacity: 0.08 },
          header: [
            {
              image: await this.getBase64ImageFromURL(
                "https://saaf-tsenane.herokuapp.com/img/logo_12.2b7a27d2.jpeg"
              ),
              width: 250,
              height: 150,
              margin: [30, 0, 0, 0],
            },
          ],
          footer: {
            columns: [
              {
                text: "Documento processado por computador",
                alignment: "left",
                fontSize: 8,
                margin: [30, 0, 0, 0],
              },
              {
                text: "TSENANE Software",
                alignment: "right",
                fontSize: 8,
                margin: [0, 0, 30, 0],
              },
            ],
          },
          content: [
            {
              columns: [
                [
                  {
                    text: `Factura/Recibo Nº: ${readingDetails.receiptNumber}\n`,
                    bold: true,
                    fontSize: 10,
                    alignment: "right",
                  },
                ],
              ],
            },
            {
              text: "\n\n\n\n\n\n\n",
            },
            {
              columns: [
                [
                  [
                    {
                      text: `Nome: ${customer.name}`,
                      bold: true,
                      fontSize: 11,
                    },
                    {
                      text: `Tarifa: ${this.tariffId(customer.tarrifTypeId)}`,
                      bold: true,
                      fontSize: 10,
                    },
                    {
                      text: `Endereço: ${customer.address}`,
                      fontSize: 10,
                    },
                    {
                      text: `Telefone: ${customer.phone}`,
                      fontSize: 10,
                    },
                    {
                      text: `NUIT: ${customer.nuit}`,
                      fontSize: 10,
                    },
                  ],
                ],
                [
                  {
                    text: `Data de emissão: ${readingDetails.dataEmissao}`,
                    alignment: "right",
                    bold: true,
                    fontSize: 10,
                  },
                  {
                    text: `Data de vencimento: ${readingDetails.dataLimite}`,
                    alignment: "right",
                    bold: true,
                    fontSize: 10,
                  },
                ],
              ],
            },
            {
              text: "Dados da factura",
              style: "sectionHeader",
            },
            {
              table: {
                widths: [
                  "*",
                  "auto",
                  "auto",
                  "auto",
                  "auto",
                  "auto",
                  "auto",
                  "auto",
                ],
                body: [
                  [
                    { text: "Nº/Doc", style: "columnsTitle" },
                    { text: "Data", style: "columnsTitle" },
                    { text: "Vencimento", style: "columnsTitle" },
                    { text: "Consumo", style: "columnsTitle" },
                    { text: "Subtotal", style: "columnsTitle" },
                    { text: "IVA", style: "columnsTitle" },
                    { text: "Multa", style: "columnsTitle" },
                    { text: "Total", style: "columnsTitle" },
                  ],
                  ...body,
                ],
              },
              layout: "lightHorizontalLines",
            },
            {
              text: `\nTOTAL A PAGAR: ${(
                this.getTDbt(customer.barcode) +
                this.dividaMlt(customer.barcode)
              ).toFixed(2)} MZN`,
              fontSize: 10,
              alignment: "right",
              color: "#000000",
            },
            {
              text: "\n\n Assinatura do técnico: _______________________________",
              alignment: "center",
              fontSize: 10,
            },
          ],
          styles: {
            sectionHeader: {
              bold: true,
              decoration: "underline",
              fontSize: 12,
              margin: [0, 15, 0, 15],
            },
            columnsTitle: {
              fontSize: 8,
              bold: true,
              color: "#000000",
            },
          },
        };

        pdfMake
          .createPdf(docDefinition)
          .download(`uploads/${customer.barcode}_.pdf`);
        // let pdfDoc = pdfMake.createPdf(docDefinition, {});
        // pdfDoc.pipe(fs.createWriteStream(`uploads/${customer.barcode}_.pdf`));
        // pdfDoc.end();
      } else {
        return;
      }
    },

    getBase64ImageFromURL(url) {
      return new Promise((resolve, reject) => {
        var img = new Image();
        img.setAttribute("crossOrigin", "anonymous");

        img.onload = () => {
          var canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;

          var ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);

          var dataURL = canvas.toDataURL("image/png");
          this.isLoading = false;
          resolve(dataURL);
        };

        img.onerror = (error) => {
          this.isLoading = false;
          reject(error);
        };

        img.src = url;
      });
    },
  },
};
</script>

